.acr-hero-parallax {
  background-color: $acr-black;
  overflow: hidden;

  .acr-hero-parallax-element {
    transition: opacity 1.5s ease-out;
  }

  button,
  .hero-ctas,
  .arc-hero-heading,
  .slick-dots {
    transition: opacity 1s ease-out;
  }

  &.is-faded {
    .acr-hero-parallax-element {
      opacity: .5;
    }

    button,
    .hero-ctas,
    .arc-hero-heading,
    .slick-dots {
      opacity: 0;
    }
  }
}
