// Base Typography
//  */
// NOTE
// html is set to 62.5% so that all the REM measurements throughout
// are based on 10px sizing. For example 1.5rem = 15px :) */
body,
html {
  color: $acr-default;
  font-family: $acr-avenir-ltpro-light, $acr-font-stack;
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased; //remove sub pixel antialiasing -- smoothing
  //font-size: 100%;
  font-weight: 100;
}

body {
  color: $acr-default;
  font-family: $acr-avenir-ltpro-light;
  font-size: 1.6rem; // currently ems cause chrome bug misinterpreting rems on body element
  letter-spacing: 0;
  line-height: 1.5;
  margin: 0;

  @media #{$sm-breakpoint} {
    font-size: 1.4rem;
  }

  @media #{$lg-breakpoint} {
    font-size: 1.6rem;
  }

  @media #{$xl-breakpoint} {
    line-height: 1.7;
  }
}

// Element Level Typography
//  */
h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
  font-weight: 100;
  margin: 0;
}

sup,
sub {
  font-size: 1.2rem;
}

sup {
  top: 0;
  vertical-align: text-top;
}

a,
a:active {
  border-bottom: 1px solid $acr-dim-grey-2;
  color: inherit;
  text-decoration: none;
  transition: .2s;

}

a:focus,
a:hover {
  border-color: $acr-bg-color-trans;
  color: $acr-red;
  text-decoration: none;
}

// theme colors (body copy and headings)
.light-theme { // light colored bg
  color: $acr-default;

  .acr-heading h6,
  .heading,
  .title-text {
    color: $acr-black;
  }
}

.dark-theme { // dark colored bg
  color: $acr-white;

  .acr-heading h6,
  .heading,
  .title-text {
    color: $acr-white;
  }
}

// When we want to declare the font specifically :
.acr-avenir-light {
  font-family: $acr-avenir-ltpro-light;
}

.acr-avenir-light-oblique {
  font-family: $acr-avenir-ltpro-light-oblique;
}

.acr-avenir-medium {
  font-family: $acr-avenir-ltpro-medium;
}

.acr-avenir-medium-oblique {
  font-family: $acr-avenir-ltpro-medium-oblique;
}

.acr-avenir-black {
  font-family: $acr-avenir-ltpro-black;
}

.acr-avenir-black-oblique {
  font-family: $acr-avenir-ltpro-black-oblique;
}

.acr-avenir-heavy {
  font-family: $acr-avenir-ltpro-heavy;
}

.acr-avenir-heavy-oblique {
  font-family: $acr-avenir-ltpro-heavy-oblique;
}

.acr-avenir-book {
  font-family: $acr-avenir-ltpro-book;
}

.acr-avenir-book-oblique {
  font-family: $acr-avenir-ltpro-book-oblique;
}

.acr-avenir-roman {
  font-family: $acr-avenir-ltpro-roman;
}

.acr-acura-regular {
  font-family: $acr-bespoke-regular;
}

.acr-acura-light {
  font-family: $acr-bespoke-light;
}

.acr-acura-book {
  font-family: $acr-bespoke-book;
}

.acr-acura-bold {
  font-family: $acr-bespoke-bold;
}

h1 {
  color: $acr-black;
  font-family: $acr-bespoke-regular;
  font-size: 1.5rem;
  letter-spacing: .1rem;
  line-height: 1.86;
  margin: 1.75rem 0;
  text-transform: uppercase;

  @media #{$sm-breakpoint} {
    font-size: 6.1rem;
    line-height: 1;
  }

  @media #{$lg-breakpoint} {
    font-size: 7.5rem;
    line-height: 1.2;
  }
}

h2 {
  color: $acr-black;
  font-family: $acr-bespoke-regular;
  font-size: 3rem;
  letter-spacing: .6rem;
  line-height: 1.3;
  margin: 1rem 0;
  text-align: center;
  text-transform: uppercase;
  width: 100%;

  @media #{$sm-breakpoint} {
    font-size: 3.5rem;
    letter-spacing: .8rem;
    line-height: 1.3;
  }

  @media #{$lg-breakpoint} {
    font-size: 5rem;
    letter-spacing: .95rem;
    line-height: 1.19;
  }
}


h3 {
  color: $acr-black;
  font-family: $acr-bespoke-light;
  font-size: 2.4rem;
  letter-spacing: .15rem;
  line-height: 1.625;
  text-transform: none;
  width: 100%;

  @media #{$sm-breakpoint} {
    font-family: $acr-bespoke-light;
    font-size: 4rem;
    letter-spacing: .1rem;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-bespoke-light;
    font-size: 5rem;
    letter-spacing: 0;
  }

  @media #{$lg-breakpoint} {
    font-size: 6rem;
  }
}


h4 {
  color: $acr-black;
  font-family: $acr-bespoke-light;
  font-size: 2.6rem;
  letter-spacing: .05rem;
  line-height: 2.9rem;
  text-transform: capitalize;
  width: 100%;

  @media #{$sm-breakpoint} {
    font-family: $acr-bespoke-light;
    font-size: 2.3rem;
    letter-spacing: .05rem;
    line-height: 1.2;
    text-transform: capitalize;
    width: 100%;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-bespoke-light;
    font-size: 2.4rem;
    letter-spacing: .02rem;
    line-height: 2.9rem;
    text-transform: capitalize;
    width: 100%;
  }

  @media #{$xl-breakpoint} {
    font-size: 3.5rem;
    letter-spacing: .1rem;
    line-height: 1.2;
  }
}

h5 {
  color: $acr-black;
  font-family: $acr-bespoke-book;
  font-size: 1.9rem;
  letter-spacing: 0;
  line-height: 1.35;
  margin: 1rem 0;
  text-transform: capitalize;
  width: 100%;

  @media #{$sm-breakpoint}  {
    font-family: $acr-bespoke-book;
    font-size: 1.6rem;
    letter-spacing: .1rem;
    line-height: 1.8rem;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-bespoke-book;
    font-size: 2.2rem;
    letter-spacing: .1rem;
    line-height: 2.8rem;
  }

  @media #{$xl-breakpoint} {
    font-size: 3rem;
    line-height: 4rem;
  }
}

h6 {
  color: $acr-black;
  font-family: $acr-avenir-ltpro-black;
  font-size: 1.8rem;
  letter-spacing: .07rem;
  line-height: 1.25;
  margin: 1rem 0;
  text-transform: uppercase;
  width: 100%;

  @media #{$sm-breakpoint} {
    font-family: $acr-avenir-ltpro-black;
    font-size: 1.7rem;
    letter-spacing: .05rem;
    line-height: 1.25;
    margin: 1rem 0 .7rem;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-avenir-ltpro-black;
    font-size: 2rem;
    letter-spacing: 0;
    line-height: 1.25;
  }
}


.acr-pull-quote {
  color: $acr-black;
  font-family: $acr-avenir-ltpro-light-oblique;
  font-size: 1.7rem;
  letter-spacing: 0;
  line-height: 1.25;
  text-align: center;

  @media #{$sm-breakpoint} {
    font-family: $acr-avenir-ltpro-light-oblique;
    font-size: 1.7rem;
    letter-spacing: .1rem;
    line-height: 1.7;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-avenir-ltpro-light-oblique;
    font-size: 2.4rem;
    letter-spacing: 0;
    line-height: 1.5;
  }

  .author {
    display: block;
    font-family: $acr-avenir-ltpro-black;
    font-size: 1.4rem;
    letter-spacing: .1rem;
    text-transform: uppercase;

    span {
      font-family: $acr-avenir-ltpro-roman;
      text-transform: uppercase;
    }

    @media #{$sm-breakpoint} {
      letter-spacing: .5rem;

      span {
        letter-spacing: .05rem;
      }
    }
  }
}

.acr-shopping-tools {

  .tools {
    &:hover span {
      color: $acr-deep-grey;
    }
  }

  span {
    &.desc {
      color: $acr-mid-grey;
      font-family: $acr-avenir-ltpro-heavy;
      font-size: 1.3rem;
      line-height: 1.4;
    }
  }

  @media #{$sm-breakpoint} {
    font-size: 1.4rem;
    letter-spacing: 0;
  }

  @media #{$lg-breakpoint} {
    font-size: 1.2rem;
  }
}



.acr-accolades {
  text-align: center;

  .tool-icon {
    color: $acr-deep-blue;
    font-size: 1rem;
  }

  .headline {
    color: $acr-deep-blue;
    font-family: $acr-bespoke-light;
    font-size: 2rem;
    letter-spacing: .07rem;
    line-height: 1.2;
    text-transform: none;

    @media #{$sm-breakpoint} {
      font-family: $acr-bespoke-light;
      font-size: 2.9rem;
      letter-spacing: .1rem;
      line-height: 1.3;
    }

    @media #{$lg-breakpoint} {
      font-family: $acr-bespoke-light;
      font-size: 4.5rem;
      letter-spacing: 0;
      line-height: 1.3;
    }

    @media #{$xl-breakpoint} {
      font-family: $acr-bespoke-light;
      font-size: 4.5rem;
      letter-spacing: 0;
      line-height: 1.3;
    }
  }

  .subheadline {
    color: $acr-deep-blue;
    display: block;
    font-family: $acr-avenir-ltpro-heavy;
    font-size: 1.3rem;
    text-transform: uppercase;

    @media #{$sm-breakpoint} {
      font-size: 1.7rem;
      letter-spacing: 0;
    }
  }

  .headline-container {
    .headline {
      color: $acr-white;
      font-family: $acr-bespoke-regular;
      font-size: 11.5vw;
      letter-spacing: 1.8vw;
      text-align: left;
      text-transform: uppercase;
      width: 100%;

      @media #{$sm-breakpoint} {
        font-size: 6.9vw;
        letter-spacing: 1.3vw;
      }

      @media #{$lg-breakpoint} {
        font-size: 5.7vw;
        letter-spacing: 1.2vw;
      }

      @media #{$xl-breakpoint} {
        font-size: 5vw;
        letter-spacing: 1vw;
        line-height: 1.2;
      }
    }
  }

  .acr-model-slick {
    .acr-pull-quote {
      font-size: 1.6rem;

      @media #{$xl-breakpoint} {
        font-size: 2.5rem;
      }
    }
  }
}

.acr-packages {
  .acr-packages-model {
    color: $acr-black;
    font-size: 3rem;
  }

  .acr-packages-slick {
    .package {
      .title {
        font-size: 3.6rem;
        line-height: 1.125;
      }

      .description,
      .tagline {
        font-size: 2rem;
        line-height: 1.5;
      }

      .tagline {
        font-family: $acr-avenir-ltpro-roman;
      }
    }
  }


  @media #{$sm-breakpoint} {
    .acr-packages-slick {
      .package {
        .title {
          font-size: 2.4rem;
          line-height: 1.125;
        }

        .description,
        .tagline {
          font-size: 1.4rem;
          line-height: 1.5;
        }

        .tagline {
          font-family: $acr-avenir-ltpro-roman;
        }
      }
    }
  }

  @media #{$lg-breakpoint} {
    .acr-packages-slick {
      .package {
        .title {
          font-size: 3.6rem;
          line-height: 1.125;
        }

        .description,
        .tagline {
          font-size: 1.6rem;
          line-height: 1.5;
        }

        .tagline {
          font-family: $acr-avenir-ltpro-roman;
        }
      }
    }
  }
}

.acr-callout {
  color: $acr-black;
  font-family: $acr-bespoke-light;
  font-size: 2.9rem;
  letter-spacing: .02rem;
  line-height: 1.3;
  text-transform: capitalize;
  width: 100%;

  ~ div {
    margin-bottom: 1.5rem;
  }

  > p {
    text-align: center;
  }

  @media #{$sm-breakpoint} {
    font-family: $acr-bespoke-light;
    font-size: 3.2rem;
    letter-spacing: .1rem;
    line-height: 1.3;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-bespoke-light;
    font-size: 5rem;
    letter-spacing: 0;
    line-height: 1.3;
  }
}

p {
  color: $acr-default;

  &.acr-larger { // larger body copy
    font-family: $acr-avenir-ltpro-light;
    font-size: 1.6rem;
    letter-spacing: 0;
    line-height: 1.4;
    text-transform: none;


    @media #{$sm-breakpoint} {
      font-family: $acr-avenir-ltpro-light;
      font-size: 1.6rem;
      letter-spacing: 0;
      line-height: 1.5;
      text-transform: none;
    }

    @media #{$lg-breakpoint} {
      font-family: $acr-avenir-ltpro-light;
      font-size: 2rem;
      letter-spacing: 0;
      line-height: 1.5;
      text-transform: none;
    }
  }

  &.acr-decorative { // decorative body copy //not on homepage--only mlp
    font-family: $acr-avenir-ltpro-light;
    font-size: 1.6rem;
    letter-spacing: 0;
    line-height: 1.5;

    @media #{$sm-breakpoint} {
      font-family: $acr-avenir-ltpro-light;
      font-size: 1.6rem;
      letter-spacing: 0;
      line-height: 1.5;
    }

    @media #{$lg-breakpoint} {
      font-family: $acr-avenir-ltpro-light;
      font-size: 1.6rem;
      letter-spacing: 0;
      line-height: 1.5;
    }
  }

  &.acr-body {
    font-family: $acr-avenir-ltpro-light;
    font-size: 1.6rem;
    letter-spacing: 0;
    line-height: 1.5;


    @media #{$sm-breakpoint} {
      font-family: $acr-avenir-ltpro-light;
      font-size: 1.4rem;
      line-height: 1.5;
    }

    @media #{$lg-breakpoint} {
      font-family: $acr-avenir-ltpro-light;
      font-size: 1.6rem;
      line-height: 1.5;
    }

    @media #{$xl-breakpoint} {
      line-height: 1.7;
    }
  }
}

.acr-cta {
  font-family: $acr-avenir-ltpro-black;
  font-size: 1.6rem;
  letter-spacing: 0;
  line-height: 1.25;
  text-transform: uppercase;

  @media #{$sm-breakpoint} {
    font-family: $acr-avenir-ltpro-heavy;
    font-size: 1.6rem;
    letter-spacing: .1rem;
    line-height: 1.25;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-avenir-ltpro-heavy;
    font-size: 1.4rem;
    letter-spacing: .1rem;
    line-height: 1.5;
  }
}

.acr-legal {
  font-family: $acr-avenir-ltpro-roman;
  font-size: 1.6rem; // 12pt
  line-height: 1;

  @media #{$sm-breakpoint} {
    font-family: $acr-avenir-ltpro-roman;
    font-size: 1.7rem; // 13pt
    line-height: 1;
  }

  @media #{$lg-breakpoint} {
    font-family: $acr-avenir-ltpro-roman;
    font-size: 1.2rem; // 9pt
    line-height: 1;
  }
}


.acr-sup {
  font-family: $acr-avenir-ltpro-heavy;
  font-size: .8rem;
  font-weight: 700;
  padding: .2rem .2rem .2rem .1rem;
  top: 6px;

  &:hover {
    color: $acr-red;
  }
}


// superscript styles
.acr-hero {
  .acr-hero-panel-container {
    h2 .acr-sup {
      top: .65em;
    }

    h3 .acr-sup {
      top: .6em;
    }
  }
}

.promo-tiles-1 {
  h5 .acr-sup {
    top: .6em;
  }
}

.acr-awards {
  p .acr-sup {
    top: 1.2em;
  }
}

.key-story {
  .desc-text .acr-sup {
    top: .4em;
  }
}

.acr-promotions {
  h5 .acr-sup {
    top: .5em;
  }
}

//remove sub pixel antialiasing by targeting higher pixel density screens
@media  only screen and (-webkit-min-device-pixel-ratio: 1.25), only screen and (min-device-pixel-ratio: 1.25), only screen and (min-resolution: 200dpi), only screen and (min-resolution: 1.25dppx) {
  -webkit-font-smoothing: subpixel-antialiased;
}
