.features-secondary-blade {
  .acr-heading {
    padding: 40px 0;
  }

  .features-grid-a {
    @media #{$sm-breakpoint} {
      max-height: 600px;
    }

    @media #{$lg-breakpoint} {
      max-height: 570px;
      min-height: 470px;
    }

    @media #{$xl-breakpoint} {
      max-height: 650px;
      min-height: 620px;
    }

    .contentbox1 {
      @media #{$sm-breakpoint} {
        left: 31%;
        top: 31%;
      }

      @media #{$lg-breakpoint} {
        left: 25%;
        top: 45%;
      }

      @media #{$xl-breakpoint} {
        left: 19%;
        top: 37%;
      }
    }

    .galleryimg1 {
      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        right: 21%;
      }

      @media #{$lg-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        right: 15%;
        top: 30%;
      }

      @media #{$lg-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        right: 14%;
        top: 29%;
      }
    }

    .galleryimg2 {

      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 65%;
        top: 70%;
      }

      @media #{$lg-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 73%;
        top: 60%;
      }

      @media #{$xl-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 62%;
        top: 57%;
      }
    }
  }

  .features-grid-b {
    @media #{$sm-breakpoint} {
      max-height: 480px;
    }

    @media #{$lg-breakpoint} {
      max-height: 450px;
      min-height: 450px;
    }

    @media #{$xl-breakpoint} {
      min-height: 500px;
    }

    .contentbox1 {
      @media #{$sm-breakpoint} {
        left: 17%;
        top: 71%;
      }

      @media #{$lg-breakpoint} {
        left: 43%;
        top: 45%;
      }

      @media #{$xl-breakpoint} {
        left: 50%;
        top: 45%;
      }
    }

    .galleryimg1 {
      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 43%;
        top: 33%;
      }

      @media #{$lg-breakpoint} {
        left: 29%;
        top: 45%;
      }

      @media #{$xl-breakpoint} {
        left: 35%;
        top: 45%;
      }
    }

    .galleryimg2 {
      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 65%;
        top: 70%;
      }

      .btn-wrapper {
        bottom: 1rem;
        display: block;
        left: 1rem;
        max-width: 24rem;
        min-height: 3.4rem;
        min-width: 16rem;
        position: absolute;
        text-align: left;
        z-index: 3;

        &:only-child {
          bottom: 0;
          left: 0;
          position: relative;
        }

        a {
          color: $acr-white;
          display: block;
          text-decoration: none;
        }

        .cta-btn {
          background: $acr-red;
          float: left;
          height: 3.4rem;
          margin-right: 1rem;
          width: 3.4rem;

          span {
            font-size: 1.4rem;
            left: 0;
            line-height: 3.4rem;
            margin: 1rem;
            position: relative;
          }
        }

        .cta-txt {
          display: block;
          font-family: $acr-avenir-ltpro-heavy;
          font-size: 1.3rem;
          line-height: 1.3;
          position: relative;
          text-align: left;
          text-transform: uppercase;
          top: .3rem;
        }
      }
    }
  }

  .features-grid-c {
    @media #{$sm-breakpoint} {
      max-height: 550px;
    }

    @media #{$lg-breakpoint} {
      max-height: 450px;
      min-height: 450px;
    }

    @media #{$xl-breakpoint} {
      min-height: 550px;
    }

    .contentbox1 {
      @media #{$sm-breakpoint} {
        right: 31%;
        top: 35%;
      }

      @media #{$lg-breakpoint} {
        right: 25%;
        top: 50%;
      }

      @media #{$xl-breakpoint} {
        right: 20%;
      }
    }

    .galleryimg1 {

      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 21%;
        top: 23%;
      }

      @media #{$lg-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 40%;
        top: 20%;
      }

      @media #{$xl-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 50%;
      }
    }

    .galleryimg2 {
      margin: 0 auto 20px;
      position: relative;
      width: 288px;

      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        position: absolute;
        width: 303px;
      }

      @media #{$lg-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 30%;
        position: absolute;
        width: 341px;
      }

      @media #{$xl-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 34%;
        position: absolute;
        top: 70%;
        width: 471px;
      }

      .btn-wrapper {
        bottom: 1rem;
        display: block;
        left: 1rem;
        max-width: 24rem;
        min-height: 3.4rem;
        min-width: 16rem;
        position: absolute;
        text-align: left;
        z-index: 3;

        a {
          color: $acr-white;
          display: block;
          text-decoration: none;
        }

        .cta-btn {
          background: $acr-red;
          float: left;
          height: 3.4rem;
          margin-right: 1rem;
          width: 3.4rem;

          span {
            font-size: 1.4rem;
            left: 0;
            line-height: 3.4rem;
            margin: 1rem;
            position: relative;
          }
        }

        .cta-txt {
          display: block;
          font-family: $acr-avenir-ltpro-heavy;
          font-size: 1.3rem;
          line-height: 1.3;
          position: relative;
          text-align: left;
          text-transform: uppercase;
          top: .3rem;
        }
      }
    }

    .galleryimg3 {

      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 20%;
        top: 55%;
      }

      @media #{$lg-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 13%;
        top: 55%;
      }

      @media #{$xl-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        left: 13%;
        top: 40%;
      }
    }
  }

  .features-grid-d {
    @media #{$sm-breakpoint} {
      max-height: 480px;
    }

    @media #{$lg-breakpoint} {
      max-height: 550px;
      min-height: 450px;
    }

    @media #{$xl-breakpoint} {
      min-height: 500px;
    }

    .contentbox1 {
      @media #{$sm-breakpoint} {
        left: 31%;
        top: 40%;
      }

      @media #{$lg-breakpoint} {
        left: 23%;
        top: 40%;
      }

      @media #{$xl-breakpoint} {
      }
    }

    .galleryimg2 {
      margin: 0 auto 20px;
      position: relative;
      width: 289px;

      @media #{$sm-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        position: absolute;
        top: 60%;
        width: 362px;
      }

      @media #{$lg-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        position: absolute;
        right: 27%;
        top: 50%;
        width: 556px;
      }

      @media #{$xl-breakpoint} {
        box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
        position: absolute;
        width: 695px;
      }


      .btn-wrapper {
        bottom: 1rem;
        display: block;
        left: 1rem;
        max-width: 24rem;
        min-height: 3.4rem;
        min-width: 16rem;
        position: absolute;
        text-align: left;
        z-index: 3;

        a {
          color: $acr-white;
          display: block;
          text-decoration: none;
        }

        .cta-btn {
          background: $acr-red;
          float: left;
          height: 3.4rem;
          margin-right: 1rem;
          width: 3.4rem;

          span {
            font-size: 1.4rem;
            left: 0;
            line-height: 3.4rem;
            margin: 1rem;
            position: relative;
          }
        }

        .cta-txt {
          display: block;
          font-family: $acr-avenir-ltpro-heavy;
          font-size: 1.3rem;
          line-height: 1.3;
          position: relative;
          text-align: left;
          text-transform: uppercase;
          top: .3rem;
        }
      }
    }
  }

  .title-text {
    margin-top: 0;
  }

  .desc-text {
    margin-bottom: 0;
  }

  .btn-wrapper {
    text-align: left;
  }

  .video-class-gallery {
    display: block;
    position: relative;

    span {
      color: $acr-white;
      left: 50%;
      position: absolute;
      top: 50%;
      z-index: 10;

      &::before {
        left: -40px;
        position: relative;
        top: -40px;
      };

      &.acr-icon-play-btn {
        font-size: 8rem;
      }
    }
  }
}
