// ----------------------
// ACURA ICON FONTS
//// ---------




// ----------------------
// FONT DECLARATIONS
//// ---------

$acr-font-stack: Arial, sans-serif;

//---------------------------------------
// Avenir
//---------------------------------------

$acr-avenir-ltpro-light: 'AvenirLTPro-Light', $acr-font-stack;
$acr-avenir-ltpro-light-oblique: 'AvenirLTPro-LightOblique', $acr-font-stack;

$acr-avenir-ltpro-medium: 'AvenirLTPro-Medium', $acr-font-stack;
$acr-avenir-ltpro-medium-oblique: 'AvenirLTPro-MediumOblique', $acr-font-stack;

$acr-avenir-ltpro-black: 'AvenirLTPro-Black', $acr-font-stack;
$acr-avenir-ltpro-black-oblique: 'AvenirLTPro-BlackOblique', $acr-font-stack;

$acr-avenir-ltpro-book: 'AvenirLTPro-Book', $acr-font-stack;
$acr-avenir-ltpro-book-oblique: 'AvenirLTPro-BookOblique', $acr-font-stack;

$acr-avenir-ltpro-heavy: 'AvenirLTPro-Heavy', $acr-font-stack;
$acr-avenir-ltpro-heavy-oblique: 'AvenirLTPro-HeavyOblique', $acr-font-stack;

$acr-avenir-ltpro-roman: 'AvenirLTPro-Roman', $acr-font-stack;


//---------------------------------------
// Acura Bespoke
//---------------------------------------

$acr-bespoke-regular: 'Acura Bespoke Regular', $acr-font-stack;
$acr-bespoke-light: 'Acura Bespoke Light', $acr-font-stack;
$acr-bespoke-book: 'Acura Bespoke Book', $acr-font-stack;
$acr-bespoke-bold: 'Acura Bespoke Bold', $acr-font-stack;


// ----------------------
// BREAKPOINT DECLARATIONS
//// ---------

$xs-breakpoint: 'only screen and (min-width: 319px)'; // 320px
$sm-breakpoint: 'only screen and (min-width: 34.375em)'; // 550px
$md-breakpoint: 'only screen and (min-width: 48em)';// 768px
$lg-breakpoint: 'only screen and (min-width: 64em)';// 1024px
$lg-breakpoint-plus-1: 'only screen and (min-width: 1025px)';// 1025px for tablets in landscape which are 1024px wide
//$lg-xl-breakpoint: 'only screen and (min-width: 75em)';// 1200px
$xl-breakpoint: 'only screen and (min-width: 87.5em)';// 1400px
//$mega-breakpoint: 'only screen and (min-width: 120em)';// 1920px


$landscape: 'and (orientation: landscape)';
$portrait: 'and (orientation: portrait)';


// ----------------------
// DESKTOP FIRST BREAKPOINT DECLARATIONS
//// ---------

$sm-breakpoint-max: 'only screen and (max-width: 551px)';


// ----------------------
// IPHONE BREAKPOINT DECLARATIONS
//// ---------
//$iphone6-breakpoint
$iphone6-breakpoint: 'only screen and (min-width: 375px)';
//$iphone6-plus-breakpoint
$iphone6-plus-breakpoint: 'only screen and (min-width: 414px)';
//$galaxy-breakpoint
$galaxy-breakpoint: 'only screen and (min-width: 360px)';
//$footer-breakpoint
$footer-breakpoint: 'only screen and (min-width: 470px)';
//---------------------------------------
// COLORS
//---------------------------------------

// -- #5D6061 -- BODY COPY per AA
$acr-default: #5d6061;

// --- WHITE ---
$acr-white: #fff;
$acr-off-white: #f9fbfc;

// --- BLACK ---
$acr-black: #000;
$acr-black-2: #151718; // FOOTER HOVER "BLACK"

// --- RED ---
$acr-red: #e82c2a;
$acr-active-red: #f50600;
$acr-mlp-red: #f30000;

// --- ORANGE ---
$acr-orange: #ff9600;

// --- GREYS ---
$acr-deep-grey: #1e2123;
$acr-mid-grey: #5e6162; //ICONS & LABELS
$acr-dim-grey-1: #b5b5b5; //EYEBROW & TEXT FIELD
$acr-dim-grey-2: #cdcdcd; // NAVIGATION
$acr-dim-grey-3: #f3f3f3; // SHOPPING TOOLS BG
$acr-dim-grey-4: rgba(136, 134, 134, .1);

$acr-dim-grey-5: #2a2a2a; // UNDERLINES AGAINST BLACK (ie: DEALER LOCATER/SEARCH FIELD)
$acr-dim-grey-6: #272c2f; // FOOTER CIRCLE UNDER ICON
$acr-dim-grey-7: #f2f2f2; // GLOBAL NAV FLYOUTS
$acr-dim-grey-8: #f3f3f3; // GALLERY TABS FOREGROUND
$acr-dim-grey-9: #fefefe; // GALLERY TAB SEAMS
$acr-dim-grey-10: #5d6061; // GLOBAL NAV INPUT PLACEHOLDER
$acr-dim-grey-11: #e7e7e7; // ACCESSORIES BACKGROUND
$acr-dim-grey-12: #ccc; // CHART PACKAGE BORDER
$acr-dim-grey-13: #c6c6c6; // MLP NAV BOTTOM BORDER COLOR
$acr-dim-grey-14: #dbdbdb; // CHART ALERT BORDER
$acr-dim-grey-15: rgb(252, 250, 252); // PROMOTIONS LINEAR GRADIENT
$acr-dim-grey-16: rgb(215, 213, 215); // PROMOTIONS LINEAR GRADIENT
$acr-dim-grey-17: #646464; // BOX SHADOW BASE
$acr-dim-grey-18: #e3e3e3;

// --- BLUES ---
$acr-wire-blue: #004e9a;
$acr-deep-blue: #293859; // ACCOLADES
$acr-gray-blue: #54595f; // MLP NAV OVERVIEW

$acr-bg-color-trans: rgba(0, 0, 0, 0); //transparent


// --- ALERTS ---
$acr-orange: #ff9600;

// -- BORDERS --
$acr-white-50-opacity: rgba(255, 255, 255, .5);
$acr-black-60-opacity: rgba(0, 0, 0, .6);
$acr-black-50-opacity: rgba(0, 0, 0, .5);
$acr-black-43-opacity: rgba(0, 0, 0, .43);
$acr-black-25-opacity: rgba(0, 0, 0, .25);
$acr-black-20-opacity: rgba(0, 0, 0, .2);
$acr-black-15-opacity: rgba(0, 0, 0, .15);

