//Global Spinner
@keyframes slide-spin {

  to {
    transform: rotate(360deg);
  }
}

.animated-spinner {
  left: 50%;
  margin-left: -35px;
  margin-top: -35px;
  position: absolute;
  top: 50%;
  z-index: 2;

  &::before {
    animation: slide-spin .75s linear infinite;
    border-radius: 50%;
    border-right: 2px solid transparent;
    border-top: 2px solid $acr-red;
    content: '';
    height: 70px;
    position: absolute;
    width: 70px;
  }
}
