.acr-accessories {
  background-color: $acr-dim-grey-11;
  margin-top: 30px;
  padding-bottom: 16%;
  padding-left: 0;
  padding-right: 0;
  padding-top: 9%;

  @media #{$sm-breakpoint} {
    margin-top: 60px;
    padding-bottom: 13%;
    padding-top: 6.5%;
  }

  @media #{$lg-breakpoint} {
    background-color: $acr-white;
    padding-bottom: 9%;
    padding-top: 3.5%;
  }

  a {
    border-bottom: 0;
  }

  article {
    overflow: hidden;
    padding: 0;

    &:hover {
      .acr-responsive-background-behavior {
        backface-visibility: hidden;
        transform: scale3d(1.11, 1.11, 1.1);
      }
    }

    .acr-responsive-background-behavior {
      transform: scale3d(1.01, 1.01, 1.01);
      transition: transform .5s cubic-bezier(.335, .015, .46, .995);
    }

    a {
      display: block;
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
    }
  }

  h5 {
    bottom: 0;
    font-size: 3rem;
    left: 0;
    line-height: 3.6rem;
    padding: 0 0 0 2.5rem;
    position: absolute;

    @media #{$sm-breakpoint} {
      font-size: 6rem;
      line-height: 6.8rem;
      padding: 0 0 2.5rem 4rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 3rem;
      line-height: 3.6rem;
      padding: 0 0 2rem 4rem;
    }

    @media #{$lg-breakpoint} {
      padding: 0 0 2rem 3rem;
    }

    span {
      display: block;
    }
  }

  .row {
    margin: 0;
  }

  .acr-responsive-background-behavior {
    background-size: cover;
  }

  .acr-heading {
    margin-bottom: 7%;

    @media #{$sm-breakpoint} {
      margin-bottom: 3.5%;
    }
  }

  p {
    &.acr-larger {
      margin-bottom: 9%;

      @media #{$xl-breakpoint} {
        margin-bottom: 5.5%;
      }
    }
  }

  .accessories-slick {
    padding: 0 2.7rem;
    position: relative;

    &::after {
      border-radius: 15%;
      bottom: 5.5rem;
      box-shadow: 0 3rem 10rem $acr-black-50-opacity;
      content: '';
      display: block;
      height: 10rem;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
      width: 83%;
      z-index: -1;

      @media #{$sm-breakpoint} {
        bottom: 7.25rem;
      }

      @media #{$lg-breakpoint} {
        bottom: 12rem;
        position: relative;
        width: auto;
      }
    }

    @media #{$sm-breakpoint} {
      padding: 0 6rem;
    }

    @media #{$sm-breakpoint} {
      padding: 0 8.33%;
    }
  }

  .slick-next {
    display: inline-block;
    right: 0;
  }

  .slick-prev {
    display: inline-block;
    left: 0;
  }

  .slick-arrow {
    height: 2.2rem;
    margin-top: 0;
    opacity: .5;
    padding: 0;
    transform: translateY(-50%);
    z-index: 1;

    @media #{$sm-breakpoint} {
      height: 5.4rem;
    }

    &:hover {
      opacity: 1;
    }

    &.acr-icon {
      width: 2.2rem;

      @media #{$sm-breakpoint} {
        width: 4.5rem;
      }
    }
  }

  .slick-dots {
    margin-top: 2rem;

    @media #{$sm-breakpoint} {
      margin-top: 3.75rem;
    }
  }
}
