.features-secondary {
  background: $acr-dim-grey-3;
  padding-bottom: 30px;

  @media #{$sm-breakpoint} {
    background: $acr-dim-grey-3;
  }

  @media #{$lg-breakpoint} {
    background: $acr-white;
  }

  @media #{$xl-breakpoint} {
    background: $acr-white;
  }

  .acr-heading {
    padding: 40px 0;
  }

  .tile {
    width: 100%;

    @media #{$lg-breakpoint} {
      max-width: 100%;
      min-height: 510px;
    }

    @media #{$xl-breakpoint} {
      max-width: 100%;
      min-height: 510px;
    }

    .small-viewport-container {
      @include acr-shadow-6;
      margin: 0 auto 30px;
      max-width: 266px;
      min-height: 315px;
      overflow: hidden;
      position: relative;

      @media #{$sm-breakpoint} {
        max-width: 460px;
        min-height: 459px;
      }

      @media #{$lg-breakpoint} {
        margin: 0;
        min-width: 100%;
      }

      @media #{$xl-breakpoint} {
        margin: 0;
        max-width: 100%;
      }
    }

    .bg-tile {
      min-width: 266px;
      position: absolute;

      @media #{$sm-breakpoint} {
        min-width: 460px;
        position: absolute;
      }

      @media #{$lg-breakpoint} {
        min-width: 100%;
        position: relative;
      }

      @media #{$xl-breakpoint} {
        max-width: 100%;
        position: relative;
      }

      img {
        width: 110%;
      }
    }

    .content-tile {
      background: $acr-white;
      bottom: 0;
      left: 0;
      min-height: 140px;
      padding: 24px;
      position: absolute;
      right: 0;
      text-align: center;

      @media #{$sm-breakpoint} {
        min-height: 150px;
        padding: 36px;
      }

      @media #{$lg-breakpoint} {
        background: transparent;
        left: 0;
        max-width: 695px;
        padding: 60px;
        text-align: left;
        top: 0;
      }

      h4 {
        color: $acr-black;
        font-size: 2.3rem;
        margin-bottom: 24px;

        @media #{$lg-breakpoint} {
          font-size: 3.6rem;
          margin-bottom: 50px;
        }
      }

      .desc-tile {
        display: none;

        @media #{$lg-breakpoint} {
          display: block;
          font-size: 1.6rem;
          margin-bottom: 70px;
        }
      }

      .btns-contaner {
        display: none;

        @media #{$lg-breakpoint} {
          display: block;
          line-height: 3rem;
        }

        a {
          display: block;
          line-height: 3rem;
        }
      }

      .mobile-link {
        display: block;

        @media #{$lg-breakpoint} {
          display: none;
        }
      }
    }
  }

  .m-t-60 {
    margin-top: 60px;
  }

  .nav-features {
    display: none;
    margin-top: -30px;
    position: relative;
    width: 100%;
    z-index: 2;

    @media #{$lg-breakpoint} {
      display: block;
    }

    @media #{$xl-breakpoint} {
      display: block;
    }

    li {
      background: $acr-dim-grey-3;
      border: 1px solid $acr-white;
      border-bottom: 0;
      margin: 0;
      padding: 0;
      width: 33.3%;

      &:first-child {
        border-left: 0;
      }

      &:last-child {
        border-right: 0;
      }

      &.active {
        @include acr-shadow-4;
        background: $acr-white;
        border-left: 0;
        border-right: 0;
        box-shadow: 0 2px 30px 0 rgba($acr-black, 1);
        z-index: 10;

        a {
          background: $acr-white;
          color: $acr-black;

          &::after {
            background: $acr-red;
            height: 4px;
            width: 100%;
          }
        }
      }

      a {
        border-radius: 0;
        color: $acr-mid-grey;
        font-family: $acr-avenir-ltpro-heavy;
        padding: 38px 60px;
        position: relative;
        text-align: center;

        &::after {
          background: $acr-red;
          bottom: 0;
          content: '';
          display: block;
          height: 4px;
          left: 0;
          margin: auto;
          position: absolute;
          right: 0;
          transition: width .4s ease, background-color .4s ease;
          width: 0;
        }
      }
    }
  }

  .slick-dots {
    top: 95%;

    button {
      background-color: $acr-deep-grey;
      height: .3rem;
      opacity: .5;
      padding: .3rem;
      width: .3rem;
    }

    .slick-active {
      button {
        background-color: $acr-deep-grey;
        height: 1rem;
        opacity: 1;
        width: 1rem;
      }
    }
  }

  .slick-prev {
    left: 0;
  }

  .slick-next {
    right: 0;
  }

  .slick-arrow {
    background-repeat: no-repeat;
    background-size: contain;
    font-size: 2rem;
    margin-top: -6rem;
    z-index: 1;

    &:hover,
    &:focus {
      color: $acr-black;
    }
  }
}
