.acr-chart {
  padding: 2rem 1.25rem 3.5rem;

  @media #{$sm-breakpoint} {
    padding: 5rem 3rem 3.5rem;
  }

  @media #{$lg-breakpoint} {
    padding: 5rem 3.5rem 6rem;
  }

  h6 {
    padding: 0 1rem;
    text-transform: none;

    @media #{$sm-breakpoint} {
      font-size: 1.8rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 2rem;
      height: 5rem;
      margin-bottom: .3rem;
    }
  }

  p {
    line-height: 1.2em;
    padding: 0 13%;

    @media #{$sm-breakpoint} {
      font-size: 1.6rem;
      padding: 0 28%;
    }

    @media #{$sm-breakpoint} {
      padding: 0;
    }
  }

  .package-container {
    display: block;
    text-align: center;

    @media #{$lg-breakpoint} {
      display: flex;
      justify-content: center;
    }
  }

  .package {
    background-color: $acr-white;
    border-bottom: .4rem solid $acr-dim-grey-12;
    cursor: pointer;
    margin-bottom: 1.1rem;
    padding: 1rem 2.5rem .5rem;
    position: relative;
    text-align: center;
    transition: background-color .2s;

    @media #{$sm-breakpoint} {
      padding-bottom: 1.4rem;
      padding-top: .5rem;
    }

    @media #{$lg-breakpoint} {
      display: inline-block;
      margin-left: 1%;
      padding: 2.5rem .5rem 1rem;
      width: 20%;
    }

    &:hover {
      background-color: $acr-dim-grey-3;

      .icon-alert {
        background-color: $acr-red;
        border-color: $acr-red;
        color: $acr-white;
      }
    }

    &::before {
      border-radius: 20%;
      box-shadow: 0 2.5rem 6rem 0 $acr-black-15-opacity;
      content: '';
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: -1;
    }

    &.active {
      background-color: $acr-red;
      border-color: $acr-red;

      h6,
      p {
        color: $acr-white;
      }

      .acr-cta-text {
        visibility: visible;

        span {
          color: $acr-white;
        }

        &:hover {
          color: $acr-white;

          span {
            &::before {
              background: $acr-white;
            }
          }
        }

        &::after {
          color: $acr-white;
          display: inline-block;
          transform: rotate(90deg);
        }
      }

      .icon-alert {
        background-color: $acr-red;
        border-color: $acr-white;
        color: $acr-white;
      }
    }

    &.related {
      border-color: $acr-red;
    }

    .acr-cta-text {
      font-size: 1.2rem;
      margin-top: .2rem;
      visibility: hidden;

      span {
        color: $acr-white;

        &::after {
          color: $acr-white;
          display: inline-block;
          transform: rotate(90deg);
       }
     }

      @media #{$sm-breakpoint} {
        font-size: 1.4rem;
        margin-top: 1rem;
      }

      @media #{$lg-breakpoint} {
        margin-top: 2rem;
      }
    }
  }

  .icon-alert {
    border: .2rem solid $acr-dim-grey-14;
    border-radius: 50%;
    color: $acr-red;
    font-size: 1.3rem;
    height: 2rem;
    line-height: 2rem;
    position: absolute;
    right: 1.15rem;
    top: 1rem;
    transition: all .2s;
    width: 2rem;
  }

  .cta {
    display: none;
    margin-top: 3rem;
    text-align: right;

    @media #{$lg-breakpoint} {
      display: block;
    }

    @media #{$xl-breakpoint} {
      margin-top: 3.5rem;
    }

    a {
      color: $acr-default;
      text-decoration: underline;
      text-transform: capitalize;
    }
  }
}
