.acr-sign-up-form-emails {
  float: none;
  margin-bottom: 11.9rem;
  margin-top: 11.8rem;
  overflow: hidden;
  position: relative;

  @media #{$sm-breakpoint} {
    margin-top: 0;
  }

  @media #{$lg-breakpoint} {
    margin-bottom: 12.6rem;
  }

  @media #{$xl-breakpoint} {
    margin-bottom: 15.1rem;
  }

  &::after {
    clear: both;
    content: '';
    display: block;
  }

  .acr-sign-up-form-emails-messges {
    display: none;
    font-family: $acr-avenir-ltpro-light;
    font-size: 4rem;
    line-height: 1.1;
    position: absolute;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    z-index: 1;

    .acr-sign-up-form-emails-message {
      margin-bottom: 3rem;
    }
  }

  .acr-sign-up-form-emails-content {
    float: none;
    margin: auto;
    padding: 0 0 1rem;
    z-index: 0;

    &.acr-hidden {
      visibility: hidden;

      label {
        &::after {
          visibility: hidden;
        }
      }
    }
  }

  header {
    margin-bottom: -.2rem;
    margin-top: 4.8rem;

    @media #{$sm-breakpoint} {
      margin-bottom: 1.8rem;
    }

    @media #{$lg-breakpoint} {
      margin-bottom: 1.5rem;
      margin-top: 8.5rem;
    }

    h2 {
      font-family: $acr-bespoke-book;
      font-size: 3.6rem;
      letter-spacing: 0;
      margin-bottom: .9rem;
      text-transform: none;
      word-spacing: .4rem;

      @media #{$lg-breakpoint} {
        font-family: $acr-bespoke-light;
        font-size: 6rem;
        margin-bottom: 1.6rem;
      }
    }

    p {
      line-height: 2.1rem;
      text-align: center;
      word-spacing: .1rem;

      @media #{$lg-breakpoint} {
        font-size: 1.8rem;
      }
    }
  }

  .acr-s-u-f-e-form {
    float: none;
    margin: auto;
    padding: 0;
  }

  .form-group {
    margin-bottom: 0;
  }

  .acr-form-element {
    padding: 3rem 0 0;

    @media #{$lg-breakpoint} {
      padding-top: 4rem;
    }

    &.acr-form-element-error {
      input {
        color: $acr-white;

        +.acr-placeholder {
          background-color: $acr-red;
          color: $acr-white;

          &::after {
            color: inherit;
          }
        }

        &.acr-not-empty {
          +.acr-placeholder {
            color: transparent;

            &::after {
              color: inherit;
            }
          }
        }
      }
    }

    .acr-placeholder {
      font-size: 1.5rem;
      letter-spacing: 1px;
      margin-top: -4.8rem;
      padding: 1.9rem 1.7rem 1.7rem;

      @media #{$lg-breakpoint} {
        font-size: 1.4rem;
        margin-top: -3.9rem;
        padding: 1.5rem 1.7rem 1.3rem;
      }

      @media #{$xl-breakpoint} {
        font-size: 1.4rem;
        margin-top: -4.1rem;
        padding: 1.5rem 1.7rem 1.3rem;
      }
    }

    &.button {
      padding-top: 1.6rem;

      @media #{$sm-breakpoint} {
        padding-top: 3.6rem;
      }

      @media #{$lg-breakpoint} {
        padding-top: 5.1rem;
      }

      .acr-cta-r-w-b {
        display: block;
        float: none;
        letter-spacing: .2rem;
        margin: auto;
        max-width: 21.4rem;

        @media #{$lg-breakpoint} {
          min-width: 100%;
        }
      }

    }

    input {
      bottom: -1rem;
      font-size: 1.5rem;
      padding: 2rem 1.7rem 1.6rem;

      @media #{$lg-breakpoint} {
        font-size: 1.4rem;
        padding: 1.6rem 1.7rem 1.2rem;
      }
    }
  }

  .acr-error {
    color: $acr-red;
    padding: 0;
    position: absolute;
    text-align: center;
    top: 1.2rem;
  }

  .form-input-group {
    margin: auto;
    padding: 0;

    input {
      float: none;
    }
  }

  .checkbox {
    padding-top: 1.7rem;

    @media #{$sm-breakpoint} {
      padding-top: 3.2rem;
    }

    @media #{$lg-breakpoint} {
      padding-top: 4.6rem;
    }

    @media #{$xl-breakpoint} {
      padding-top: 4.4rem;
    }

    label {
      font-size: 1.4rem;
      line-height: 1.6rem;
      position: relative;
      word-spacing: .2rem;

      &::before {
        background-color: $acr-dim-grey-3;
      }

      &::after,
      &::before {
        top: .2rem;

        @media #{$sm-breakpoint} {
          top: -.7rem;
        }
      }
    }
  }
}
