//// ---------------------------------------------------------------------------------------------------  ////
////    Start _layout.scss
//// ---------------------------------------------------------------------------------------------------  ////

body {
  background-color: $acr-white;
}

section {
  @extend %clearfix;
}

.wrapper {
  backface-visibility: hidden;
  margin: 50px auto auto;
  max-width: $wrapper-max-width;
  overflow: hidden;

  @media #{$sm-breakpoint} {
    margin: 60px auto auto;
  }

  @media #{$lg-breakpoint} {
    margin: 70px auto auto;
  }

  &.expanded-nav {
    margin-top: 90px;

    @media #{$sm-breakpoint} {
      margin-top: 125px;
    }

    @media #{$lg-breakpoint} {
      margin-top: 135px;
    }

    header.acr-header {
      top: 0;
    }
  }

  &.collapsed-nav {
    margin-top: 50px;

    @media #{$sm-breakpoint} {
      margin-top: 60px;
    }

    @media #{$lg-breakpoint} {
      margin-top: 70px;
    }

    header.acr-header {
      transform: translate3d(0, -50px, 0);

      @media #{$sm-breakpoint} {
        transform: translate3d(0, -60px, 0);
      }

      @media #{$lg-breakpoint} {
        transform: translate3d(0, -70px, 0);
      }
    }

    nav.mlp-nav {
      transform: translate3d(0, -50px, 0);

      @media #{$sm-breakpoint} {
        transform: translate3d(0, -60px, 0);
      }

      @media #{$lg-breakpoint} {
        transform: translate3d(0, -70px, 0);
      }
    }
  }
}

.blade-wrapper {
  margin: auto;
  max-width: $blade-max-width;

  &.promo-tiles {

    article {
      //height: 300px;
    }

    img {
      position: relative;
      z-index: 1;
    }

    .acr-heading {
      padding: 2rem .4rem;
      position: absolute;
      z-index: 5;

      @media #{$sm-breakpoint}{
        margin: auto;
      }
    }

    h5 {
      bottom: 0;
      position: absolute;
      z-index: 5;
    }
  }

  .acr-cutline {
    color: $acr-black;
    font-family: $acr-avenir-ltpro-light;
    font-size: 1rem;
    padding-bottom: 30px;

    @media #{$sm-breakpoint} {
      font-size: 1.2rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 1.2rem;
    }

    @media #{$xl-breakpoint} {
      font-size: 1.2rem;
    }
  }
}



// Menus
.tabbed-menu { // for use in Features Blades tabbed content
  text-align: center;

  > ul {
    display: inline-block;

    > li {
      @include cta-txt-type($acr-black);
      border: 0;
      display: inline-block;
      letter-spacing: .2rem;
      line-height: 0;
      margin: 0 auto;
      vertical-align: middle;

      &:first-child {
        a::after,
        button::after {
          width: 0;
        }
      }

      &::after {
        transition: color .2s;
      }

      &.active::after,
      &:hover::after {
        color: $acr-red;
        content: '\e92e';
        font-family: 'acura-icon';
        font-size: 6px;
        text-decoration: none;
      }

      +li {
        //border-left: 1px solid $acr-black;
        outline: none;
        padding-left: 4px;
        position: relative;
        text-align: center;
      }

      > a,
      > button {
        border-bottom: 0;
        display: block;
        font-size: 1.5rem;
        letter-spacing: .3rem;
        padding: 10px 39px 17px;
        position: relative;
        transition: color .2s;

        &::after {
          background: $acr-black;
          content: '';
          height: 83%;
          left: 0;
          position: absolute;
          top: -2px;
          width: 1px;
        }

        span.sup {
          display: inline;
          font-size: 1rem;
          left: 0;
          line-height: 0;
          position: relative;
          top: -5px;
        }

      }

      &:hover a,
      &.active:hover a,
      &.active > a,
      &:hover button,
      &.active:hover button,
      &.active > button {
        background-color: transparent;
        border: 0;
        color: $acr-red;

        span.sup {
          color: $acr-red;
        }
      }
    }
  }
}

// SHOPPING TOOLS

.acr-shopping-tools {  // this is just for the style quide for layout.. please pull into your component ..

  .acr-tools {
    border-right: 1px solid $acr-white;
    cursor: pointer;
    display: inline-block;
    padding: 1.5rem 0;
    position: relative;

    .hoverline {
      &::after {
        background: transparent;
        bottom: 0;
        content: '';
        display: block;
        height: 4px;
        left: 0;
        margin: auto;
        position: absolute;
        right: 0;
        transition: width .4s ease, background-color .4s ease;
        width: 0;
      }
    }

    &:hover .hoverline::after {
      background: $acr-red;
      height: 4px;
      width: 100%;
    }

    span {
      display: block;

      &.tool-icon {
        font-size: 5rem;

        @media #{$sm-breakpoint}{
          font-size: 5.7rem;
        }

        @media #{$lg-breakpoint}{
          font-size: 6.3rem;
        }

        @media #{$xl-breakpoint}{
          font-size: 5.7rem;
        }
      }
    }
  }
}

// Use the 'inner-content' as the wrapper class for content within a "acr-grid-item" element
.inner-content {
  overflow: hidden;
}

// Tile Components
// There is one shared component for floating content being used in the 25pt grid
// Use the 'acr-card' class as the main class
.acr-card {
  background-color: $acr-white;
  box-shadow: none;
  padding: 0 40px 20px 35px;

  @media #{$sm-breakpoint} {
    box-shadow: 0 2px 30px 0 rgba($acr-black, .1);
    padding: 40px 40px 40px 35px;
    width: 300px;
  }

  @media #{$lg-breakpoint} {
    width: 390px;
  }


  h4 {
    font-size: 2.4rem;
    line-height: 2.8rem;

    @media #{$sm-breakpoint} {
      font-size: 2rem;
      line-height: 2.4rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 2.6rem;
      line-height: 3.2rem;
    }
  }

  p {
    margin: 2rem 0 .9rem;
  }

  a.acr-cta-text {
    display: block;
  }

  // for the larger text
  .title-text {
    color: $acr-black;
    font-family: $acr-bespoke-light;
    font-size: 2.4rem;
    letter-spacing: .05rem;
    line-height: 2.8rem;
    text-transform: capitalize;
    width: 100%;

    @media #{$sm-breakpoint} {
      font-size: 2rem;
      line-height: 2.4rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 2.6rem;
      line-height: 3.2rem;
    }
  }



  // for the smaller descriptive text
  .desc-text {
    margin: 2rem 0;
    text-align: left;
  }
}

.acr-image {
  img {
    position: relative;
    z-index: 1;
  }

  .inner-inner-content {
    left: 0;
    position: absolute;
    top: 0;

    .title-text,
    .desc-text,
    .btn-wrapper {
      border-bottom: 0;
      position: relative;
      z-index: 2;
    }
  }
}

// offsets for the new href class ..
.cta-wrapper,
.inner-content,
.btn-wrapper {
  a {
    border-bottom: 0;
  }
}

// acr features & packages & specs & gallery blades - common css
.acr-blade-veh-info {
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  .blade-wrapper {
    padding-bottom: 30px;
    padding-top: 30px;
  }

  h3 {
    font-size: 3rem;
    line-height: 1.1;

    @media #{$sm-breakpoint} {
      font-size: 4rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 5rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 6rem;
    }
  }

  // bg height should be height of the bg img // use setHeight

  .info {
    align-items: flex-start;
    display: flex;
    height: auto;
    padding-bottom: 30px;
    padding-top: 24px;

    .row {
      margin: 0;
    }


    &.bottom {
      align-items: flex-end;

      @media #{$lg-breakpoint-plus-1} {
        align-items: center;
      }
    }

    &.top {
      align-items: flex-start;
      top: 0;
    }
  }

  .info-content {
    float: left;
    margin-left: 18px;
    min-height: 1px;
    position: relative;
    width: 75%;

    @media #{$sm-breakpoint} {
      margin-left: 52px;
      margin-top: 13px;
      width: 50%;
    }

    @media #{$md-breakpoint} {
      width: 40%;
    }

    @media #{$lg-breakpoint} {
      margin-top: 44px;
      width: 39%;
    }

    @media #{$xl-breakpoint} {
      margin-left: 130px;
      width: 29%;
    }

    p {
      margin: 2rem 0 0;
    }

    .acr-cutline {
      margin-top: 0;
      padding-bottom: 0;

      @media #{$sm-breakpoint} {
        margin-top: 0;
        padding-bottom: 0;
      }

      @media #{$md-breakpoint} {
        margin-top: 2rem;
        padding-bottom: 30px;
      }

      @media #{$lg-breakpoint} {
        margin-top: 2rem;
        padding-bottom: 30px;
      }

      @media #{$xl-breakpoint} {
        margin-top: 2rem;
        padding-bottom: 30px;
      }
    }

    .acr-cta-text {
      margin-top: 0;

      @media #{$sm-breakpoint} {
        margin-top: 0;
      }

      @media #{$md-breakpoint} {
        margin-top: 1.5rem;
      }

      @media #{$lg-breakpoint} {
        margin-top: 1.5rem;
      }

      @media #{$xl-breakpoint} {
        margin-top: 1.5rem;
      }
    }
  }

  // when there is on bg image
  &.veh-info-nobg {

    .info {

      align-items: flex-start;
      display: flex;
      height: 100%;

      @media #{$sm-breakpoint} {
        h3 {
          display: block;
        }
      }
    }
  }

  &.veh-info-gallery {

    h3 {
      display: none;
    }
  }

  // specs blade
  &.veh-info-specs {

    .responsive-table {
      margin-top: -4px;

      @media #{$sm-breakpoint} {
        margin-top: 9px;
      }

      @media #{$lg-breakpoint} {
        margin-top: 41px;
      }
    }

    h3 {
      line-height: 1;
      margin-bottom: 1rem;
    }

    .info-content {
      width: 100%;
    }
  }
}

footer {
  background: $acr-deep-grey;
}
