.acr-awards {
  height: 50.4rem;
  overflow: hidden;
  position: relative;
  width: 100%;

  &.acr-accolades {
    padding: 0;
  }

  .awards-container {
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;

    .awards-headline {
      position: relative;
      z-index: 1;
    }

    .awards-copy {
      float: none;
      margin-left: auto;
      margin-right: auto;
      position: relative;
      z-index: 1;

      &.acr-cta {
        margin-left: 8rem;
        margin-right: 8rem;
      }
    }

    .stars {
      display: block;
      margin-bottom: 1rem;
    }
  }

  .acr-heading {
    left: auto;
    margin-bottom: 2.8rem;
    margin-top: 5.5rem;

    .acr-underheading {
      margin: auto;
    }
  }

  .headline {
    margin-bottom: 1.2rem;
    padding: 0;
    width: 30rem;
  }

  .subheadline {
    margin-bottom: 2.2rem;
  }

  .acr-bg {
    height: inherit;
    left: -9999px;
    position: absolute;
    right: -9999px;

    img {
      height: 100%;
      position: relative;
      top: 0;
      z-index: 0;
    }
  }
}

@media #{$sm-breakpoint} {
  .acr-awards {
    height: 58rem;
    margin-top: 4.5rem;

    .awards-container {

      .acr-heading {
        margin-bottom: 2.5rem;
        margin-top: -.4rem;
      }

      .headline {
        line-height: 1.2;
        margin-bottom: 2rem;
        margin-top: 3rem;
        width: 43rem;
      }

      .subheadline {
        margin-bottom: 3rem;
      }

      .awards-copy {

        &.acr-cta {
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
  }
}

@media #{$lg-breakpoint} {
  .acr-awards {
    height: 68rem;
    margin-top: 5rem;

    .awards-container {
      padding: 4rem;

      .acr-heading {
        margin-bottom: 2rem;
        margin-top: 5.1rem;
      }

      .headline {
        font-size: 3.4rem;
        margin-bottom: 3rem;
        margin-top: 3rem;
        width: 44.3rem;
      }


      .subheadline {
        font-size: 1.8rem;
        margin-bottom: 4rem;
      }

      .stars {
        margin-bottom: 1.5rem;
      }
    }
  }
}

@media #{$xl-breakpoint} {
  .acr-awards {
    height: 92.8rem;
    margin-top: 0;

    .awards-container {
      margin-top: 11rem;

      .acr-heading {
        margin-bottom: 4rem;
        margin-top: 1rem;
      }

      .headline {
        font-size: 4.5rem;
        line-height: 1.1;
        margin-bottom: 3rem;
        width: 63rem;
      }

      .subheadline {
        font-size: 2.3rem;
        margin-bottom: 4rem;
      }

      .stars {
        margin-bottom: 1.2rem;
      }
    }

    @media #{$xl-breakpoint}{
      background-size: 2000px auto;
    }
  }
}
