// [AR-116] Home Page Section (Quote)
.quote-component {
  //Mobile First (< 551px)
  backface-visibility: hidden;
  background: $acr-white;
  display: none;
  height: 528px;
  overflow: hidden;
  position: relative;

  article {
    .acr-icon-quote {
      font-size: 2rem;
      padding: 0;
      position: absolute;
    }
  }
  //Small Breakpoint +
  @media #{$sm-breakpoint} {
    display: block;

    article {
      left: 0;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;

      blockquote.acr-pull-quote {
        border: 0;

        .acr-icon-quote {
          font-size: 3.1rem;
          margin-left: -4.5rem;
          margin-top: -2rem;
        }

        .underheading {
          background: $acr-dim-grey-2;
          height: 3px;
          margin: 1.2em auto;
          width: 45px;
        }
      }
    }
  }
  //Large Breakpoint +
  @media #{$lg-breakpoint} {
    article {
      blockquote.acr-pull-quote {
        .acr-icon-quote {
          font-size: 3.6rem;
          margin-left: -5rem;
          margin-top: -2.5rem;
        }

        .underheading {
          height: 4px;
          margin: 1.9em auto;
          width: 89px;
        }
      }
    }
  }
  //XL Breakpoint +
  @media #{$xl-breakpoint} {
    article {
      blockquote.acr-pull-quote {
        .acr-icon-quote {
          margin-left: -5rem;
          margin-top: -2.5rem;
        }
      }
    }
  }
}
