$coral: #ff7f50; // for testing purposes
$beige: #f5f5dc; // for testing purposes

.multi-col {
  display: block;
  margin: auto auto 5rem;
  max-width: $wrapper-max-width;
  padding: 4rem 3.4rem 0;
  position: relative;

  a {
    border-bottom: 0;
    display: block;
    height: 100%;
  }

  .col-wrapper {
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    float: none;
    margin: auto;
    min-height: 30.7rem;
    padding: 0;

    .article-wrapper {
      display: flex;
      flex-direction: column;
      height: 100%;
      margin: auto;
      min-height: 30.7rem;
      position: relative;
      top: 0;
      width: 100%;
      z-index: 2;

      .inner-wrapper {
        height: 100%;
        padding-bottom: 0;
        position: absolute;
        width: 100%;
        z-index: 5;

        .content-block {
          padding: 5%;

        }
      }
    }

    article {
      backface-visibility: hidden;
      //background: $coral; // for testing purposes
      //flex: 1;
      flex-direction: column;
      height: 100%;
      overflow: hidden;
      padding: 0;
      position: relative;
      z-index: 5;

      &:nth-child(2n) {
        //background: $beige; // for testing purposes
      }

      &:last-child {
        @media #{$sm-breakpoint} {
          padding-left: .5px;
          z-index: 4;
        }
      }

      &:focus {
        outline: none;
      }

      .multi-col-tile-01 {
        .content-block {
          height: 100%;
          position: relative;
          width: 100%;
          z-index: 10;

          .acr-heading {
            left: 0;
            margin: 0;
          }

          .copy {
            font-size: 6vw;

            @media #{$sm-breakpoint} {
              font-size: 2.8rem;
            }

            padding: 0;
            position: relative;
            z-index: 1;
          }

          .sub-copy {
            font-size: 1.5rem;
            font-weight: bold;
            margin: 1rem 0 .4rem;
            padding: 0;
            position: relative;
            z-index: 1;
          }

          .acr-copy-block {
            bottom: 0;
            left: 0;
            margin-left: auto;
            margin-right: auto;
            position: absolute;
            right: 0;
          }
        }

        h5 {
          bottom: 0;
          margin: 0;
        }

        img {
          display: inline-block;
          left: 0;
          position: relative;
          top: 0;
          transition: -webkit-transform 500ms cubic-bezier(.335, .015, .46, .995);
          width: 100%;
          z-index: 1;
        }

        &:hover img {
          transform: scale3d(1.1, 1.1, 1);
          transform-style: preserve-3d;
        }

        .acr-cta-text span {
          font-size: 1.4rem;
          margin: 0;

          @media #{$sm-breakpoint} {
            margin: .5rem 0;
          }

          text-transform: uppercase;
        }

        .acr-cta-block {
          display: none;
        }
      }

      .multi-col-tile-02 {

        .content-block {
          height: 100%;
          width: 100%;

          .acr-heading {
            left: 0;
            margin: 0;
          }

          .copy {
            border-bottom: 0;
            display: inline-block;
            font-family: $acr-avenir-ltpro-black;
            font-size: 1.4rem;
            letter-spacing: .07rem;
            line-height: 2rem;
            padding: 0;
            position: relative;
            text-transform: uppercase;
            z-index: 1;
          }

          .sub-copy {
            font-family: $acr-bespoke-book;
            font-size: 2.5rem;
            line-height: 5rem;
            margin: 0;
            padding: 0;
            position: relative;
            z-index: 1;
          }

          .acr-copy-block {
            bottom: 3px;
            left: 0;
            margin-left: auto;
            margin-right: auto;

            @media #{$sm-breakpoint} {
              position: relative;
            }

            right: 0;

            @media #{$sm-breakpoint} {
              position: relative;
              top: 50%;
              transform: translateY(-50%);
            }

            > div {
              left: 0;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              width: 100%;
            }
          }

          &:hover .acr-cta-icon {
            &.is-black-bg {
              background: linear-gradient(to right, $acr-black 50%, $acr-white 0%);
              background-color: transparent;
              background-position: left bottom;
              background-size: 200% 100%;
              transition: all .3s ease;

              svg {
                fill: $acr-white;
              }
            }
          }
        }

        h5 {
          bottom: 0;
          margin: 0;
        }

        img {
          display: inline-block;
          left: 0;
          position: relative;
          top: 0;
          transform: translateZ(0) scale3d(1, 1, 1);
          transition: transform 500ms cubic-bezier(.335, .015, .46, .995);
          width: 100%;
          z-index: 0;
        }

        &:hover {
          img {
            transform: scale3d(1.1, 1.1, 1);
            transform-style: preserve-3d;
          }
        }

        .acr-cta-block {
          margin: auto;

          .acr-cta-icon {
            margin: auto;
          }
        }
      }
    }
  }

  @media #{$sm-breakpoint} {

    .col-wrapper {
      box-shadow: 0 17px 50px 0 rgba($acr-black, .2);

      .article-wrapper {
        .inner-wrapper {
          padding-bottom: 1.4rem;
        }
      }

      article {

        h5 {
          margin: 1rem;
        }

        .content-block {
          .acr-copy-block {
            bottom: 0;
          }

          .copy {
            font-size: 2.8rem;
          }
        }

        .multicol-tile-01 {
          .content-block {
            .copy {
              font-size: 2.8rem;
            }
          }
        }

        .multi-col-tile-02 {
          .inner-wrapper {
            align-items: center;
            display: flex;
            justify-content: center;
            padding-bottom: 0;
          }

          .content-block {
            flex: none;

            .acr-copy-block {
              position: relative;
              top: 50%;
              transform: translateY(-50%);
            }

            > div {
              left: 0;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              width: 100%;
            }
          }

          .acr-cta-text span {
            margin: 1rem 0 0;
          }
        }
      }
    }
  }


  @media #{$lg-breakpoint} {
    padding: 4rem 6.1rem 7.6rem;

    .col-wrapper {
      float: none;
      font-size: 0;
      letter-spacing: 0;
      line-height: 0;
      padding: 0;
      position: relative;

      &::before,
      &::after {
        bottom: 0;
        left: 1rem;
        right: 1rem;
      }

      img {
        position: relative;
      }

      .article-wrapper {
        flex-direction: row;
        position: absolute;
      }

      article {
        display: inline-block;
        float: none;
        overflow: hidden;
        padding: 0;

        h5 {
          margin-bottom: 0;
        }

        .acr-cta-text span {
          margin: 1rem 0;
        }
      }
    }
  }

  @media #{$xl-breakpoint} {
    padding-bottom: 13.5rem;
    padding-top: 11.8rem;
  }
}
