.arc-threesixty {
  @media #{$sm-breakpoint} {
    padding-bottom: 40px;
  }

  .display-wrapper {
    margin-top: 30px;
    overflow: hidden;

    .item {
      width: 100%;
    }

    @media #{$sm-breakpoint} {
      margin-bottom: -15px;
      margin-top: 50px;
      padding: 0 15px;
      position: relative;
      z-index: 0;
    }

    @media #{$lg-breakpoint} {
      margin-top: 75px;
      padding: 0 30px;
    }

    @media #{$xl-breakpoint} {
      margin-top: 75px;
      padding: 0 25px;
    }
  }

  iframe {
    border: 0;
    height: 100%;
    max-width: 100%;
    width: 100%;
  }

  .arc-container {
    box-shadow: 0 10px 30px 0 $acr-black-15-opacity;
    margin: 0 auto;
    max-width: 1024px;
    padding: 0 10px;

    @media #{$sm-breakpoint} {
      box-shadow: none;
    }

    .item {
      display: none;

      &.is-active {
        display: block;

        @media #{$sm-breakpoint} {
          display: inline-block;
          line-height: 0;
          vertical-align: top;
        }
      }
    }
  }

  .threesixty {
    height: 540px;
    overflow: hidden;
    position: relative;
    width: 100%;

    &::before {
      background-color: $acr-black-43-opacity;
      bottom: 0;
      content: '';
      display: block;
      left: 0;
      opacity: 0;
      position: absolute;
      right: 0;
      top: 0;
      transition: all .5s ease;
      z-index: 0;
    }

    ol,
    li {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    .threesixty-icon {
      bottom: 0;
      display: none;
      left: 50%;
      margin: 0 auto;
      position: absolute;
      transform: translate(-50%, -50%);
      width: 60px;
      z-index: 3;

      @media #{$sm-breakpoint} {
        bottom: 20px;
        width: 60px;
      }

      @media #{$md-breakpoint} {
        bottom: 20px;
        width: 80px;
      }

      @media #{$lg-breakpoint} {
        bottom: 30px;
        width: 110px;
      }

      img {
        height: auto;
        width: 100%;
      }
    }

    &.is-ready {

      &::before {
        opacity: 1;
        z-index: 2;
      }

      .threesixty-icon {
        display: block;
      }
    }

    .threesixty-images {
      display: none;
      height: 100%;
      z-index: 1;

      img {
        height: auto;
        position: absolute;
        top: 0;
        width: 100%;
      }
    }

    html.ie11 & {
      .current-image {
        z-index: 2;
      }

      .previous-image {
        z-index: 1;
      }
    }

    html:not(.ie11) & {
      .current-image {
        opacity: 1;
      }

      .previous-image {
        opacity: 0;
      }

    }

  }

  .spinner {
    background-color: $acr-default;
    color: $acr-black;
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 5;
  }

  .row {
    margin: 0;

    &.buttons {
      text-align: center;

      .acr-cta-btn {
        margin-left: 0;
        margin-right: 0;
      }

      @media #{$sm-breakpoint} {
        display: inline-block;
      }
    }

    &.bottom {
      font-size: 0;

      @media #{$sm-breakpoint} {
        display: table;
        position: relative;
        width: 100%;
        z-index: 2;
      }
    }
  }


  .color-buttons {
    font-size: 0;
    line-height: 0;

    a {
      border-bottom: 0;
    }

    @media #{$sm-breakpoint} {
      display: inline-block;
      vertical-align: middle;
    }
  }

  .color-selector {
    background-color: $acr-white;
    padding: 15px;

    @media #{$sm-breakpoint} {
      box-shadow: 0 10px 30px 0 $acr-black-15-opacity;
      display: inline-block;
      display: table-cell;
    }

    @media #{$lg-breakpoint} {
      padding: 30px;
    }

    @media #{$xl-breakpoint} {
      padding: 25px;
    }

    .color-swatches {
      @media #{$sm-breakpoint} {
        float: right;
      }
    }

    .threesixty-disclaimer {
      display: block;
      font-size: 1rem;
      line-height: 1.3rem;
      margin-top: 20px;
      text-align: center;
      vertical-align: middle;

      @media #{$sm-breakpoint} {
        display: inline-block;
        margin-right: 15px;
        margin-top: 0;
        text-align: left;
      }
    }

    .color-name {
      color: $acr-black;
      font-size: 1.6rem;
      margin: 20px 0;
      text-align: center;

      @media #{$sm-breakpoint} {
        display: inline-block;
        font-size: 1rem;
        line-height: 1.3rem;
        margin: 0 5px 0 0;
        text-align: left;
        vertical-align: middle;
        width: 70px;
      }

      @media #{$md-breakpoint} {
        font-size: 1.2rem;
        line-height: 1.7rem;
        width: 84px;
      }

      @media #{$lg-breakpoint} {
        font-size: 1.6rem;
        line-height: 2.2rem;
        width: 112px;
      }
    }

    .btn-color-select {
      background-color: $acr-default;
      border-radius: 50%;
      display: inline-block;
      height: 49px;
      margin: 0 8px 12px;
      position: relative;
      width: 49px;

      &:last-child {
        margin-right: 0;
      }

      svg {
        background-color: $acr-active-red;
        border-radius: 50%;
        bottom: 0;
        display: none;
        fill: $acr-white;
        height: 22px;
        padding: 5px;
        position: absolute;
        right: -5px;
        width: 22px;
      }

      @media #{$sm-breakpoint} {
        height: 30px;
        margin: 0 6px 0 0;
        width: 30px;

        svg {
          height: 15px;
          padding: 2px;
          width: 15px;
        }
      }

      @media #{$md-breakpoint} {
        height: 40px;
        margin: 0 6px 0 0;
        width: 40px;

        svg {
          height: 20px;
          padding: 3px;
          width: 20px;
        }
      }

      @media #{$lg-breakpoint} {
        height: 50px;
        margin: 0 13px 0 0;
        width: 50px;

        svg {
          height: 25px;
          width: 25px;
        }
      }

      @media #{$xl-breakpoint} {
        margin: 0 6px 0 0;
      }

      &.is-active {

        svg {
          display: block;
        }
      }
    }
  }

  .build-n-price {
    padding: 40px 0;
    text-align: center;

    @media #{$sm-breakpoint} {
      display: none;
    }

    &.is-internal {
      box-shadow: 0 10px 30px 0 $acr-black-15-opacity;
      display: none;
      padding: 15px;

      svg {
        height: 3rem;
        padding: .5rem 0;
        vertical-align: middle;
        width: 3rem;
      }

      span {
        display: none;

        @media #{$xl-breakpoint} {
          display: inline-block;
          font-family: $acr-avenir-ltpro-black;
          line-height: 2.5rem;
          margin-right: 5px;
          position: relative;
          text-transform: uppercase;
          top: 2px;
        }
      }

      @media #{$sm-breakpoint} {
        background-color: $acr-white;
        display: table-cell;
        width: 10%;

        svg {
          border: 1px solid $acr-bg-color-trans;// for safari bug
          height: 2.5rem;
          width: 4rem;
        }

        .acr-cta-btn {
          min-width: 30px;
          padding-left: 0;
          padding-right: 0;

          &::before {
            line-height: 1.6rem;
          }
        }
      }

      @media #{$md-breakpoint} {
        width: 8%;

        svg {
          width: 5rem;
        }

        .acr-cta-btn {
          min-width: 35px;
        }
      }

      @media #{$lg-breakpoint} {
        padding: 30px;

        svg {
          border: 1px solid $acr-bg-color-trans;// for safari bug
          height: 3rem;
          width: 3rem;
        }

        .acr-cta-btn {
          min-width: 55px;
        }
      }

      @media #{$xl-breakpoint} {
        padding: 25px;
        width: 20%;

        .acr-cta-btn {
          min-width: 150px;

          &::before {
            display: none;
          }
        }
      }
    }
  }

  .cell-spacer {
    display: none;

    @media #{$sm-breakpoint} {
      display: table-cell;
      width: 2%;
    }
  }
}
