//
//          ADDITIONAL VISIBILITY HELPERS
//

.hidden-xl {
  @media #{$xl-breakpoint}{
    display: none;
  }
}

@media #{$xl-breakpoint}{

  .visible-xl {
    display: block;
  }

  .visible-xs,
  .visible-sm,
  .visible-md,
  .visible-lg {

    display: none;
  }
}

.visible-xl {
  display: none;

  @media #{$sm-breakpoint}{
    display: none;
  }

  @media #{$md-breakpoint}{
    display: none;
  }

  @media #{$lg-breakpoint}{
    display: none;
  }

  @media #{$xl-breakpoint}{
    display: block;
  }
}

@media #{$xl-breakpoint}{

  .visible-xl {
    display: block;
  }

  .visible-xs,
  .visible-sm,
  .visible-md,
  .visible-lg {

    display: none;
  }
}



