//
//          HORIZONTAL & VERTICAL ALIGNMENT
//

.text-left { text-align: left; }
.text-right { text-align: right; }
.text-center { text-align: center; }

.v-centered-content {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

//
//          CLEARFIX SOLUTION
//
* {
  box-sizing: border-box;

  &::before,
  &::after {
    box-sizing: border-box;
  }
}

.clearfix {
  // scss-lint:disable PropertySpelling
  *zoom: 1;
  // scss-lint:enable PropertySpelling

  &::before,
  &::after {
    content: ' ';
    display: table;
  }

  &::after {
    clear: both;
  }
}

//
//          ELLIPSIS AUTOMATIC FIX
//

.ellipsis {
  display: block;
  overflow: hidden;
  white-space: nowrap;

  &.multiline {
    white-space: normal;
  }
}

.acr-nowrap {
  display: inline;
  white-space: nowrap;
}

.acr-fixed {
  position: fixed;
}

.acr-no-overflow {
  //iOS requires all this
  height: 100%;
  overflow: hidden;
  position: fixed;
  width: 100%;
}

.acr-inactive {
  opacity: .5;
}
