//Only at tablet bp and above
.acr-header[role=menu] nav[role=menubar] {
  //When inputs are expanded
  &.expanded {
    .logo {
      display: none;
    }

    section {
      &:first-of-type {
        display: none;
      }

      ul.secondary {
        justify-content: center;

        li:not(.expanded) {
          display: none;
        }

        .site-search.expanded {
          justify-content: space-around;
          margin: 0;
        }

        .acr-icon-mag,
        svg {
          border-bottom-color: $acr-deep-grey;
        }

        .acr-icon-locator-error {
          border-bottom-color: $acr-red;
        }

        .dealers-btn,
        .search-link {
          display: none;

          ~ .search {
            opacity: 1;
            right: 50%;
            z-index: 5;

            .acr-icon-locator-error {
              border-bottom: 2px solid $acr-red;
              padding-bottom: 1rem;
              width: 3.8rem;
            }

            svg {
              background: $acr-black;
              border-bottom-style: solid;
              border-bottom-width: 2px;
              margin-left: 0;
              z-index: 2;
            }

            .acr-icon-explore,
            .acr-icon-button-close {
              align-self: center;
              margin-left: 1rem;
            }

            .form.acr-in {
              transform: translateX(0%);
            }
          }
        }

        .dealers-btn ~ .search svg {
          width: 1.8rem;
        }

        .search-link ~ .search svg {
          width: 2.2rem;
        }
      }
    }
  }

  .dealers-btn {
    align-items: center;
    color: $acr-dim-grey-3;
    display: inline-block;
    font-family: $acr-avenir-ltpro-medium;
    font-size: 1.4rem;
    white-space: nowrap;

    svg {
      fill: $acr-white;
      height: 2rem;
      //margin-right: .5rem;
      margin-top: -.4rem;
      vertical-align: middle;
      width: 1rem;
    }

    span {
      letter-spacing: .05rem;
      line-height: 3.1rem;
      vertical-align: middle;
    }

    &:hover {
      color: $acr-dim-grey-1;

      svg {
        fill: $acr-dim-grey-1;
      }
    }

    &.dealers-btn-mobile {
      align-items: center;
      border: 1px solid $acr-mid-grey;
      border-radius: 0;
      display: flex;
      font-family: $acr-avenir-ltpro-black;
      font-size: 1rem;
      height: 3.5rem;
      justify-content: space-bewteen;
      padding-left: 1rem;
      text-transform: uppercase;
      width: 8.5rem;

      svg {
        float: left;
        height: auto;
        margin-right: .35rem;
        margin-top: 0;
        width: .9rem;
      }
    }
  }

  .dealer-locator {
    flex: 1 1 50%;
    position: relative;
    width: 100%;
  }

  section {
    ul.secondary {
      position: relative;

      li {
        flex: 1 1 25%;
        justify-content: center;

        &.dealer-locator {
          flex: 1 1 40%;
        }

        > a {
          color: $acr-dim-grey-3;
          font-size: 1.4rem;
          line-height: 2.4rem;
          text-transform: none;
        }

        > a:hover {
          color: $acr-white;
        }
      }
    }
    //Animated Inputs for Dealer
    //Locator and Site Search
    li.dealer-locator,
    li.site-search {
      .search-link {
        font-size: 2rem;
        vertical-align: middle;

        //Magnifying glass only
        svg {
          float: left;
          height: auto;
        }

        &:hover {
          color: $acr-dim-grey-1;
        }
      }

      .search {
        align-items: flex-end;
        display: flex;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate3d(50%, -50%, 0);
        z-index: -1;

        > a,
        > span {
          margin-left: 1rem;
        }
      }

      .acr-icon-locator-error {
        display: none;
        font-size: 4.2rem;
      }

      .form {
        align-items: flex-end;
        display: inline-flex;
        transform: translateX(-130%);
        transition: .25s all ease-out;

        .field {
          border-bottom: 2px solid $acr-deep-grey;
          display: inline-block;
          padding-left: 1rem;

          input {
            background: transparent;
            border: 0;
            color: $acr-white;
            font-family: $acr-avenir-ltpro-medium;
            font-size: 1.8rem;
            height: 4rem;
            padding: 0;
            width: 26rem;

            &::-webkit-input-placeholder {
              font-family: $acr-avenir-ltpro-heavy;
              font-weight: 600;
            }

            &::placeholder {
              font-family: $acr-avenir-ltpro-heavy;
              font-weight: 600;
              opacity: 1;
            }

            &:placeholder-shown {
              font-family: $acr-avenir-ltpro-heavy;
              font-weight: 600;
            }
          }

          input:-ms-input-placeholder {
            color: $acr-dim-grey-10;
            font-family: $acr-avenir-ltpro-heavy;
          }

          .acr-icon-explore {
            &:hover {
              color: $acr-deep-blue;
            }
          }
        }
      }
      //To display validation error state on inputs
      .acr-form-error {
        .field {
          border-bottom-color: $acr-red;
        }

        // svg {
        //   display: none;
        // }

        // .acr-icon-locator-error {
        //   display: inline-block;
        //   font-size: 3rem;
        // }
      }
    }

    li.site-search .form {
      transform: translateX(130%);
    }
  }
}
//Tablet View
@media #{$sm-breakpoint} {
  .acr-header[role=menu] nav[role=menubar] {
    section {
      ul.secondary li {
        &.dealer-locator .dealers-btn,
        &.site-search .search-link {
          appearance: none;
          background: none;
          border: 0;
          font-size: 1.25rem;
          line-height: 1;
        }

        //Magnifying glass only
        &.site-search svg {
          width: 1.7rem;
        }
      }
    }
  }
}
//Desktop View
@media #{$lg-breakpoint} {
  .acr-header[role=menu] nav[role=menubar] {
    //When inputs are expanded
    &.expanded {
      .logo {
        display: flex;
      }

      section {
        &:first-of-type {
          display: inline-block;
        }

        ul.secondary {
          //justify-content: flex-end;
          //margin-right: 3vw;

          .dealers-btn ~ .search {
            left: 0;
            right: auto;
            transform: translate3d(0, -50%, 0);
          }

          .search-link ~ .search {
            left: auto;
            right: 10%;
            transform: translate3d(0, -50%, 0);
          }
        }
      }
    }

    section ul {
      &.secondary {
        li {
          &.site-search .search-link {
            font-size: 2.8rem;

            svg {
              width: 2.8rem;
            }
          }

          &.dealer-locator .dealers-btn {
            font-size: 1.5rem;

            svg {
              height: auto;
              width: 1.15rem;
            }
          }
        }
      }
    }
  }
}

html.iphone {
  .acr-header[role=menu] nav[role=menubar] {
    section {
      ul.primary {
        li {
          margin-left: 0;

          svg {
            display: none;
          }
        }
      }
    }
  }
}
