.shoping-tools-component {
  background: $acr-white;

  .row {
    margin: 0;
    padding: 0;
  }
//// --------------to remove

  &:hover .tool-icon {
    color: $acr-mid-grey;
  }

  a:hover .tool-icon {
    color: $acr-deep-grey;
  }
//// ---------------------------------------------------------------------------

  .head-section-comp {
    display: none;
  }

  .buttons-container {
    a {
      background: $acr-dim-grey-3;
      border: 1px solid $acr-white;
      border-top: 0;
      color: $acr-mid-grey;
      display: flex;
      flex-direction: column;
      font-family: $acr-avenir-ltpro-heavy;
      font-size: 1.6rem;
      height: 105px;
      justify-content: center;
      line-height: 1.6rem;
      padding-top: 24px;
      text-decoration: none;

      .acr-icon {
        display: block;
        margin: 0 auto 1rem;
        width: 9rem;
      }

      &:hover {
        background-position: 0;

        .acr-desc {
          color: $acr-deep-grey;
        }

        .acr-icon {
          fill: $acr-deep-grey;
        }
      }

      .tool-icon {
        display: block;
        font-size: 7rem;

        @media screen and (min-width:1400px) {
          padding-top: 20px;
        }
      }
    }
  }

  @media screen and (min-width:550px) and (max-width:1023px) {
    .head-section-comp {
      display: block;
      padding-bottom: 34px;
      padding-top: 53px;
    }

    .buttons-container {
      a {
        background: $acr-dim-grey-3;
        font-size: 1.2rem;
        height: 176px;
        padding-top: 10px;
        width: 33.3%;

        .tool-icon {
          font-size: 7rem;
        }
      }
    }
  }

  @media screen and (max-width:549px) {
    .head-section-comp {
      display: block;
      padding-bottom: 34px;
      padding-top: 53px;
    }

    .buttons-container {
      a {
        background: $acr-dim-grey-3;
        font-size: 1.2rem;
        padding-top: 10px;
        width: 33.3%;

        .tool-icon {
          font-size: 5rem;
        }

        .acr-desc {
          margin: 0 auto;
          width: 70px;
        }
      }
    }
  }

  @media screen and (min-width:1024px) {
    .buttons-container {
      a {
        background: $acr-dim-grey-3;
        font-size: 1.4rem;
        height: 176px;
        padding-top: 24px;
      }

      .tool-icon {
        font-size: 7rem;
      }
    }
  }

  .acr-shopping-tools {
    .acr-tools {
      border-right: 1px solid $acr-white;
      cursor: pointer;
      float: left;
      padding: 1.5rem 0;
      position: relative;

      &::after {
        background: transparent;
        bottom: 0;
        content: '';
        display: block;
        height: 4px;
        left: 0;
        margin: auto;
        position: absolute;
        right: 0;
        transition: width .4s ease, background-color .4s ease;
        width: 0;
      }

      &:hover::after {
        background: $acr-red;
        height: 4px;
        width: 100%;
      }

      span {
        display: block;

        &.tool-icon {
          font-size: 5rem;

          @media #{$sm-breakpoint}{
            font-size: 5.7rem;
          }

          @media #{$lg-breakpoint}{
            font-size: 6.3rem;
          }

          @media #{$xl-breakpoint}{
            font-size: 5.7rem;
          }
        }
      }
    }

    .acr-tools-mobile {
      border-right: 1px solid $acr-white;
      cursor: pointer;
      padding: 1.5rem 0;
      position: relative;

      span {
        display: block;

        &.tool-icon {
          font-size: 5rem;

          @media #{$sm-breakpoint} {
            font-size: 5.7rem;
          }

          @media #{$lg-breakpoint} {
            font-size: 6.3rem;
          }

          @media #{$xl-breakpoint}{
            font-size: 5.7rem;
          }
        }
      }
    }
  }
}
