// BUTTONS

// Latest CTA classes

.acr-cta-block { // for SVG icon btns with text (optional)
  cursor: pointer;
  display: table;
  max-width: 300px;

  .acr-cta-icon {
    @extend %acr-cta-base-wicon;
    @include cta-btn-wicon($acr-white, $acr-white, $acr-white, $acr-white, $acr-white, $acr-black, $acr-white);
    float: left;
    margin-right: 10px;

    + .cta-text {
      display: table-cell;
      font-family: $acr-avenir-ltpro-heavy;
      font-size: 1.45rem;
      letter-spacing: .07rem;
      line-height: 1.5rem;
      position: relative;
      text-transform: uppercase;
      top: 3px;
      vertical-align: middle;
    }

    &.is-white {
      + .cta-text {
        color: $acr-white;
      }
    }

    &.is-red {
      @include cta-btn-wicon($acr-red, $acr-bg-color-trans, $acr-red, $acr-red, $acr-red, $acr-white, $acr-black);

      + .cta-text {
        color: $acr-black;
      }
    }

    &.is-static { // default: red bg, white svg, black cta text
      @include cta-btn-wicon($acr-red, $acr-red, $acr-white, $acr-red, $acr-red, $acr-white, $acr-black);
      background: $acr-red;

      + .cta-text {
        color: $acr-black;
      }
    }

    &.is-red-bg {
      @include cta-btn-wicon($acr-red, $acr-red, $acr-white, $acr-red, $acr-red, $acr-black, $acr-black);

      + .cta-text {
        color: $acr-white;

        &.is-dark-theme {
          color: $acr-black;
        }
      }
    }

    &.is-white-bg {
      @include cta-btn-wicon($acr-white, $acr-white, $acr-white, $acr-red, $acr-red, $acr-white, $acr-white);

      + .cta-text {
        color: $acr-red;
      }
    }
  }

  &:hover {

    .is-white {
      background-color: $acr-white;
      border: 2px solid $acr-white;

      svg {
        fill: $acr-black;
      }

      @media #{$sm-breakpoint} {
        background-position: left bottom;
      }
    }

    .is-red {
      background-color: $acr-red;
      border: 2px solid $acr-red;

      svg {
        fill: $acr-white;
      }

      @media #{$sm-breakpoint} {
        background-position: left bottom;
      }
    }

    &.is-static {
      background: $acr-red;

      svg {
        fill: $acr-white;
      }
    }

    .is-red-bg {
      background-position: left bottom;
      border: 2px solid $acr-red;

      svg {
        fill: $acr-white;
      }
    }

    .is-white-bg {
      background-position: left bottom;
      border: 2px solid $acr-red;

      svg {
        fill: $acr-white;
      }
    }

    &.is-black-bg {
      background-position: left bottom;
      border: 2px solid $acr-black;

      svg {
        fill: $acr-white;
      }
    }
  }


}


.acr-cta-btn {
  @extend %acr-cta-base;
  @include cta-btn-type-brdr($acr-red, $acr-bg-color-trans, $acr-white, $acr-red, $acr-white);
  border: 2px solid $acr-red;

  &:hover,
  &:active,
  &:focus,
  &.is-active {
    color: $acr-white;
  }

  &.is-dark {
    @include cta-btn-type-brdr($acr-red, $acr-white, $acr-black, $acr-red, $acr-white);
  }

  &.is-red-bg {
    @include cta-btn-type-full($acr-red, $acr-white, $acr-black);
  }

  &.is-black-bg {
    @include cta-btn-type-full($acr-black, $acr-white, $acr-red);
  }

  &.is-white-bg {
    @include cta-btn-type-full($acr-white, $acr-red, $acr-red);

    &:hover,
    &:active,
    &:focus,
    &.is-active {
      color: $acr-white;
    }
  }

  &.is-black {
    @include cta-btn-type-brdr($acr-black, $acr-white, $acr-black, $acr-black, $acr-white);
  }

  &.is-white {
    @include cta-btn-type-brdr($acr-white, $acr-white, $acr-white, $acr-white, $acr-black);
  }

  &.is-transparent-bg {
    @include cta-btn-type-brdr($acr-red, $acr-bg-color-trans, $acr-white, $acr-bg-color-trans, $acr-white);
  }

  &.is-toggle {
    @include cta-btn-toggle($acr-red, $acr-dim-grey-3, $acr-mid-grey, $acr-white, $acr-dim-grey-12, $acr-mid-grey);
    font-size: 1.2rem;
    min-width: 0;
    padding: 1.4rem 3rem 1rem;

    @media #{$sm-breakpoint} {
      font-size: .9rem;
      padding: .8rem .8rem .5rem;
    }

    @media #{$md-breakpoint} {
      font-size: 1rem;
      padding: 1.2rem 2rem .8rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 1.4rem;
      padding: 1.6rem 2.5rem 1.1rem;
    }
  }

  &.is-icon {
    min-width: 0;
    padding: .6rem 0 .3rem;
    width: 33px;

    + span {
      margin-left: 5px;
    }
  }

  &.is-small {
    font-size: 1rem;
    padding: .8rem 1.5rem .5rem;

    @media #{$sm-breakpoint} {
      font-size: .9rem;
      padding: .8rem .8rem .5rem;
    }

    @media #{$md-breakpoint} {
      font-size: 1rem;
      padding: 1.2rem 2rem .8rem;
    }

    @media #{$lg-breakpoint} {
      font-size: 1.4rem;
      padding: 1.4rem 2.5rem 1rem;
    }
  }

  &.is-spaced {
    margin: 2rem;
  }

  // No fixed widths
  &.is-liquid {
    max-width: none;
    min-width: 0;
  }
}

.acr-cta-text {
  @include cta-txt-type($acr-black);
  border-bottom: 0;

  span {
    &::after {
      content: '\e914';
    }
  }

  &.is-red {
    @include cta-txt-type($acr-red);
  }

  &.is-orange {
    @include cta-txt-type($acr-orange);


    &:hover span,
    &:active span {
      &::before {
        background: $acr-orange;
      }
    }

    span::after,
    &::after {
      color: $acr-orange;
    }

    &:hover {
      color: $acr-orange;

      &::after {
        color: $acr-orange;
      }
    }
  }

  &.is-white {
    @include cta-txt-type($acr-white);
  }

  &.is-modal {
    span {

      &::after {
        content: '\e91c';
      }
    }

    &:hover span,
    &:active span {
      &::before {
        width: calc(100% - 1.9rem);
      }
    }

  }

  &.responsive-table-info {
    span {
      padding-right: 0;
    }
  }
}

// THEMES
// light theme
.light-theme,
