  &.acr-cta-block,
  .acr-cta-block { // for SVG icon btns with text (optional)
    cursor: pointer;
    display: table;
    max-width: 300px;

    .acr-cta-icon {
      @extend %acr-cta-base-wicon;
      @include cta-btn-wicon($acr-red, $acr-bg-color-trans, $acr-red, $acr-red, $acr-red, $acr-white, $acr-black);
      float: left;
      margin-right: 10px;

      + .cta-text {
        color: $acr-black;
        display: table-cell;
        font-family: $acr-avenir-ltpro-heavy;
        font-size: 1.45rem;
        letter-spacing: .07rem;
        line-height: 1.5rem;
        position: relative;
        text-transform: uppercase;
        top: 3px;
        vertical-align: middle;
      }

      &.is-red-bg {
        @include cta-btn-type-full($acr-red, $acr-white, $acr-black);
        padding: .8rem 0 .5rem;

        .acr-icon {
          width: 33px;
        }
      }

      &.is-white-bg {
        background: $acr-white;
        border: 2px solid $acr-red;

        @media #{$sm-breakpoint} {
          background: linear-gradient(to right, $acr-red 50%, $acr-white 0%);
          background-position: right bottom;
          background-size: 200% 100%;
          border: 2px solid $acr-red;
          transition: background .3s ease;

          &:hover,
          &:active,
          &:focus,
          &.is-active {
            background-position: left bottom;
            border: 2px solid $acr-red;
          }
        }
      }

      &.is-black-bg {
        @include cta-btn-type-full($acr-white, $acr-red, $acr-black);
        padding: .8rem 0 .5rem;

        .acr-icon {
          width: 33px;
        }
      }
    }

    &:hover .acr-cta-icon {
      background-color: $acr-red;
      border: 2px solid $acr-red;

      svg {
        fill: $acr-white;
      }

      @media #{$sm-breakpoint} {
        background-position: left bottom;
      }

      &.is-red-bg {
        background-color: $acr-black;
        border: 0;
      }

      &.is-white-bg {
        background-color: $acr-red;
      }

      &.is-black-bg {
        background-color: $acr-black;
        border: 0;
      }
    }
  }

  .acr-cta-text {
    &.is-modal {
      span::after {
        content: '\e91c';
      }

      &:hover span,
      &:active span {
        &::before {
          width: calc(100% - 1.9rem);
        }
      }
    }
  }
}

