// dark theme

.acr-cta { // OLD CTA code
  font-family: $acr-avenir-ltpro-black;
  font-size: 1.7rem;
  letter-spacing: .07rem;
  line-height: 1.5;

  @media #{$sm-breakpoint} {
    font-size: 1.4rem;
    letter-spacing: 0;
    min-width: 17rem;
  }

  @media #{$lg-breakpoint} {
    font-size: 1.45rem;
    margin: auto;
    min-width: 17rem;
    padding: 1.4rem 0;
  }

  &.bttn {
    display: block;
    margin: 3rem;
    padding: 1.5rem 0 1rem;
    text-align: center;
    transition: color .25s ease;

    @media #{$sm-breakpoint} {
      display: table;
      margin: 0 auto;
      padding: 1.5rem 1.5rem 1rem;
    }

    @media #{$lg-breakpoint} {
      margin: auto;
      padding: 1.4rem 3.1rem 1rem;
    }
  }

  // hero area
  &.bttn-hero {
    cursor: pointer;
    display: inline-block;
    margin: 0;
    padding: 0;

    .bttn {
      background: linear-gradient(to right, $acr-white 50%, transparent 0%);
      background-position: right bottom;
      background-size: 200% 100%;
      border: 2px solid $acr-white;
      height: 33px;
      min-width: 0;
      overflow: hidden;
      position: relative;
      transition: all .3s ease;
      width: 33px;

      [class^='acr-icon-'] {
        display: inline-block;
      }

      .acr-hidden {
        left: -100%;
        opacity: 0;
        position: absolute;
        top: 0;
        transition: .2s ease-in-out;
      }
    }

    &:hover .bttn {
      background: $acr-white;
      background-position: left bottom;

      [class^='acr-icon-'] {
        color: $acr-black;
      }

      .acr-hidden {
        opacity: 1;
        transform: translate(100%, 0);
        transition-delay: .2s;
      }
    }

    .cta-text {
      color: $acr-white;
      margin-left: 7px;
      position: relative;
    }
  }


  &.border-red {
    background: linear-gradient(to right, $acr-red 50%, transparent 0%);
    background-position: right bottom;
    background-size: 200% 100%;
    border: 2px solid $acr-red;
    cursor: pointer;
    // -webkit-transition: all .3s ease;
    transition: all .3s ease;

    &:hover {
      background: $acr-red;
      background-position: left bottom;
      color: $acr-white;
    }

    @media #{$lg-breakpoint} {
      font-size: 1.6rem;
      margin: auto;
      padding: 1.4rem 0;
    }
  }


  &.border-black {
    border: 2px solid $acr-black;

    @media #{$sm-breakpoint} {
    }

    @media #{$lg-breakpoint} {
      font-size: 1.6rem;
      margin: auto;
      padding: 1.4rem 0;
    }
  }
}

