$acr-bg-styleguide: #f1f1f1;

.style-guide {

  // all styleguide specific only
  background-color: $acr-white;
  color: $acr-default;
  padding-bottom: 100px;

  .container { //only for style guide purposes
    margin: auto;
    width: auto;
  }

  .content-specific {

    .row {
      border: 1px solid $acr-dim-grey-2;
      border-top: 0;

      &.first {
        border-left: 0;
        border-right: 0;
      }
    }

    *[class^='col-'] {
      padding: 1rem .5rem;
    }

  }

  .shadows {
    .tile {
      background: $acr-white;
      border-radius: 2px;
      display: block;
      float: left;
      height: 200px;
      margin: 1rem 1rem 4rem;
      padding: 1rem;
      position: relative;
      width: 100%;

      h5 {
        position: relative;
        top: 25%;
      }

      h6 {
        position: relative;
        top: 10%;
      }
    }
  }

  .msg {
    padding: 20px;
  }

  .separator {
    border: 0;

    @media #{$lg-breakpoint}{
      border-right: 2px solid $acr-black;

      &.is-left {
        border-left: 2px solid $acr-black;
      }
    }
  }

  blockquote {

    &:nth-child(odd) {
      background: $acr-dim-grey-4;
    }

    p.smaller {
      font-size: 1.3rem;
      line-height: 1.5;
    }

    strong {
      font-size: 1.8rem;

      &.highlight {
        background: $acr-dim-grey-2;
        margin-bottom: 10px;
        opacity: .6;
        padding: 4px;
      }
    }
  }

  ul.colors {
    padding: 10px 20px;

    li {
      color: $acr-white;
      display: inline-block;
      margin: 0;
      padding: 20px;

      &:first-child {
        background: $acr-default;
      }

      &:last-child {
        background: $acr-black;
      }
    }
  }

  .row { // styleguide exception
    margin: 0;
  }

  .btn-block {
    *[class^='col-'] {
      padding: 0;
    }

  }

  .divider-h {
    background-color: $acr-deep-blue;
    height: 2px;
    margin: 2rem 0;
  }

  .class {
    font-family: verdana;
    font-style: italic;
  }

  .blade-wrapper {
    padding-bottom: 30px;
    padding-top: 30px;
  }

  // acr features & packages blades
  .acr-blade-veh-info {
    height: 486px;

    @media #{$sm-breakpoint} { // bg height should be height of the bg img
      height: 486px;
    }

    @media #{$lg-breakpoint} { // bg height should be height of the bg img
      height: 550px;

      h3 {
        display: block;
      }
    }

    @media #{$xl-breakpoint} { // bg height should be height of the bg img
      height: 700px;
    }
  }

  .guide {
    border: 1px solid $acr-dim-grey-1;
    margin: 2em 0;
    padding: 1em;

    @media #{$md-breakpoint}{
      padding: 2em;
    }

    &.first {
      margin-top: 0;
    }


  }

  .m-auto {
    margin: auto;
  }

  .is-underline {
    text-decoration: underline;
  }

  .is-emphasis {
    font-weight: bold;
  }


  // for style guide only - dev should use their own div container for the img
  .img-container {
    cursor: pointer;
    height: 250px;
    overflow: hidden;
    width: 250px;

  }

  .video-container {
    cursor: pointer;
    overflow: hidden;
    position: relative;

    img {
      max-width: 100%;
    }

    @media #{$md-breakpoint}{
      height: 225px;
      width: 400px;
    }

  }


  .tbd {
    background: $acr-dim-grey-3;
    display: block;
    font-size: 13px;
    font-style: italic;
    padding: .2rem 1rem;
    text-align: center;
    width: 20rem;
  }

  .type-styles-usage {
    color: $acr-deep-blue;
    overflow: hidden;
    position: relative;
    text-align: left;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin-bottom: 0;
    }

    .is-small {
      font-size: 90%;
    }

    .is-smaller {
      font-size: 60%;
    }

    .is-smallest {
      font-size: 40%;
    }

    .is-big {
      font-size: 190%;
    }

    .is-bigger {
      font-size: 200%;
    }

    .is-biggest {
      font-size: 250%;
    }

    a.guide-only {
      text-align: center;
    }

    .colors {

      div {
        height: 200px;
        padding: 1em 0 0;
        position: relative;

        @media #{$md-breakpoint}{
          border-bottom: 0;
        }
      }

      span {
        display: block;
        font-size: 1.6rem;
        padding: 0 1em;
        width: 100%;

        &.location {
          bottom: 0;
          line-height: 1rem;
          opacity: .7;
          overflow: hidden;
          padding: 1em;
          position: absolute;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }

  .cta {
    &.bttn {
      margin-bottom: 0;

      @media #{$sm-breakpoint}{
        margin: 3rem;
      }
    }
  }

  .acr-cta-block {
    clear: both;
    margin-bottom: 20px;
    max-width: none;
    padding-right: 0;
    width: auto;

    @media #{$sm-breakpoint}{
      padding-right: 40px;

      &.is-icon-only {
        padding: 0;
      }
    }

    @media #{$md-breakpoint}{
      //clear: none;
      float: left;
    }
  }

  .btn-wrapper {
    clear: both;
    margin-bottom: 20px;

    @media #{$sm-breakpoint}{
      clear: none;
    }
  }

  .divider {
    float: left;
    height: 33px;
    margin: 0 44px;
    width: 1px;

    @media #{$sm-breakpoint}{
      display: none;
    }
  }

  .text {
    .buffer-top {
      margin-top: 2rem;

      @media #{$lg-breakpoint} {
        margin-top: auto;
      }
    }

    .demobox {
      padding: 0 2rem;
    }
  }

  .buttons {

    .demobox {
      padding: 2rem;
    }
  }

  .buttons,
  .text {
    text-align: center;

    .bg-image {// bg image for transparent btn bg example
      background: url('../../img/styleguide/fpo/style-transbtn-bg.jpg');
      background-size: cover;
      padding: 2rem;
    }

    strong {
      display: block;
    }

    a {
      margin: 0;

      strong {
        color: $acr-black;
        text-decoration: none;
      }
    }

    @media #{$sm-breakpoint} {

      a {
        margin: 2rem 0;
      }

      a,
      button,
      .demobox {
        display: inline-block;
        margin: 0;
      }
    }
  }

  .iconfonts div {
    margin: 1rem 0;
  }

  .dot {
    border-radius: 50%;
    display: table-cell;
    padding: 40px;
    vertical-align: middle;
  }

  // SLIDER EXAMPLE
  .styleguide-slider {

    div {
      background: $acr-dim-grey-3;
      margin: auto;
      text-align: center;
      width: 200px;

      @media #{$lg-breakpoint} {
        width: 500px;
      }
    }

    .slick-next {
      display: inline-block;
      right: 0;
    }

    .slick-prev {
      display: inline-block;
      left: 0;
    }

    .slick-arrow {
      height: 2.2rem;
      margin-top: 0;
      opacity: .5;
      padding: 0;
      transform: translateY(-50%);
      z-index: 1;

      @media #{$sm-breakpoint} {
        height: 5.4rem;
      }

      &:hover {
        opacity: 1;
      }
    }
  }

  // SHOPPING TOOLS
  .acr-shopping-tools {  // this is just for the style quide for layout.. please pull into your component ..

    .acr-tools {
      border-right: 1px solid $acr-white;
      cursor: pointer;
      display: inline-block;
      padding: 1.5rem 0;
      position: relative;

      .hoverline {
        &::after {
          background: transparent;
          bottom: 0;
          content: '';
          display: block;
          height: 4px;
          left: 0;
          margin: auto;
          position: absolute;
          right: 0;
          transition: width .4s ease, background-color .4s ease;
          width: 0;
        }
      }

      &:hover .hoverline::after {
        background: $acr-red;
        height: 4px;
        width: 100%;
      }

      span {
        display: block;

        &.tool-icon {
          font-size: 5rem;

          @media #{$sm-breakpoint}{
            font-size: 5.7rem;
          }

          @media #{$lg-breakpoint}{
            font-size: 6.3rem;
          }

          @media #{$xl-breakpoint}{
            font-size: 5.7rem;
          }
        }
      }
    }
  }

  // HEADER + NAV
  .acr-header {
    // DEMO for the hover animation ..
    nav {
      background: $acr-black;
      float: left;
      padding: 0;
      position: relative;
      text-align: center;
      width: 100%;

      &.dealer-locater-search-example {

        ul.secondary {
          display: block;
          float: none;

          li {
            padding: 1rem;
          }
        }

        .dealer-locator,
        .site-search {
          background: $acr-black;
          cursor: pointer;
          float: left;
          left: 0;
          position: relative;

          &:hover {
            .search {
              color: $acr-white;
              opacity: 1;
              position: absolute;
              right: 0;
              transition: .4s;
              transition-delay: .3s;
            }

            .dealer-link,
            .search-link {
              opacity: 0;
              position: relative;
              transition: .6s;
              z-index: -1;
            }
          }

          .dealer-link,
          .search-link {
            opacity: 1;
            padding: 0;
            transition: .6s;
            z-index: 5;
          }

          .search {
            background: $acr-black;
            height: 50px;
            opacity: 0;
            position: absolute;
            top: 1.2rem;
            transition: .6s;
            width: 280px;
            z-index: 5;

            .field {
              border-bottom: 2px solid $acr-dim-grey-5;
              display: inline-block;
              height: calc(100% - 1.2rem);
              width: 70%;

              input,
              .input {
                background: transparent;
                border: 0;
                border-radius: 0;
                color: $acr-white;
                font-family: $acr-avenir-ltpro-medium;
                font-weight: bold;
                height: 100%;
                padding-top: 1rem;
              }

              input:focus,
              .input:focus {
                outline: none;
              }

              .input:not(:focus):invalid {
                background-color: $acr-black;
                border-bottom: $acr-white 1px solid;
                outline: none;
              }

              input:focus:invalid {
                background-color: $acr-black;
              }
            }

            .acr-btn-mag {
              float: left;
              width: 30px;
            }

            .acr-btn-close {
              float: right;
            }

            .acr-btn-search {
              float: right;
              position: relative;
            }
          }
        }

        .dealer-locator {
          &:hover {
            .search {
              right: 0;
              transform: translate(-50%, 0);
            }
          }

          .search {
            left: 0;
            transform: translate(-100%, 0);

            .field {
              .acr-icon-tools-dealer {
                color: $acr-mid-grey;
                font-size: 4rem;
                left: -3rem;
                position: absolute;
              }

              .input,
              input {
                color: $acr-white;
                left: 20px;
                position: relative;
              }
            }
          }
        }

        .site-search {
          .acr-btn-mag {
            span {
              color: $acr-mid-grey;
              font-size: 2.7rem;
              font-weight: bold;
              top: 1rem;

              &:hover {
                color: $acr-white;
              }
            }
          }

          &:hover {

            .search {
              position: absolute;
              right: 0;
              transform: translate(0, 0);
            }
          }

          .search {
            right: 0;
            transform: translate(100%, 0);

            .acr-btn-mag {
              width: auto;

              span {
                font-size: 1.9rem;
              }
            }
          }
        }
      }

      .logo {
        color: $acr-white;
        left: 0;
        margin-left: auto;
        margin-right: auto;
        position: absolute;
        right: 0;

        @media #{$sm-breakpoint}{
          right: auto;
        }
      }

      .top-bar {
        overflow: hidden;
        padding: 1rem 1rem .2rem;

        .menu {
          display: inline-block;
          float: left;
          height: 40px;
          position: relative;
          top: -3px;
          transition: .6s;

          @media #{$sm-breakpoint} {
            display: none;
          }

          &.active {
            @media #{$sm-breakpoint}{
              display: block;
              float: right;
              right: 36px;
            }

            a span {
              font-size: 3.4rem;
              top: 4px;
              width: 0;

              &::after {
                width: 0;
              }
            }

            ~ .btn {
              display: none;
            }
          }

          a {
            color: $acr-white;
            font-family: $acr-avenir-ltpro-black;
            font-size: 1.1rem;
            text-transform: uppercase;

            span {
              background: $acr-white;
              border-radius: 10px;
              display: block;
              height: 3px;
              position: relative;
              width: 34px;

              &::after {
                background: $acr-white;
                border-radius: 10px;
                content: '';
                display: block;
                height: 3px;
                margin-top: 7px;
                position: absolute;
                width: 34px;
              }
            }
          }
        }

        .btn {
          background: $acr-black;
          border: 1px solid $acr-mid-grey;
          border-radius: 0;
          float: right;
          margin: 0;
          padding: 0;// only for style guide

          a {
            color: $acr-white;
            font-family: $acr-avenir-ltpro-black;
            font-size: 1.1rem;
            margin-left: -10px;
            margin-right: 10px;
            text-transform: uppercase;

            &::before {
              color: $acr-white;
              content: '\e918';
              font-family: 'acura-icon';
              font-size: 2.3rem;
              left: 1.1rem;
              position: relative;
              top: -.2rem;
              vertical-align: middle;
            }
          }

          &:hover a {
            color: $acr-dim-grey-1;

            &::before {
              color: $acr-dim-grey-1;
            }
          }
        }

        @media #{$lg-breakpoint}{
          display: none;
        }
      }

      ul.submenu {
        background: $acr-black;
        clear: both;
        margin: 26px 0 0;
        padding: 0;// only for style guide

        a,
        a:active {
          border-bottom: 0;
        }

        li {
          display: block;
          margin: 0;
          padding: 2rem 0;
          text-align: center;
          width: 100%;

          &.active,
          &:active,
          &:hover {
            background: $acr-mid-grey;
          }

          > a {
            color: $acr-white;
            font-family: $acr-bespoke-regular;
            font-size: 1.46rem;
            font-weight: 600;
            position: relative;
            text-transform: uppercase;
            top: .1rem;
          }
        }

        @media #{$lg-breakpoint}{
          display: none;
        }
      }

      @media #{$sm-breakpoint}{
        background: $acr-black;
        padding: 0;

        ul li {
          display: inline-block;
          margin: 0;
          padding: 20px 5px;
        }
      }

      @media #{$lg-breakpoint}{

        ul li {
          padding: 20px;
        }
      }

      ul.primary {
        display: none;

        a,
        a:active {
          border-bottom: 0;
        }

        @media #{$sm-breakpoint}{
          background: $acr-black;
          display: inline-block;
          float: left;
          margin: 0;
          padding: 0;// only for style guide

          li {
            padding-left: 10px;
            padding-right: 3rem;

            &.active,
            &:active,
            &:hover {
              background: $acr-white;
              color: $acr-black;

              &::after {
                visibility: hidden;
              }

              a {
                color: $acr-black;
              }
            }

            &::after {
              color: $acr-red;
              content: '\e91f';
              font-family: 'acura-icon';
              font-size: 4rem;
              position: relative;
              top: -.2rem;
              vertical-align: middle;

            }

            > a {
              color: $acr-white;
              font-family: $acr-avenir-ltpro-heavy;
              font-size: 1.3rem;
              left: 1rem;
              padding-left: .5rem;
              position: relative;
              text-transform: uppercase;
              top: .1rem;
            }
          }
        }

        @media #{$lg-breakpoint}{
          background: $acr-black;
          display: inline-block;
          margin: 0;
          padding: 0;// only for style guide

          li {
            &.active,
            &:active,
            &:hover {
              background: $acr-white;
              color: $acr-black;

              &::after {
                visibility: hidden;
              }

              a {
                color: $acr-black;
              }
            }

            &::after {
              color: $acr-red;
              content: '\e91f';
              font-family: 'acura-icon';
              font-size: 4rem;
              position: relative;
              top: -.2rem;
              vertical-align: middle;

            }

            > a {
              color: $acr-white;
              font-family: $acr-avenir-ltpro-heavy;
              font-size: 1.6rem;
              margin-left: 3rem;
              position: relative;
              text-transform: uppercase;
              top: .1rem;
            }
          }
        }
      }

      ul.secondary {
        display: none;

        a,
        a:active {
          border-bottom: 0;
        }

        @media #{$sm-breakpoint}{

          background: $acr-black;
          display: inline-block;
          float: right;
          height: 64px;
          margin: 0;
          padding: 0;

          li {

            padding: 0 .5rem;
            position: relative;

            .menu {
              margin-right: 10px;
              position: relative;
              top: -10px;

              &.active {
                a span {
                  font-size: 3.4rem;
                  top: 4px;
                  width: 0;
                }

                a span::after {
                  width: 0;
                }

                ~ .btn {
                  display: none;
                }
              }

              a {
                color: $acr-white;
                font-family: $acr-avenir-ltpro-black;
                font-size: 1.1rem;
                text-transform: uppercase;

                span {
                  background: $acr-white;
                  border-radius: 10px;
                  display: block;
                  height: 3px;
                  position: relative;
                  top: -4px;
                  width: 34px;
                }

                span::after {
                  background: $acr-white;
                  border-radius: 10px;
                  content: '';
                  display: block;
                  height: 3px;
                  margin-top: 7px;
                  position: absolute;
                  width: 34px;
                }
              }
            }

            > a {
              color: $acr-dim-grey-2;
              display: block;
              font-family: $acr-avenir-ltpro-heavy;
              font-size: 1.3rem;
              line-height: 2.3;
              padding: 20px 5px;
              position: relative;
              text-transform: none;

              span {
                left: -29px;
                position: absolute;
                top: 15px;
              }

              &:hover {
                color: $acr-white;
              }
            }
          }
        }

        @media #{$lg-breakpoint}{
          background: $acr-black;
          display: inline-block;
          margin: 0;
          padding: 0 1rem 0 0;// only for style guide

          li {
            padding: 0;

            .menu {
              display: none;
            }

            .acr-btn-mag {
              span {
                color: $acr-mid-grey;
                font-size: 2.7rem;
                font-weight: bold;
                top: .5rem;

                &:hover {
                  color: $acr-white;
                }
              }
            }

            > a {
              font-size: 1.5rem;
              padding: 1rem 1.4rem;
              top: .1rem;

              span {
                left: 1.9rem;
                position: relative;
                top: .7rem;
              }

              &:hover {
                color: $acr-white;
              }
            }
          }
        }
      }
    }

    // 'mobile' & 'desktop' & 'tablet' classes are for STYLE GUIDE only
    &.mobile,
    .mobile {
      display: block;

      @media #{$sm-breakpoint}{
        display: none;
      }

      @media #{$lg-breakpoint}{
        display: none;
      }
    }

    &.desktop,
    .desktop,
    &.tablet,
    .tablet {
      display: none;
    }

    &.desktop,
    .desktop {
      @media #{$sm-breakpoint}{
        display: none;
      }

      @media #{$lg-breakpoint}{
        display: block;
      }
    }

    &.tablet,
    .tablet {
      display: none;

      @media #{$sm-breakpoint}{
        display: block;
      }

      @media #{$lg-breakpoint}{
        display: none;
      }
    }

    .flyout {

      a,
      a:active {
        border-bottom: 0;
      }

      &:hover a {
        opacity: .5;

        img {
          filter: grayscale(100%);
          //filter: grayscale(100%);
        }
      }

      &:hover .model:hover a {
        opacity: 1;

        img {
          //filter: none;
          filter: grayscale(0%);
        }
      }

      .model {
        color: $acr-mid-grey;
        font-family: $acr-avenir-ltpro-roman;
        font-size: 1.2rem;
        line-height: 1.4;
        padding: 3rem;

        > a {
          color: $acr-mid-grey;
          font-family: $acr-avenir-ltpro-roman;
          font-size: 1.2rem;
          line-height: 1.4;
          padding: 3rem;
        }

        span {
          color: $acr-black;
          display: block;
          font-size: 1.6rem;
          line-height: 1.4;
        }

        strong {
          color: $acr-black;
          display: block;
          font-family: $acr-avenir-ltpro-black;
          font-size: 1.5rem;
        }

        div {
          display: inline-block;
        }

        img {
          max-width: 100%;
        }
      }
    }
  }

  // DISCLAIMER

  .disclaimer-example {
    border: 0;
    height: 343px;
    overflow: hidden;
    position: relative;

    @media #{$sm-breakpoint}{
      height: 277px;
    }

    @media #{$lg-breakpoint}{
      height: 231px;
    }

    @media #{$lg-breakpoint}{
      height: 182px;
    }
  }

  .acr-disclaimer {
    bottom: -3px;
    display: block;
    position: absolute;
    top: 10px;
    z-index: 200;
  }

  .acr-card {
    margin: 3rem auto;
  }

    // visibility classes issue with xl and lg .for styleguide only
  @media #{$xl-breakpoint}{
