.icon-styleguide {
  text-align: center;

  ul {
    margin: 0;
    padding: 0;
  }

  li {
    display: inline-block;
    height: 11rem;
    margin: 1.2rem 1.2rem 0 0;
    text-align: center;
    width: 15rem;

    span {
      font-size: 1rem;
      word-break: break-all;
    }
  }

  .code {
    background-color: $acr-default;
    border-radius: 5px;
    color: $acr-white;
    display: inline-block;
    padding: 5px 10px;
  }

  .acr-demo-icon {
    background-color: $acr-dim-grey-1;
    display: inline-block;
    line-height: 0;
    margin: .5rem;

    &:hover {
      background-color: $acr-black;

      svg {
        fill: $acr-white;
      }
    }

    svg {
      fill: $acr-black;
    }
  }
}

