// GRID GUIDE COLORS
$color-coral: #ff7f50;
$color-light-blue: #add8e6;
$color-brown: #9a5b10;



// FOR STACKING ELEMENTS ON MOBILE
.acr-pt-grid {
  display: none;
}

.grid-content {
  position: relative;
  top: 0;
  z-index: 20;
}

.acr-grid-25-container {
  padding: 0;

  .acr-grid-item {
    text-align: center;
    z-index: 10;

  }

  .bg {
    position: absolute;
    z-index: 1;

    // You must enter an explicit height if the BG IMG does not exist or is not tall enough
    @media #{$sm-breakpoint} {
      height: 1300px;
    }

    @media #{$lg-breakpoint} {
      height: 800px;
    }

    @media #{$xl-breakpoint} {
      height: 1200px;
    }
  }
}

