.acr-grid-item {
  margin-bottom: 30px;

  @media #{$sm-breakpoint} {
    margin: auto;
  }

  @media #{$lg-breakpoint} {
    margin: auto;
  }

  @media #{$xl-breakpoint} {
    margin: auto;
  }
}

.show-mobile {
  display: block;

  @media #{$sm-breakpoint} {
    display: none;
  }

  @media #{$lg-breakpoint} {
    display: none;
  }

  @media #{$xl-breakpoint} {
    display: none;
  }
}

.show-desktop {
  display: none;

  @media #{$sm-breakpoint} {
    display: block;
  }

  @media #{$lg-breakpoint} {
    display: block;
  }

  @media #{$xl-breakpoint} {
    display: block;
  }
}

.remove-shadow {
  box-shadow: none;

  @media #{$sm-breakpoint} {
    box-shadow: 0 19px 38px rgba($acr-black, .30), 0 15px 12px rgba($acr-black, .22);
  }

  @media #{$lg-breakpoint} {
    box-shadow: 0 19px 38px rgba($acr-black, .30), 0 15px 12px rgba($acr-black, .22);
  }

  @media #{$xl-breakpoint} {
    box-shadow: 0 19px 38px rgba($acr-black, .30), 0 15px 12px rgba($acr-black, .22);
  }
}

.add-shadow {
  box-shadow: 0 19px 38px rgba($acr-black, .30), 0 15px 12px rgba($acr-black, .22);

  @media #{$sm-breakpoint} {
    box-shadow: none;
  }

  @media #{$lg-breakpoint} {
    box-shadow: none;
  }

  @media #{$xl-breakpoint} {
    box-shadow: none;
  }
}
