//// ---------------------------------------------------------------------------------------------------  ////
////    Start _responsive-table-header-sticky-container.scss
//// ---------------------------------------------------------------------------------------------------  ////
.responsive-table-header-sticky-container {
  display: none;
  position: fixed;
  z-index: 1;

  table {
    margin-bottom: 0;
    table-layout: fixed;

    > thead > tr {
      // border-bottom: 3px solid $acr-deep-grey;

      th {
        border-bottom: 0;
        border-right: 1px solid $acr-white;
        color: $acr-white;
        height: 5.3rem;
        position: relative;
        text-align: center;
        text-transform: uppercase;
        vertical-align: middle;

        &:nth-child(2n) {
          border-right: 1px solid $acr-dim-grey-3;
        }

        &.responsive-table-fixed-column {
          background-color: $acr-dim-grey-1;
          text-align: right;
          text-transform: capitalize;

          h3 {
            font-size: 10rem;
          }

          h4 {
            font-family: $acr-bespoke-regular;
            font-size: 2.4rem;
          }

          p {
            color: $acr-deep-grey;
            font-family: $acr-bespoke-light;
            font-size: 1.6rem;
          }
        }

        &.trim-1::after,
        &.trim-2::after,
        &.trim-3::after,
        &.trim-4::after,
        &.trim-5::after {
          background-color: $acr-red;
          bottom: 0;
          content: '';
          display: block;
          height: 4px;
          left: 0;
          margin: auto;
          position: absolute;
          right: 0;
          transition: width .4s ease;
          width: calc(100% - 1px);
        }
      }
    }

    &.responsive-table-header-sticky h4 {
      word-wrap: break-word;
    }
  }

  @media only screen and (max-width: 1023px) {
    .hide-mobile {
      display: none;
    }
  }
}
