// The mixin relies on a baseline of 10px
// The mixin relies on a function to parse the integer from a value with a unit
@function parse-int($n) {
  @return $n / ($n * 0 + 1);
}

@mixin rem($property, $values) {

  // Definition of values for both units
  $px: ();
  $rem: ();

  // Iteration through each value in the given parameter $values
  @each $value in $values {

    // If the value is either auto or 0, Append it to the list as-is
    @if $value == 0 or $value == auto {
      $px: append($px , $value);
      $rem: append($rem, $value);

    // If the value has a unit, Split it to get both the unit and the raw value
    } @else {
      $unit: unit($value);
      $val: parse-int($value);

      // Append values to the lists depending on the unit of the given value
      @if $unit == 'px' {
        $px: append($px, $value);
        $rem: append($rem, ($val / 10 + rem));
      }

      @if $unit == 'rem' {
        $px: append($px, ($val * 10 + px));
        $rem: append($rem, $value);
      }
    }
  }

  // If the two lists are the same, Ouput only one (like margin-top: 0)
  @if $px == $rem {
    // Output the result
    #{$property}: $px;
  } @else {
    #{$property}: $px;
    #{$property}: $rem;
  }
}
