.acr-multi-tab-navigation {
  position: relative;

  &.dark-theme {
    * {
      color: $acr-white;
      fill: $acr-white;
    }

    .acr-multi-tab-header {
      h2,
      p,
      li {
        color: $acr-white;
      }
    }

    .acr-multi-tab-xs-header {
      color: $acr-white;
    }
  }

  &.light-theme {
    * {
      color: $acr-black;
      fill: $acr-white;
    }

    .acr-multi-tab-header {
      h2,
      p,
      li {
        color: $acr-black;
      }
    }

    .acr-multi-tab-xs-header {
      color: $acr-black;
    }
  }

  .acr-multi-tab-xs-header {
    font-family: 'AvenirLTPro-Heavy';
    font-size: 1.4rem;
    position: absolute;
    text-align: center;
    top: 20rem;
  }

  .acr-multi-tab-header {
    padding: 3.5rem 1.8rem;
    position: absolute;
    text-align: center;
    width: 100%;
    z-index: 1;

    @media #{$sm-breakpoint} {
      padding: 4.3rem 2.8rem;
    }

    @media #{$lg-breakpoint} {
      padding: 6.5rem 5.3rem;
    }

    @media #{$xl-breakpoint} {
      padding: 7.5rem 11.3rem;
    }

    h2 {
      margin-top: 2.3rem;

      @media #{$sm-breakpoint} {
        margin-top: 3.1rem;
      }

      @media #{$lg-breakpoint} {
        margin-top: 5.2rem;
      }

      @media #{$lg-breakpoint} {
        margin-top: 6.3rem;
      }

    }

    p {
      font-size: 1.6rem;
      margin-top: -.3rem;

      @media #{$lg-breakpoint} {
        margin-top: -.2rem;
      }

      @media #{$xl-breakpoint} {
        margin-top: 1.6rem;
      }
    }

    .tabbed-menu {
      margin-top: 2rem;

      @media #{$lg-breakpoint} {
        margin-top: 2.8rem;
      }

      @media #{$xl-breakpoint} {
        margin-top: 3.7rem;
      }
    }
  }

  .acr-multi-tab-list {
    list-style: none;

    .acr-multi-tab-button {
      background-color: transparent;
      border: 0;
    }
  }

  .slick-slide {
    position: relative;
  }

  .slick-arrow {
    padding: 2.7rem 0;
    top: 20rem;
    z-index: 1;

    svg,
    use {
      fill: $acr-mlp-red;
      height: 2rem;
    }
  }

  .slick-next {
    right: 0;
  }

  .slick-dots {
    bottom: 0;
    position: absolute;
    top: auto;
    width: 100%;
  }
}
