.acr-gallery,
.acr-mini-gallery {
  margin-bottom: 3.5rem;

  .grid {
    margin: 0;
    width: 100%;

    &.ready {
      display: block;
    }
  }

  .thumb {
    cursor: pointer;
    overflow: hidden;
    padding: 0;
    position: relative;

    &:hover {
      .acr-icon-direction-45 {
        opacity: 1;
      }

      .rollover-bg {
        opacity: .2;
      }
    }
  }

  .thumb-content {
    background-position: center;
    background-size: cover;
    transform: scale(1.005); // Fixes rounding errors on container width that make a 1px gap
    transition: transform .5s cubic-bezier(.335, .015, .46, .995);

    &.hover {
      transform: scale(1.1);
    }
  }

  .play {
    align-items: center;
    color: $acr-white;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    transition: background-color .2s;
    width: 100%;

    &:hover {
      background-color: $acr-black-60-opacity;
    }
  }

  .acr-icon-play-btn {
    font-size: 6rem;

    @media #{$lg-breakpoint} {
      font-size: 7.5rem;
    }

    @media #{$xl-breakpoint} {
      font-size: 10rem;
    }
  }

  .rollover-bg {
    background-color: $acr-black;
    cursor: pointer;
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    transition: opacity .2s;
    width: 100%;
  }

  .acr-icon-direction-45 {
    border: .1rem solid $acr-white;
    bottom: .9rem;
    color: $acr-white;
    font-size: .8rem;
    opacity: 0;
    padding: .8rem;
    position: absolute;
    right: .9rem;
    transition: opacity .2s;

    @media #{$sm-breakpoint} {
      bottom: 1.5rem;
      font-size: .9rem;
      padding: .9rem;
      right: 1.5rem;
    }

    @media #{$lg-breakpoint} {
      bottom: 1.3rem;
      right: 1.3rem;
    }

    @media #{$xl-breakpoint} {
      bottom: 1.8rem;
      font-size: 1.2rem;
      padding: 1.2rem;
      right: 1.8rem;
    }
  }

  .view-colors-360 {
    margin: 0;
  }

  .view-full-gallery {
    margin: 3.5rem 3rem 2.8rem;

    @media #{$sm-breakpoint} {
      margin: 4.5rem auto 9.6rem;
    }

    @media #{$lg-breakpoint} {
      margin: 5rem auto 3.2rem;
    }

    @media #{$xl-breakpoint} {
      margin: 7.5rem auto 3rem;
    }
  }
}

.acr-gallery {

  .header {
    background-color: $acr-white;
    padding: 3rem 0;

    @media #{$sm-breakpoint} {
      margin-top: -2px;
      padding: 4rem 0;
    }

    @media #{$lg-breakpoint} {
      margin-top: 21px;
      padding: 5rem 0;
    }

    @media #{$xl-breakpoint} {
      margin-top: 9px;
    }


    .title {
      display: none;

      @media #{$sm-breakpoint} {
        display: block;
        line-height: 1;
      }
    }

    h3 {

      @media #{$sm-breakpoint} {
        line-height: 1.1;
        margin-left: 50px;
      }

      @media #{$md-breakpoint} {
        margin-top: -3px;
      }

      @media #{$xl-breakpoint} {
        line-height: 1.4;
        margin-left: 130px;
        margin-top: auto;
      }
    }
  }

  .tabs-container {
    @media #{$sm-breakpoint} {
      padding: 0;
    }

    @media #{$lg-breakpoint} {
    }
  }

  .tabs {
    background-color: $acr-dim-grey-9;
    font-size: 1.3rem;
    margin: 0 1.2rem;
    position: relative;
    text-align: center;

    @media #{$sm-breakpoint} {
      font-size: 1.2rem;
      margin-left: .5rem;
    }

    @media #{$xl-breakpoint} {
      font-size: 1.6rem;
    }
  }

  .tab {
    padding: 0;

    &:last-child {
      .tab-content {
        margin-right: 0;
      }
    }
  }

  .tab-content {
    background-color: $acr-dim-grey-8;
    margin-right: .1rem;
    position: relative;
    z-index: 5;

    &.active {
      @include acr-shadow-4;
      background-color: $acr-white;
      z-index: 10;


      &::after {
        width: 100%;
      }
    }

    &::after {
      background: transparent;
      border-bottom: 2px solid $acr-red;
      bottom: 0;
      content: '';
      display: block;
      height: 2px;
      position: absolute;
      transition: width .4s ease, background-color .4s ease;
      width: 0;
    }

    .tab-anchor {
      border-bottom: 0;
      color: $acr-default;
      display: block;
      height: 100%;
      padding: 1.5rem 0 1.2rem;

      @media #{$lg-breakpoint} {
        padding: 2.3rem 0 2rem;
      }

      @media #{$xl-breakpoint} {
        padding: 3rem 0 2.7rem;
      }
    }
  }

  .grid {
    transition: opacity .2s;

    &.fade-out {
      opacity: 0;
    }

    .thumb {
      display: none;

      &.visible {
        display: block;
      }
    }
  }
}

.acr-mini-gallery {
  .title {
    margin: 3.5rem 0;

    @media #{$sm-breakpoint} {
      margin: 3.5rem 0;
    }

    @media #{$lg-breakpoint} {
      margin: 4.5rem 0;
    }

    @media #{$xl-breakpoint} {
      margin: 8rem 0 7.5rem;
    }
  }
}
