.key-story {
  position: relative;

  .row {
    margin: 0;
    padding: 0;
  }

  .bg {
    background-position: center 2.2rem;
    background-repeat: no-repeat;
    background-size: 1050px auto;
    border: 0;
    min-height: 645px;
    position: absolute;
    width: 100%;

    @media #{$sm-breakpoint} {
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 133.5rem auto;
      min-height: 793px;
    }

    @media #{$lg-breakpoint} {
      background-position: center -6.75rem;
      background-repeat: no-repeat;
      background-size: 140.5rem auto;
      min-height: 78.9rem;
    }

    @media #{$xl-breakpoint} {
      background-position: center -6.75rem;
      background-repeat: no-repeat;
      background-size: 2000px 950px;
    }
  }

  .acr-heading {
    padding: 50px 0;
  }

  .blade-wrapper {
    position: relative;
  }

  .head-section-comp {
    position: relative;
    z-index: 20;

    .acr-heading {
      h6 {
        color: $acr-black;

        @media #{$sm-breakpoint} {
          color: $acr-white;
        }

        @media #{$lg-breakpoint} {
          color: $acr-white;
        }

        @media #{$xl-breakpoint} {
          color: $acr-white;
        }
      }
    }

    .acr-underheading {
      margin: 0 auto;

      @media #{$sm-breakpoint} {
        margin: 0;
      }

      @media #{$lg-breakpoint} {
        margin: 0;
      }

      @media #{$xl-breakpoint} {
        margin: 0;
      }
    }

    @media #{$sm-breakpoint} {
      left: 35px;
      position: absolute;
      top: 0;
    }

    @media #{$lg-breakpoint} {
      left: 3%;
      position: absolute;
      top: 0;
    }

    @media #{$xl-breakpoint} {
      left: 4%;
      position: absolute;
      top: 0;
    }
  }

  .acr-grid-25-container {
    margin-bottom: 4rem;
    min-height: 64.5rem;
    overflow: inherit;
    padding: 0;

    @media #{$sm-breakpoint} {
      min-height: 78.9rem;
    }

    @media #{$lg-breakpoint} {
      min-height: 76.9rem;
    }

    @media #{$xl-breakpoint} {
      min-height: 88.1rem;
    }

    .acr-grid-item {
      position: relative;

      @media #{$sm-breakpoint} {
        position: absolute;
      }

      @media #{$lg-breakpoint} {
        position: absolute;
      }

      @media #{$xl-breakpoint} {
        position: absolute;
      }

      .inner-content {
        overflow: inherit;
      }

      &.nsx-car {
        margin-bottom: 2rem;
        text-align: left;

        @media #{$sm-breakpoint} {
          bottom: 3.1rem;
          text-align: left;
        }

        @media #{$lg-breakpoint} {
          bottom: 10%;
          margin-left: 0;
        }

        @media #{$xl-breakpoint} {
          bottom: 100px;
          text-align: center;
        }

        img {
          box-shadow: 0 2px 30px 0 rgba($acr-black, .45);
          max-height: 175px;

          @media #{$sm-breakpoint} {
            max-height: 222px;
          }

          @media #{$lg-breakpoint} {
            max-height: 250px;
          }

          @media #{$xl-breakpoint} {
            max-height: 352px;
          }
        }
      }

      &.takeitsign {
        margin: 0 auto 9.1rem;
        max-width: 29.25rem;

        img {
          width: 100%;
        }

        @media #{$sm-breakpoint} {
          margin-top: 30px;
          min-width: 570px;

          img {
            width: 100%;
          }
        }

        @media #{$lg-breakpoint} {
          margin-top: 30px;
          min-width: 914px;

          img {
            width: 100%;
          }
        }

        @media #{$xl-breakpoint} {
          margin-top: 30px;
          min-width: 1280px;

          img {
            width: 100%;
          }
        }
      }

      &.copytext {
        margin: 0 auto;
        max-width: 32rem;
        padding: 0 2.3rem;

        @media #{$sm-breakpoint} {
          margin-top: 130px;
          min-width: 410px;
          padding: 0;
        }

        @media #{$lg-breakpoint} {
          margin-top: 0;
          text-align: left;
        }

        @media #{$xl-breakpoint} {
          margin-top: 0;
          text-align: left;
        }

        .desc-text {
          font-size: 1.4rem;
          margin-bottom: 4.5rem;

          @media #{$sm-breakpoint} {
            font-size: 1.75rem;
          }

          @media #{$lg-breakpoint} {
            font-size: 2.125rem;
          }

          @media #{$xl-breakpoint} {
            font-size: 2.125rem;
            margin-top: 0;
            text-align: left;
          }
        }

        .btn-wrapper {
          margin-bottom: 23px;
          position: relative;
          text-align: center;

          @media #{$lg-breakpoint} {
            text-align: left;
          }

          @media #{$xl-breakpoint} {
            text-align: left;
          }

          a {
            color: $acr-white;
            text-align: center;
          }
        }

      }

      .image-max {
        max-width: 50%;
      }
    }
  }
}
