//// ---------------------------------------------------------------------------------------------------  ////
////    Start _responsive-table.scss
//// ---------------------------------------------------------------------------------------------------  ////
.responsive-table {
  background-color: $acr-white;
  margin: 0 auto;

  a {
    border-bottom: 0;
  }

  .acr-cutline {
    text-align: center;
  }

  // Header on Mobile
  .responsive-table-header-mobile {
    @include rem(padding, 0 18px 20px);

    .responsive-table-mobile-title {
      padding: 0 1.5rem 0 0;

      @media #{$sm-breakpoint} {
        padding: 0 1.5rem 0 3.3rem;
      }
    }

    img {
      @include rem(margin, 0 auto 20px);
      display: block;
      max-width: 100%;
    }

    ul.acr-trim-tiles {
      display: block;
      list-style: none;
      padding: 0;
      text-align: center;

      .acr-trim-tile {
        background-color: $acr-dim-grey-3;
        border-right: 1px solid $acr-white;
        cursor: pointer;
        display: flex;
        flex-flow: column;
        font-family: $acr-avenir-ltpro-heavy;
        height: 5rem;
        justify-content: center;
        line-height: 1.1;
        margin-top: 1px;
        padding: 0;
        position: relative;
        text-align: center;
        vertical-align: middle;

        &::after {
          background-color: $acr-red;
          bottom: 0;
          content: '';
          display: block;
          height: 4px;
          left: 0;
          margin: auto;
          position: absolute;
          right: 0;
          transition: width .4s ease;
          width: 0;
        }

        &.active {
          background-color: $acr-white;
          border-right: 0;
          box-shadow: 6px 10px 87px 0 rgba($acr-dim-grey-17, .3);
          z-index: 10;

          &::after {
            width: 100%;
          }
        }

        span {
          display: inline-block;
          font-size: 1.4rem;
          padding: 1rem;
        }
      }
    }
  }

  .disclaimer-callout {
    z-index: initial;
  }

  .table {
    margin-bottom: 5rem;
    table-layout: fixed;

    &.responsive-table-header-sticky h4 {
      word-wrap: break-word;
    }

    // Table thead Static
    &.responsive-table-static {
      @include rem(margin-bottom, 50px);
      text-align: center;

      > thead > tr {
        &.responsive-table-desktop th {
          @include rem(height, 40px);
          border-top: 0;
          text-transform: none;
          width: 13%;

          &:first-child {
            text-align: left;
            width: 26%;

            h4 {
              font-size: 4.4rem;
            }
          }
        }

        &.responsive-table-tablet th {
          text-transform: none;
        }

        > th {
          color: $acr-red;
          height: auto;

          &:last-child {
            border-right: 0;
          }

          &.capitalize {
            text-transform: capitalize;
          }

          h4 {
            word-wrap: break-word;
          }

          a {
            @include rem(margin-top, 10px);
            display: inline-block;
            text-transform: uppercase;

            &:hover,
            &:focus {
              text-decoration: none;
            }
          }

          img {
            display: block;
            margin: 0 auto;
            max-width: 100%;
          }
        }
      }
    }

    > thead > tr {
      > th {
        @include rem(height, 53px);
        background: $acr-white;
        border-bottom: 0;
        border-top: 0;
        color: $acr-white;
        text-align: center;
        text-transform: uppercase;
        vertical-align: middle;

        &:last-child {
          border-right: 0;
        }

        &:nth-child(2n) {
          background-color: $acr-dim-grey-3;
        }

        &.acr-trim-header {
          position: relative;

          &::after {
            background-color: $acr-red;
            bottom: 0;
            content: '';
            height: 4px;
            left: 1px;
            position: absolute;
            width: calc(100% - 2px);
          }
        }

        &.responsive-table-fixed-column {
          background-color: $acr-white;
          border-right: 0;
          padding: 6px 12px 6px 6px;
          text-align: right;
          text-transform: capitalize;
        }

        h4 {
          font-family: $acr-avenir-ltpro-heavy;
          font-size: 1.7rem;
        }
      }
    }

    > tbody > tr {
      border-bottom: 1px solid $acr-dim-grey-13;

      &.responsive-table-has-subcategory {
        border-bottom: 0;

        > td:not(.responsive-table-fixed-column) {
          background-color: $acr-white;
        }
      }

      &.responsive-table-subcategory {
        border-bottom: 0;

        + tr:not(.responsive-table-subcategory) {
          border-top: 1px solid $acr-dim-grey-13;
        }

        .subcategory-row-label {
          font-family: $acr-avenir-ltpro-medium;
        }

        td:not(.responsive-table-fixed-column) {
          border-bottom: 1px solid $acr-dim-grey-13;
        }
      }

      // Accordion
      &.responsive-table-title-accordion {
        border-bottom: 0;

        &.fixed {
          position: fixed;
          top: 0;
          z-index: 1;
        }

        td.responsive-table-title-accordion-link {
          @include rem(height, 65px);
          background-color: $acr-white;
          border-bottom: .2rem solid $acr-dim-grey-13;
          cursor: pointer;
          text-align: left;

          h4 {
            color: $acr-black;
            font: 2rem  $acr-avenir-ltpro-black;
            line-height: 2;
            text-transform: uppercase;

            .section-description {
              font: 1.6rem $acr-avenir-ltpro-book;
              text-transform: none;

              svg {
                height: 1rem;
              }
            }

            .minus-icon,
            .plus-icon {
              float: right;
              font-size: 0;

              svg {
                fill: $acr-red;
                height: 2.8rem;
                width: 2.8rem;
              }
            }

            .plus-icon {
              display: inline-block;
            }

            .minus-icon {
              display: none;
            }

            &.active {
              .minus-icon {
                display: inline-block;
              }

              .plus-icon {
                display: none;
              }
            }
          }
        }
      }

      &:last-child {
        border-bottom: 0;
      }

      > td {
        @include rem(height, 100px);
        border-top: 0;
        text-align: center;
        vertical-align: middle;
        width: 13%;

        &:nth-child(even) {
          background-color: $acr-dim-grey-3;
        }

        &:nth-child(odd) {
          background-color: $acr-white;
        }

        &.responsive-table-fixed-column {
          letter-spacing: 0;
          line-height: 1.5rem;
          padding: 6px 12px 6px 6px;
          text-align: right;
          width: 26%;

          @media #{$md-breakpoint} {
            letter-spacing: 0;
            line-height: 1.5rem;
          }

          @media #{$lg-breakpoint} {
            letter-spacing: .05rem;
            line-height: 1.8rem;
          }

          svg {
            fill: $acr-active-red;
            height: 1.6rem;
            margin-top: .1rem;
            right: .3rem;
            top: 0;
            vertical-align: text-bottom;
            width: 1.6rem;
          }

          a {
            &:hover,
            &:focus {
              color: $acr-red;
              text-decoration: none;
            }
          }

          h5 {
            font: 1.7rem $acr-avenir-ltpro-medium;
            margin-bottom: .2rem;
            position: relative;
            text-transform: uppercase;
            word-wrap: break-word;
          }

          p {
            color: $acr-black;
            font-size: 1.4rem;
          }
        }

        svg {
          @include rem(height, 14px);
          @include rem(width, 14px);
          fill: $acr-black;
        }
      }

      // Details
      &.responsive-table-detail {
        border-bottom: 0;

        td {
          border: 0;
          height: auto;
          padding: 0;
          position: relative;
        }

        .collapse.in {
          border-bottom: 1px solid $acr-dim-grey-13;
          overflow: hidden;
        }

        span.responsive-table-detail-close {
          cursor: pointer;
          position: absolute;
          right: 1.5rem;
          top: 2.5rem;

          @media only screen and (max-width: 1023px) {
            top: 1rem;
          }

          svg {
            @include rem(height, 30px);
            @include rem(width, 30px);
            fill: $acr-deep-grey;
            vertical-align: middle;

            &:active,
            &:hover,
            &:focus,
            &:active:focus {
              fill: $acr-red;
            }
          }
        }
      }
    }
  }

  .responsive-table-header-title {
    margin-left: 25px;
    padding-bottom: 5rem;
    position: relative;

    @media #{$sm-breakpoint} {
      margin-left: 52px;
    }

    @media #{$xl-breakpoint} {
      margin-left: 130px;
      margin-top: 27px;
    }
  }
  // Styles on Mobile
  @media only screen and (max-width: 1023px) {
    .show-mobile {
      display: block;
    }

    .hide-mobile {
      display: none;
    }

    .table {
      table-layout: auto;

      > tbody > tr {
        // Accordion
        &.responsive-table-title-accordion {
          td.responsive-table-title-accordion-link {
            @include rem(height, 50px);

            h4 {
              font-size: 1.4rem;
            }

            .section-description {
              display: none;
            }
          }
        }

        // Table <tbody>
        > td {
          @include rem(height, 76px);
          width: 50%;

          &.responsive-table-fixed-column {
            width: 50%;
          }

          &.active {
            background-color: $acr-dim-grey-3;
            display: table-cell;
          }
        }
      }
    }
  }

  @media only screen and (min-width: 551px) {
    .show-mobile {
      display: none;
    }
  }

  // Styles on Tablet
  @media only screen and (min-width: 551px) and (max-width: 768px) {
    .hide-tablet {
      display: none;
    }

    .table {
      // Table thead Static
      &.responsive-table-static > thead > tr {
        border-bottom: 0;

        &.responsive-table-desktop > th {
          @include rem(height, 24px);
          border-top: 0;
        }

        th {
          a {
            padding: 0;
          }
        }
      }

      > tbody > tr {
        // Accordion
        &.responsive-table-title-accordion {
          td.responsive-table-title-accordion-link {
            @include rem(height, 40px);
          }
        }
      }
    }
  }

  // Styles on Desktop
  @media only screen and (min-width: 769px) {
    .hide-desktop {
      display: none;
    }
  }

  tr.responsive-table-detail.no-border.responsive-table-subcategory.powertrain {
    .responsive-table-colspan {
      border-bottom: 0;
    }
  }
}
