.acr-feature-modal {
  background-color: $acr-white;
  display: none;
  height: 100vh;
  left: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1000;

  @media #{$sm-breakpoint} {
    background-color: $acr-black-50-opacity;
    padding: 0 8.3%;
  }

  &.visible {
    align-items: center;
    display: flex;
    justify-content: center;

    .collapse-content {
      .header {
        svg {
          display: inline-block;
        }
      }
    }
  }

  img {
    width: 100%;
  }

  h5 {
    font-size: 3rem;
    padding-right: 15%;
  }

  p {
    margin-bottom: 1em;
  }

  picture {
    font-size: 0;
    overflow: hidden;

    @media #{$sm-breakpoint} {
      order: 2;
      width: 50%;
    }
  }

  .feature-modal-window {
    overflow: hidden;
    transition: all .2s;

    @media #{$sm-breakpoint} {
      .content-container picture {
        width: 50%;
      }
    }

    .animated-spinner-container {
      background-color: $acr-white;
      height: 30vh;
      width: 40vw;
    }

    .header {
      background-color: $acr-white;
      height: 5.5rem;
      position: relative;

      @media #{$sm-breakpoint} {
        height: auto;
        padding: 1.8rem 3rem 1rem;
      }

      @media #{$xl-breakpoint} {
        padding: 1.8rem 4rem;
      }

      .header-name {
        @media #{$sm-breakpoint} {
          display: inline-block;
          font-size: 2.4rem;
          line-height: 2.4rem;
        }

        @media #{$xl-breakpoint} {
          font-size: 3rem;
          line-height: 3.3rem;
        }
      }

      .acr-icon {
        cursor: pointer;
        height: 2.7rem;
        margin: 1.3rem .8rem;
        position: absolute;
        right: 0;
        top: 0;
        width: 2.7rem;

        @media #{$sm-breakpoint} {
          height: 3.2rem;
          margin: 1.8rem 2.9rem;
          width: 3.2rem;
        }

        @media #{$xl-breakpoint} {
          height: 4.2rem;
          margin: 1.8rem 3.9rem;
          width: 4.2rem;
        }
      }
    }

    .content-container {
      background-color: $acr-white;
      position: relative;

      @media #{$sm-breakpoint} {
        display: flex;
        max-height: calc(100vh - 7.5rem);
        padding: 0 3rem 3rem;
      }

      @media #{$xl-breakpoint} {
        padding: 0 4rem 4rem;
      }

      .content {
        min-height: 10rem;
        overflow: auto;
        padding: .3rem 1.7rem;

        @media #{$sm-breakpoint} {
          order: 1;
          padding: 0;
          width: 50%;
        }

        @media #{$sm-breakpoint} {
          max-height: calc(100vh - 8.5rem);
        }

        .content-name {
          @media #{$sm-breakpoint} {
            display: none;
          }
        }

        .description {
          @media #{$sm-breakpoint} {
            border-top: 1px solid $acr-dim-grey-14;
            font-size: 1.2rem;
            padding-right: 5rem;
            padding-top: 2.5rem;

            ul li {
              font-size: 1.2rem;
            }
          }

          @media #{$xl-breakpoint} {
            font-size: 1.6rem;
          }
        }
      }
    }
  }
}
