//---------------------------------------
// Custom Icon Fonts
//---------------------------------------

@font-face {
  font-family: 'acura-icon';
  src: url('../fonts/icon-fonts/acura-icon.eot?jae3pc');
  src: url('../fonts/icon-fonts/acura-icon.eot?jae3pc#iefix') format('embedded-opentype'),
       url('../fonts/icon-fonts/acura-icon.ttf?jae3pc') format('truetype'),
       url('../fonts/icon-fonts/acura-icon.woff?jae3pc') format('woff'),
       url('../fonts/icon-fonts/acura-icon.svg?jae3pc#acura-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}


