.acr-offers {
  background-color: $acr-white;
  margin-top: -2px;
  padding: 12rem 0 0;
  position: relative;

  .acr-model-slick {
    article {
      margin: 0 2.7rem;
      outline: 0;
      padding-top: 2.4rem;


      h3,
      .offer-description,
      .offer-information,
      .offer-disclaimer,
      .offers-ctas {
        text-align: center;
      }

      .offers-ctas {
        margin: 0 auto;
        max-width: 290px;

        .acr-cta-text {
          margin-top: 3rem;
        }
      }

      h3 {
        font-family: $acr-avenir-ltpro-light;
        font-size: 3.6rem;
      }

      .offer-description {
        color: $acr-black;
        font-family: $acr-avenir-ltpro-medium;
        margin: 0 3rem 3rem;
      }

      .offer-information {
        margin-bottom: 3rem;
      }

      .offer-payment {
        display: block;
        font-size: 3.4rem;
        margin-bottom: 1rem;
      }

      .offer-disclaimer {
        font-size: 1.4rem;
        line-height: 1.4;
        margin: 0 auto 3rem;
      }
    }
  }
}

@media #{$sm-breakpoint} {
  .acr-offers {
    padding: 0;

    .acr-model-slick {
      .slick-prev {
        margin-left: 1.5rem;
      }

      .slick-next {
        margin-right: 1.5rem;
      }

      article {
        background-color: $acr-white;
        padding: 2.4rem;

        h3 {
          font-family: $acr-bespoke-light;
          font-size: 6rem;
          margin-bottom: .4rem;
        }

        .offer-description {
          font-size: 2rem;
          margin-bottom: .6rem;
        }

        .offer-information,
        .offer-payment {
          margin-bottom: 0;
        }

        .offer-information {

          span {
            display: inline-block;
          }

          .offer-payment,
          .offer-details {
            font-family: $acr-avenir-ltpro-light;
          }

          .offer-payment {
            font-size: 4.85rem;
            margin-right: 1rem;
          }

          .offer-details {
            font-size: 2rem;
            line-height: 1.3;
            margin-bottom: 2.3rem;
            text-align: left;
          }
        }

        .offer-disclaimer {
          margin-bottom: 2.3rem;
          margin-top: -.5rem;
          width: 50%;
        }

        .acr-cta-text {
          margin: auto;
        }
      }

      .slick-dots {
        margin-top: 2rem;
      }
    }
  }
}



@media #{$lg-breakpoint} {
  .acr-offers {
    .acr-model-slick {
      .slick-prev {
        margin-left: 2.5rem;
      }

      .slick-next {
        margin-right: 2.5rem;
      }
    }
  }
}

@media #{$xl-breakpoint} {
  .acr-offers {
    .acr-model-slick {
      .slick-prev {
        margin-left: 5rem;
      }

      .slick-next {
        margin-right: 5rem;
      }

      article {
        h3 {
          margin-bottom: .4rem;
        }

        .offer-description,
        .offer-information,
        .offer-payment {
          margin-bottom: 0;
        }

        .offer-disclaimer {
          margin-bottom: 2.3rem;
          margin-top: -.5rem;
        }
      }

      .slick-dots {
        margin-top: 2rem;
      }
    }
  }
}
