.acr-mle-engine {
  background-repeat: no-repeat;
  background-size: cover;
  padding: 35px 0 60px;

  &.is-light-theme * {
    color: $acr-white;

    svg {
      fill: $acr-white;
    }
  }

  &.is-dark-theme *,
  &.is-dark-theme .specs .name ,
  &.is-dark-theme .highlights .heading {
    color: $acr-black;

    svg {
      fill: $acr-black;
    }
  }

  @media #{$sm-breakpoint} {
    padding: 45px 0 65px;
  }

  @media #{$lg-breakpoint} {
    padding: 50px 0 80px;
  }

  .specs {
    padding-left: 19px;
    padding-right: 19px;
    text-align: center;

    @media #{$sm-breakpoint} {
      margin: 0 auto;
      max-width: 80%;
      padding-left: 50px;
      padding-right: 50px;
    }

    @media #{$lg-breakpoint} {
      max-width: 600px;
      padding-left: 60px;
      padding-right: 60px;
    }

    .spec-item {
      margin: 15px 0;
      position: relative;

      &:nth-child(4)::after,
      &:nth-child(5)::after {
        border-right: 1px solid $acr-white;
        content: '';
        display: block;
        height: 3.6rem;
        position: absolute;
        right: 0;
        top: 0;
        transform: rotate(35deg);

        @media #{$lg-breakpoint} {
          height: 6rem;
          top: -1rem;
        }
      }

      @media #{$lg-breakpoint} {
        margin: 35px 0 15px;
      }
    }

    .name {
      color: $acr-dim-grey-1;
      font-family: $acr-avenir-ltpro-heavy;
      font-size: 1.1rem;
      margin-top: 1rem;
      text-transform: uppercase;

      @media #{$sm-breakpoint} {
        font-size: 1.2rem;
      }

      @media #{$lg-breakpoint} {
        font-size: 1.4rem;
        margin-top: 2rem;
      }
    }

    .detail {
      font-family: $acr-avenir-ltpro-light;
      font-size: 3.6rem;
      line-height: 3.6rem;

      @media #{$lg-breakpoint} {
        font-size: 6rem;
      }
    }

    .icon {
      height: 3.6rem;
    }

    svg {
      height: 3.6rem;
      width: auto;

      @media #{$lg-breakpoint} {
        height: 4rem;
      }
    }
  }

  .highlights {
    border: 1px solid $acr-dim-grey-1;
    border-top: 0;
    margin-left: 19px;
    margin-right: 19px;
    margin-top: 20px;
    position: relative;

    &::before {
      @extend %custom-border;
      left: 0;
    }

    &::after {
      @extend %custom-border;
      right: 0;
    }

    @media #{$sm-breakpoint} {
      margin-left: 20px;
      margin-right: 20px;
      margin-top: 30px;
    }

    @media #{$md-breakpoint} {
      margin-left: 40px;
      margin-right: 40px;
    }

    @media #{$lg-breakpoint} {
      margin: 60px auto 0;
      max-width: 790px;
    }

    @media #{$xl-breakpoint} {
      margin-top: 75px;
      max-width: 700px;
    }

    .heading {
      color: $acr-dim-grey-1;
      display: inline-block;
      font-size: 1.3rem;
      height: 22px;
      left: 50%;
      line-height: 1.3rem;
      margin-left: -49px;
      padding: 5px 10px;
      position: absolute;
      text-align: center;
      text-transform: uppercase;
      top: -11px;
      width: 98px;

      @media #{$sm-breakpoint} {
        font-size: 1.5rem;
        line-height: 1.5rem;
        margin-left: -55px;
        width: 110px;
      }
    }
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 25px;

    li {
      margin-bottom: 15px;
      position: relative;
      width: 100%;

      &::before {
        background-color: $acr-white;
        border-radius: 50%;
        content: '';
        display: block;
        height: 3px;
        left: -10px;
        position: absolute;
        top: 10px;
        vertical-align: middle;
        width: 3px;
      }

      &:last-child {
        margin-bottom: 0;
      }

      @media #{$sm-breakpoint} {
        float: left;
        margin-bottom: 5px;
        margin-left: 4px;
        padding-right: 2px;
        width: 49%;
      }

      @media #{$md-breakpoint} {
        margin-left: 9%;
        padding-right: 7px;
        width: 40%;
      }
    }
  }
}

%custom-border {
  border-top: 1px solid $acr-dim-grey-1;
  content: '';
  position: absolute;
  top: 0;
  width: 35%;

  @media #{$sm-breakpoint} {
    width: 38%;
  }

  @media #{$lg-breakpoint} {
    width: 42%;
  }

  @media #{$xl-breakpoint} {
    width: 41%;
  }
}
