// special -- needs to be added to the MAIN styles
.acr-grid-25-container {
  overflow: hidden;

  .bg {
    position: relative;
    z-index: 1;
  }

  .grid-item {
    position: relative;
  }

  .acr-heading.acr-fleft {
    left: 0;
    margin: 3rem 0 5rem 3rem;
    position: relative;

    @media #{$sm-breakpoint}{
      margin-bottom: 5rem;
    }
  }
}

// overall class for MLP starts off with:
// model-ilx or model-tlx, etc ...
.acr-mlp {
  position: relative;

  .row {
    margin: auto;
  }

  &.intro {
    background-position: top center;
    background-repeat: repeat-y;
    background-size: 550px auto;

    @media #{$sm-breakpoint}{
      background-size: 1024px auto;
    }

    @media #{$lg-breakpoint}{
      background-size: 1400px auto;
    }

    @media #{$xl-breakpoint}{
      background-size: 2000px auto;
    }
  }

  .inner-wrapper {
    background-position: top center;
    background-repeat: no-repeat;
    background-size: 550px auto;

    @media #{$sm-breakpoint}{
      background-size: 1024px auto;
    }

    @media #{$lg-breakpoint}{
      background-size: 1400px auto;
    }

    @media #{$xl-breakpoint}{
      background-size: 2000px auto;
    }

    .inner-inner-wrapper {
      margin: 0 2rem;

      @media #{$sm-breakpoint}{
        margin: 0 2rem;
      }

      @media #{$lg-breakpoint}{
        margin: 0 2rem;
      }

      @media #{$xl-breakpoint}{
        margin: 0 3rem;
      }
    }

    .hmc-grid-100 {
      background-position: bottom center;
      background-size: 550px auto;

      @media #{$sm-breakpoint}{
        background-size: 1024px auto;
      }

      @media #{$lg-breakpoint}{
        background-size: 1400px auto;
      }

      @media #{$xl-breakpoint}{
        background-size: 2000px auto;
      }
    }

  }

  .hero {
    .hero-container {
      overflow: hidden;
      padding-top: 21%;
      position: relative;
      text-align: center;
      z-index: 10;

      @media #{$md-breakpoint}{
        padding-top: 160px;
      }

      @media #{$lg-breakpoint}{
      }

      .cta {
        margin-bottom: 5rem;
        margin-top: 5rem;

        @media #{$md-breakpoint}{
          margin-top: 8rem;
        }

        @media #{$lg-breakpoint}{
          margin-top: 7rem;
        }

        @media #{$xl-breakpoint}{
          margin-top: 18rem;
        }
      }

      .details {
        margin: 0 auto;
        text-align: center;
        z-index: 2;

        @media #{$sm-breakpoint}{
          position: relative;
          top: -25px;
        }

        @media #{$md-breakpoint}{
          padding-top: 11%;
          text-align: left;
        }

        @media #{$lg-breakpoint}{
          margin-left: 15px;
          padding-top: 8%;
        }

        @media #{$xl-breakpoint}{
          margin: 10px 0 10px 39px;
          padding-top: 9%;
        }

        .tagline {
          display: block;
          font-family: $acr-bespoke-book;
          font-size: 3.2rem;
          line-height: 1.1;

          @media #{$md-breakpoint}{
            max-width: 200px;
          }

          @media #{$lg-breakpoint}{
            font-size: 3.7rem;
            max-width: 250px;
          }
        }

        .price {
          display: block;
          font-family: $acr-avenir-ltpro-heavy;
          font-size: 2.6rem;
          line-height: 1.3;
          margin: 2.6rem 0;

          @media #{$sm-breakpoint}{
            font-size: 2.35rem;
            margin: 2.2rem 0;
          }

          @media #{$lg-breakpoint}{
            font-size: 2.6rem;
            margin: 2rem 0;
          }

          @media #{$xl-breakpoint}{
            margin: 2rem 0;
          }
        }

        .specs {
          display: flex;
          justify-content: center;
          margin: 0;

          @media #{$md-breakpoint}{
            display: block;
          }

          span {
            display: inline-block;
            font-family: $acr-bespoke-regular;
            font-size: 2.4rem;
            padding: 0 2rem 0 0;
            position: relative;
            text-transform: uppercase;
            white-space: nowrap;

            &::after {
              content: '/';
              position: absolute;
              right: 3px;
              top: 0;
            }

            &:first-of-type {
              content: '';
              display: block;
              float: left;
              padding-right: 1rem;

              @media #{$sm-breakpoint}{
                display: block;
              }

              @media #{$md-breakpoint}{
                float: none;
              }

              &:first-of-type::after {
                content: '';
              }
            }

            &:last-of-type {
              padding: 0;
            }

            &:last-of-type::after {
              content: '';
            }

            small {
              display: block;
              font-size: 1.4rem;
              font-weight: 600;
              letter-spacing: .08rem;
              margin-top: .5rem;
              text-transform: uppercase;
            }
          }
        }

        .disclaimer {
          display: block;
          font-family: $acr-avenir-ltpro-light;
          font-size: 2rem;
          font-style: italic;
          font-weight: 600;
          margin: 1.5rem 0;

          @media #{$sm-breakpoint}{
            font-size: 1.4rem;
            line-height: 1.3;
          }

          @media #{$md-breakpoint}{
            max-width: 180px;
          }

          @media #{$lg-breakpoint}{
            max-width: none;
          }
        }
      }

      .main {
        margin: auto .5rem;
        position: relative;
        text-align: right;
        z-index: 0;

        @media #{$sm-breakpoint}{
          margin: auto 3.5rem;
          position: relative;
          right: 0;
        }

        @media #{$md-breakpoint}{
          bottom: 0;
          left: 0;
          margin-left: 0;
          margin-right: 0;
          padding: 0 10%;
          position: absolute;
          right: 0;
          top: 134px;
        }

        @media #{$lg-breakpoint}{
          padding: 0;
          text-align: center;
          top: 62px;
        }

        @media #{$xl-breakpoint}{
          top: 7.4rem;
        }

        .hero-img-container {
          position: relative;
          top: -2rem;
          z-index: 2;

          img {
            max-width: 100%;

            @media #{$md-breakpoint}{
              width: 85%;
            }

            @media #{$lg-breakpoint}{
              width: auto;
            }
          }

          @media #{$md-breakpoint}{
            right: -7rem;
          }

          @media #{$lg-breakpoint}{
            right: -14rem;
          }

          @media #{$xl-breakpoint}{
            right: auto;
            top: -4rem;
          }
        }

        .logo {
          position: relative;
          z-index: 1;

          @media #{$md-breakpoint}{
            left: -3rem;
          }

          @media #{$lg-breakpoint}{
            width: 61%;
          }
        }

        svg {
          width: 100%;

          @media #{$md-breakpoint} {
            height: 12vw;
            max-height: 200px;
          }
        }
      }
    }
  }

  .overview {
    background-position: bottom center;
    background-repeat: no-repeat;
    background-size: 550px auto;
    border: 0;
    padding: 0;
    position: relative;


    @media #{$sm-breakpoint}{
      background-size: 1024px auto;
    }

    @media #{$lg-breakpoint}{
      background-size: 1400px auto;
    }

    @media #{$xl-breakpoint}{
      background-size: 2000px auto;
    }

    .bg {
      height: auto;

      @media #{$sm-breakpoint}{
        position: relative;
      }

      @media #{$lg-breakpoint}{
        height: 1000px;
      }

      @media #{$xl-breakpoint}{
        height: 1250px;
      }
    }

    .comp {
      align-items: center;
      display: flex;
      justify-content: center;
      margin: 0 auto 4.2rem;
      max-width: $thirty-six-36th;

      &.margin-b-0 {
        margin-bottom: 0;
      }

      &.w25,
      &.w50 {
        .inner-content {
          max-width: $thirty-36th;

          img {
            width: auto;
          }
        }
      }

      &.w75 {
        .inner-content {
          max-width: 90%;

          img {
            width: 100%;
          }
        }
      }

      &.w100 {
        .inner-content {
          max-width: 100%;
        }
      }

      @media #{$sm-breakpoint} {
        &.w25,
        &.w50,
        &.w75 {
          .inner-content {
            max-width: $twenty-eight-36th;
          }
        }
      }

      @media #{$md-breakpoint} {
        &.w25 {
          .inner-content {
            max-width: 25%;
          }
        }

        &.w50 {
          .inner-content {
            max-width: 50%;
          }
        }

        &.w75 {
          .inner-content {
            max-width: 75%;
          }
        }

        &.w100 {
          .inner-content {
            max-width: 100%;
          }
        }
      }

      .inner-content {
        overflow: visible;
        padding: 0;
        position: relative;
        vertical-align: top;

        .title-text {
          z-index: 3;

          ~ * {
            display: block;
          }

          ~ picture {
            margin-top: 5.5rem;
          }
        }
      }

      &.ratio1-1::before {padding-top: 100%;}
      &.ratio2-1::before {padding-top: 50%;}
      &.ratio4-3::before {padding-top: 75%;}
      &.ratio16-9::before {padding-top: 56.25%;}

      img {
        position: relative;
        width: 100%;
        z-index: 2;

        @media #{$lg-breakpoint} {
          width: auto;
        }
      }

      // inner content fields
      // text fields
      .desc-text {
        text-align: left;
      }

      .title-text {
        font-family: $acr-bespoke-regular;
        font-size: 6rem;
        left: -2rem;
        letter-spacing: .95rem;
        line-height: 1.19;
        position: absolute;
        text-align: left;
        top: 0;
        width: 1px;
      }

      .btn-wrapper {
        bottom: 1rem;
        display: block;
        left: 1rem;
        max-width: 24rem;
        min-height: 3.4rem;
        min-width: 16rem;
        position: absolute;
        text-align: left;
        z-index: 3;

        &:only-child {
          bottom: 0;
          left: 0;
          position: relative;
        }

        a {
          color: $acr-white;
          display: block;
          text-decoration: none;
        }

        .cta-btn {
          background: $acr-red;
          float: left;
          height: 3.4rem;
          margin-right: 1rem;
          width: 3.4rem;

          span {
            font-size: 1.4rem;
            left: 0;
            line-height: 3.4rem;
            margin: 1rem;
            position: relative;
          }
        }

        .cta-txt {
          display: block;
          font-family: $acr-avenir-ltpro-heavy;
          font-size: 1.3rem;
          line-height: 1.3;
          position: relative;
          text-align: left;
          text-transform: uppercase;
          top: .3rem;
        }
      }

      p,
      span,
      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        color: $acr-white;
        position: absolute;
      }

      // specific components for this page //
      .intro-copy {
        margin: 0 5rem 5rem;

        @media #{$sm-breakpoint}{
          width: 50vw;
        }

        @media #{$lg-breakpoint}{
          margin-left: 0;
          width: 40vw;
        }
      }

      &.light-theme {
        p,
        span:not(.acr-icon-direction-45),
        h1,
        h2,
        h3,
        h4,
        h5,
        h6,
        .btn-wrapper .cta-txt {
          color: $acr-black;
        }
      }

      .els-studio {
        height: 50px;
        width: 300px;

        .btn-wrapper {
          width: 23vw;
        }

        @media #{$sm-breakpoint}{
        }

        @media #{$lg-breakpoint}{
        }
      }


      .spec-pkg {

        a {
          color: $acr-white;
          text-decoration: none;
        }

        img {
          max-width: 100%;

          @media #{$sm-breakpoint}{
            padding-left: 3rem;
            padding-right: 3rem;
          }
        }
      }

      .headlight-jewel {

        img {
          width: 100%;
        }

        a {
          color: $acr-white;
          text-decoration: none;
        }
      }

      .gear-shift {

        a {
          color: $acr-white;
          text-decoration: none;
        }

        @media #{$sm-breakpoint}{
        }

        @media #{$lg-breakpoint}{
        }

        @media #{$xl-breakpoint}{
        }

        img {
          top: 3rem;
          width: 100%;

          @media #{$sm-breakpoint}{
            top: 0;
          }
        }

        .desc-text {
          font-size: 7rem;
          left: 3rem;
          top: 0;

          @media #{$sm-breakpoint}{
            font-size: 5rem;
            left: -2rem;
            top: -3rem;
          }
        }
      }
    }
  }
}
