.form-inline {
  button {
    display: inline-block;
  }
}

.acr-placeholder {
  background-color: $acr-dim-grey-3;
  color: $acr-black;
  font-family: $acr-avenir-ltpro-light;
  font-size: 1.4rem;
  margin-top: -4.7rem;
  overflow: hidden;
  padding: 1.3rem 1.6rem;
  white-space: nowrap;
  z-index: 0;
}

input {
  background-color: transparent;
  border: 0;
  font-size: 1.4rem;
  padding: 1.3rem 2.4rem;
  z-index: 1;

  &.acr-required {
    +.acr-placeholder {
      &::after {
        color: $acr-red;
        content: '*';
      }
    }
  }

  &.acr-not-empty {
    +.acr-placeholder {
      color: transparent;

      &::after {
        color: inherit;
        content: '*';
      }
    }
  }
}

.checkbox {
  label {
    padding-left: 3.8rem;

    &::before {
      background-color: $acr-dim-grey-3;
      content: '';
      display: inline-block;
      height: 2.8rem;
      left: 0;
      position: absolute;
      top: 0;
      width: 2.8rem;
    }

    &::after {
      border: .2rem solid $acr-red;
      border-right: 0;
      border-top: 0;
      content: '';
      height: .8rem;
      left: .6rem;
      margin-top: .8rem;
      position: absolute;
      top: 0;
      transform: rotate(-45deg);
      visibility: hidden;
      width: 1.6rem;
    }
  }

  input {
    display: none;

    &:checked + label {
      &::after {
        visibility: visible;
      }
    }
  }
}

.ie {
  .acr-placeholder {
    margin-top: -4.3rem;
  }
}
