.acr-accolades {
  background-repeat: no-repeat;
  background-size: 100% auto;

  .accolades-slider {
    margin: 0;
  }

  .title {
    padding: 17.188vw 0 12.5vw;
  }

  .acr-accolades-tagline {
    margin: 0 -2rem;
  }

  .headline-container {
    @include acr-shadow-5;
    background-repeat: no-repeat;
    background-size: cover;
    height: 80vw;
    padding: 0 0 0 11.5vw;
    width: 80vw;

    .headline {
      position: relative;
      top: 50%;
      transform: translate3d(0, -50%, 0);
      width: 80vw;

      .acr-anim {
        display: block;
      }
    }
  }

  .acr-model-slick {
    .slick-slide {
      background: transparent;
      height: auto;
      margin: 0;
      outline: 0;
      padding-top: 2.4rem;

      aside {
        margin-left: 0;
        margin-right: 0;

        .acr-icon-quote {
          left: 0;
          top: 0;
        }

        .heading {
          border: 0;
          margin: 0;
          padding: 2rem;
        }

        .underheading {
          background: $acr-dim-grey-2;
          height: 3px;
          margin: 1.2rem auto;
          width: 45px;
        }

        .author > span {
          white-space: nowrap;
        }
      }
    }
  }
}

@media #{$sm-breakpoint} {
  .acr-accolades {
    .title {
      padding: 14.36vw 0 12.91vw;
    }

    .acr-accolades-tagline {
      margin: 0 -3rem;
    }

    .headline-container {
      height: 50vw;
      padding-left: 9vw;
      width: 50vw;


      .headline {
        width: 50vw;
      }
    }

    .acr-model-slick {
      padding: 0 0 5rem;

      .slick-slide {
        height: 28rem;
        margin: 5rem 0 0;
        padding-top: 0;

        aside {
          .heading {
            padding: 10px;
          }

          .underheading {
            background: $acr-dim-grey-2;
            height: 3px;
            margin: 2.4rem auto;
            width: 4.5rem;
          }

          .acr-icon-quote {
            margin-top: 0;
          }
        }
      }

      .slick-prev {
        margin-left: 2.6rem;
      }

      .slick-next {
        margin-right: 2.6rem;
      }
    }
  }
}

@media #{$lg-breakpoint} {
  .acr-accolades {
    .title {
      padding: 9rem 0 7rem;
    }

    .headline-container {
      height: 43.457vw;
      padding-left: 6vw;
      width: 43.457vw;

      .headline {
        width: 42.457vw;
      }
    }

    .acr-model-slick {
      .slick-slide {
        margin: 0;
      }
    }
  }
}

@media #{$xl-breakpoint} {
  .acr-accolades {
    .title {
      padding: 7.857vw 0 6.428vw;
    }

    .headline-container {
      height: 37.143vw;
      padding-left: 5.714vw;
      width: 37.143vw;

      .headline {
        width: 37.143vw;
      }
    }

    .acr-model-slick {
      padding-bottom: 5rem;

      .slick-slide {
        height: 35rem;
        margin: 0 2.7rem;
        padding-top: 2.7rem;
      }
    }
  }
}
