.vehicle-lineup {
  background: $acr-white;
  position: relative;
  top: 0;
  z-index: 10;

  .carslist {
    width: 100%;

    h6 {
      margin-top: 0;

      @media #{$sm-breakpoint} {
        margin-top: 6px;
      }

      @media #{$lg-breakpoint} {
        margin-top: 0;
      }
    }

    .head-section-comp {
      margin-bottom: 30px;
      text-align: center;
      width: 100%;

      .bladeheadline {
        margin-top: 2rem;
      }
	  }

    .tile-carlist-comp {
      .tile {
        float: left;
        padding-bottom: 26%;
        position: relative;
        width: 33.3%;

        .line {
          display: none;
          height: 20px;
          margin: 15px;
          position: absolute;
          text-indent: -5000px;
          width: 92px;
          z-index: 2;
        }

        img {
          left: 0;
          position: absolute;
          top: 0;
          width: 100%;
          z-index: 1;
        }

        span {
          color: $acr-black;
          display: none;
          font-size: 1.4rem;
          left: 5%;
          line-height: 1;
          position: absolute;
          top: 16%;
          z-index: 2;

          @media #{$xl-breakpoint}{
            font-size: 1.8rem;
            line-height: 1;
          }

          @media #{$lg-breakpoint}{
            font-size: 1.8rem;
            line-height: .9;
          }
        }

        a {
          .normalcar {
            display: block;
          }

          .overcar {
            opacity: 0;
            transition: opacity .2s ease;
            visibility: hidden;

            &::after {
              @include acr-shadow-6;

              background: $acr-white;
              bottom: 0;
              content: '';
              left: 0;
              position: absolute;
              right: 0;
              top: 0;
              transform: matrix(.97, 0, 0, .97, 0, 0);
              transition: transform .2s ease;
              z-index: -1;
            }
          }
        }

        &.active,
        &:hover {
          z-index: 50;
        }
      }
    }
  }

  @media screen and (min-width:550px) and (max-width:1023px) {
    .carslist {
      padding-top: 29px;
    }
  }

  @media screen and (max-width:549px) {
    .carslist {

      .head-section-comp {
        margin-bottom: 25px;
        text-align: center;
        width: 100%;

        .bladeheadline {
          display: none;
        }
			}

      .col-mob {
        float: left;
        width: 50%;
      }

      .tile-carlist-comp {
        .tile {
          padding-bottom: 78%;
          width: 100%;

          img {
            height: auto;
            width: 100%;
          }
				}

        a {
          .tile-tagline {
            display: none;
            left: 5%;
            opacity: 0;
            top: 15%;
          }

          &:hover {
            span {
              display: none;
						}
					}
				}
			}
		}
	}
}

//AR-4193 Converting hover animations to css only
html.no-ipad.no-iphone.no-ipod.no-android .vehicle-lineup .carslist .tile-carlist-comp .tile a {

  &:hover {

    .line {
      background-color: $acr-black;
    }

    .tile-tagline {
      display: block;
    }

    .normalcar {
      display: none;
    }

    .overcar {
      opacity: 1;
      visibility: visible;

      &::after {
        transform: matrix(1.05, 0, 0, 1.08, 0, 0);
      }
    }
  }

}
