.multi-tab-content-type-2 {
  background-size: cover;
  padding-bottom: 3rem;
  padding-top: 25.7rem;
  text-align: center;

  @media #{$lg-breakpoint} {
    padding-top: 33rem;
  }

  @media #{$xl-breakpoint} {
    padding-top: 34rem;
  }

  .acr-multitab-img {
    margin: auto;
    max-width: 100%;

    @media #{$sm-breakpoint} {
      margin-top: 1.2rem;
    }
  }

  h3 {
    font-family: "Acura Bespoke Book", Arial, sans-serif;
    font-size: 2.2rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5.5rem;
    width: 88.9%;

    @media #{$sm-breakpoint} {
      font-size: 2.6rem;
      margin-top: 5.1rem;
      width: 66.7%;
    }

    @media #{$lg-breakpoint} {
      font-size: 3.2rem;
    }

    @media #{$xl-breakpoint} {
      font-size: 3.6rem;
    }
  }

  p {
    font-size: 1.4rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: .7rem;
    width: 88.9%;

    @media #{$sm-breakpoint} {
      font-size: 1.6rem;
      width: 66.7%;
    }

    @media #{$lg-breakpoint} {
      margin-top: .9rem;
    }

    @media #{$xl-breakpoint} {
      margin-top: 1.5rem;
    }
  }

  .acr-multi-tab-cta {
    margin-top: .7rem;

    @media #{$lg-breakpoint} {
      margin-top: 1.2rem;
    }

    @media #{$xl-breakpoint} {
      margin-top: 1.9rem;
    }
  }
}
