.acr-multi-features {
  float: none;
  margin: 60px auto 25px;

  @media #{$lg-breakpoint} {
    margin: auto auto 5rem;
    padding: 0;
  }

  .heading {
    margin-bottom: 2rem;
    text-align: center;

    @media #{$xl-breakpoint} {
      margin-bottom: 3.5rem;
    }

    h2 {
      color: $acr-black;
      font-family: $acr-bespoke-light;
      font-size: 3rem;
      letter-spacing: 0;
      text-transform: initial;

      @media #{$lg-breakpoint} {
        font-size: 6rem;
        margin-bottom: 1.3rem;
      }
    }

    .acr-multi-features-description {
      margin-bottom: 1.4rem;
    }

    p {
      font-size: 1.6rem;

      @media #{$lg-breakpoint} {
        font-size: 2rem;
      }
    }
  }

  .acr-mlp-slide {
    max-height: 400px;
    outline: none;
    overflow: hidden;
    position: relative;

    @media #{$sm-breakpoint} {
      max-height: none;
    }

    @media #{$lg-breakpoint} {
      &.dark-theme,
      &.light-theme {
        .acr-mlp-content {
          .acr-cta-text {
            &.is-modal {
              span {
                padding-right: 0;
              }
            }
          }
        }
      }
    }

    .acr-mlp-content {
      margin: 0;
      padding: 3rem;
      position: relative;
      text-align: center;

      @media #{$lg-breakpoint} {
        margin-left: 5.5rem;
        padding: 0;
        position: absolute;
        text-align: left;
        top: 50%;
        transform: translateY(-50%);
      }

      h3 {
        color: $acr-black;
        font-family: $acr-bespoke-light;
        font-size: 2rem;

        @media #{$lg-breakpoint} {
          font-size: 3.6rem;
          line-height: 1;
          margin-bottom: 3rem;
          text-align: left;
        }
      }

      .acr-mlp-description {
        margin-bottom: 3rem;

        @media #{$lg-breakpoint} {
          margin-bottom: .5rem;
        }
      }

      .acr-cta-text {

        &.is-modal {
          @media #{$lg-breakpoint} {
            font-size: 1.85rem;
          }
        }
      }
    }

    &.acr-mlp-all-features {
      background: $acr-dim-grey-18;

      @media #{$lg-breakpoint} {
        min-height: 510px;
      }

      .acr-mlp-features-heading {
        padding: 2rem;

        @media #{$lg-breakpoint} {
          padding: 2rem 5.8rem;
        }

        h3 {
          font-size: 3.4rem;
          line-height: 1.2;
        }
      }

      .acr-mlp-features-container {
        height: 75%;
        outline: none;
        overflow: auto;


        &::after {
          background-color: $acr-dim-grey-18;
          bottom: 0;
          content: '';
          display: block;
          height: 2.5rem;
          opacity: .7;
          position: absolute;
          width: 100%;
        }

        h4 {
          font-family: $acr-avenir-ltpro-medium;
          font-size: 1.6rem;
          margin-top: 3rem;
        }
      }

      .acr-mlp-feature-list {
        list-style: none;
        margin: 0;
        padding: 0;
      }

      .acr-mlp-cta-modal {
        background-color: transparent;
        border: 0;
        color: $acr-default;
        font-family: $acr-avenir-ltpro-light;
        margin-bottom: 1rem;
        padding: 0;
        text-align: left;
        text-decoration: underline;
      }

      .acr-mlp-col {
        padding: 0 2rem 2.4rem;

        @media #{$lg-breakpoint} {
          padding: 0 5.8rem 2.4rem;
        }
      }
    }

    .acr-mlp-bg {
      width: 100%;

      .acr-mlp-img {
        width: inherit;
      }
    }

    @media #{$lg-breakpoint} {
      &.light-theme {
        .acr-mlp-content {
          h3,
          p {
            color: $acr-black;
          }

          .acr-cta-text {
            @include cta-txt-type($acr-black);
          }
        }
      }

      &.dark-theme {
        .acr-mlp-content {
          h3,
          p {
            color: $acr-white;
          }

          .acr-cta-text {
            @include cta-txt-type($acr-white);
          }
        }
      }
    }
  }

  .acr-mlp-tabs {
    @media #{$lg-breakpoint} {
      display: flex;
      float: none;
      font-size: 0;
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        display: inline-block;
        flex: 1;
        height: 15.8rem;

        ~li {
          padding-left: 1px;
        }

        .acr-mlp-tab {
          background-color: $acr-dim-grey-8;
          border: 0;
          color: $acr-default;
          font-family: $acr-avenir-ltpro-heavy;
          font-size: 1.6rem;
          height: 100px;
          margin: 0;
          width: 100%;

          &.acr-mlp-active {
            @include acr-shadow-4;
            background-color: $acr-white;
            border-bottom: .4rem solid $acr-mlp-red;
            color: $acr-black;

            &:hover {
              background-color: inherit;
            }
          }

          &:hover {
            background-color: $acr-dim-grey-12;
          }
        }
      }
    }
  }

  .acr-multi-features-slick {
    .slick-list {
      @include acr-shadow-2;
      max-height: 400px;

      @media #{$sm-breakpoint} {
        max-height: none;
      }

      @media #{$lg-breakpoint} {
        box-shadow: none;
        max-height: 510px;
        overflow: hidden;
      }
    }

    .acr-mlp-ctas {
      @media #{$lg-breakpoint} {
        margin-top: 2rem;
      }
    }

    .acr-mlp-ctas-list {
      list-style: none;
      padding: 0;
      text-align: left;
    }

    .slick-arrow {
      margin: 0;
      opacity: .5;
      padding: 0;
      z-index: 1;

      &:hover {
        opacity: 1;
      }

      &.slick-prev {
        transform: translateX(-100%);
      }

      &.slick-next {
        right: 0;
        transform: translateX(100%);
      }

      .acr-icon {
        height: 2.2rem;
        width: 3.2rem;
      }
    }

    .slick-dots {
      margin-top: 20px;
    }
  }

  .slick-slide {
    opacity: 0;
    transition: opacity .5s ease-in-out;
  }

  .slick-active {
    opacity: 1;
  }
}
