.acr-alert {
  bottom: 0;
  left: 50%;
  padding: 0;
  position: fixed;
  transform: translateX(-50%);
  z-index: 2048;

  .acr-alert-container {
    &.row {
      margin: auto;
    }

    &.acr-disclaimer {
      display: block;
      padding-bottom: 1.5rem;
      position: relative;

      .alert-msg {
        display: block;

        @media #{$sm-breakpoint} {
          display: flex;
          flex-direction: row;
        }
      }
    }

    .acr-icon-alert {
      color: $acr-orange;
      font-size: 2.7rem;
      position: absolute;
      top: 3.3rem;
      vertical-align: middle;

      @media #{$sm-breakpoint} {
        position: relative;
        top: 0;
      }
    }

    p {
      font-size: 2rem;
      letter-spacing: .06rem;
      line-height: 1.3;
      margin-top: .8rem;
      text-transform: capitalize;

      strong {
        color: $acr-black;
        display: block;
        font-family: $acr-avenir-ltpro-heavy;
        font-size: 1.6rem;
        margin-bottom: 1.6rem;
        margin-left: 34px;

        @media #{$sm-breakpoint} {
          display: inline;
          margin: 0;
        }
      }

      @media #{$sm-breakpoint} {
        font-size: 1.6rem;
        line-height: 1.5;
        margin-left: .7rem;
        margin-top: .3rem;
      }
    }

    a {
      display: block;
      font-size: 1.5rem;
      position: relative;
      top: 3px;

      &.acr-orange {
        color: $acr-orange;
      }
    }
  }

  @media #{$sm-breakpoint} {

    p {
      display: inline;
      font-size: 1.7rem;
      margin-left: auto;
      padding-right: 4%;

      strong {
        &::after {
          content: '\2013';
        }
      }
    }

    .alert-cta {
      display: inline;
    }
  }

  @media #{$lg-breakpoint} {
    p {
      font-size: 2rem;
    }
  }

}
