footer {
  padding-top: 3rem;

  .acr-footer {
    float: none;
    margin: auto;
    padding: 0;
    text-align: center;

    a {
      border-bottom: 0;
    }

    hr {
      border-bottom: 1px solid rgba($acr-black, .1);
      border-top: 1px solid rgba($acr-white, .1);
      height: 0;
      margin-bottom: 24px;
      margin-top: 20px;
      width: 90%;

      @media #{$sm-breakpoint} {
        width: 100%;
      }

      @media #{$lg-breakpoint} {
        width: 90%;
      }

    }

    .logo {
      display: inline-block;

      .acr-icon-footer {
        fill: $acr-white;
        height: 4rem;
        width: 4.1rem;
      }

      &:hover {
        .acr-icon-footer {
          fill: $acr-dim-grey-2;
        }
      }
    }

    &.secondary {
      li {
        color: $acr-dim-grey-2;
        display: inline-block;
        font-size: 1.2rem;
        line-height: 3rem;
        padding: 0 .5rem;

        >a {
          color: $acr-dim-grey-2;
          font-size: 1.1rem;
          margin: 0 1rem;
          padding: .2rem 0;
          text-transform: uppercase;

          &:hover {
            color: $acr-white;
          }

          @media #{$lg-breakpoint} {
            font-size: 1rem;
          }
        }

        ~li {
          background: url('../img/divider.png') no-repeat 0 6px;
          background-size: 1px 50%;

          &.no-border {
            background-image: none;
          }
        }
      }
    }

    ul {
      display: block;
      padding: 0;

      li {
        display: inline-block;
        list-style-type: none;
        padding: 0;

        ~ li {
          background: url('../img/divider.png') no-repeat 0 10px;
          background-size: 1px 50%;

          &.no-border {
            background-image: none;
          }

          @media #{$lg-breakpoint} {
            background: none;
          }
        }

        > a {
          color: $acr-dim-grey-2;
          display: block;
          font-family: $acr-avenir-ltpro-roman;
          font-size: 1.5rem;
          line-height: 1.8rem;
          margin: 0 .2rem;
          padding: 1rem 1.4rem 1rem 1.5rem;

          &:hover {
            color: $acr-white;
          }

          @media #{$sm-breakpoint} {
            font-family: $acr-avenir-ltpro-heavy;
            font-size: 1.5rem;
            line-height: 1.5;
          }

          @media #{$lg-breakpoint} {
            font-size: 1.5rem;
            line-height: 1.6;
          }
        }
      }

      &.inner {

        @media #{$sm-breakpoint} {
          margin: 0 1rem;
        }
      }

      &.social {
        margin-bottom: 0;
        padding: 2rem 0 2.5rem;

        @media #{$sm-breakpoint} {
          padding: 3rem;
        }

        li {
          color: $acr-dim-grey-2;
          font-size: 1rem;
          margin: 0 3px;
          padding: 0;

          ~ li {
            background: none;
          }

          &:hover,
          &:focus {
            cursor: pointer;

            span:first-of-type {
              background: $acr-black-2;
            }
          }

          span {
            &:first-of-type {
              background: $acr-dim-grey-6;
              border-radius: 50%;
              display: block;
              height: 33px;
              width: 33px;
              z-index: 5;
            }
          }

          .acr-icon {
            fill: $acr-white;
            height: 2rem;
            left: 50%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            z-index: 10;

            @media #{$sm-breakpoint} {
              height: 1.6rem;
            }
          }
        }

        a {
          padding: 0;
          position: relative;
        }
      }
    }

    .no-border {
      background: none;
    }
  }
}

@media #{$footer-breakpoint} {
  footer {
    .acr-footer {
      ul {
        &.social {
          li {
            margin: 0 .4rem;

            span {
              &:first-of-type {
                height: 40px;
                width: 40px;
              }
            }

            .acr-icon {
              height: 1.6rem;
            }
          }
        }
      }
    }
  }
}

@media #{$sm-breakpoint} {
  footer {
    padding: 3rem 2rem;

    .acr-footer {
      ul.secondary {
        li {
          span {
            &:first-of-type {
              font-size: 3rem;
            }

            &:last-of-type {
              font-size: 1.5rem;
            }
          }

          >a {
            font-size: 1.2rem;
            text-transform: none;
          }

          ~li {
            background: none;
          }
        }
      }

      ul.social {
        padding: 3rem 0;
      }
    }
  }
}

@media #{$md-breakpoint} {
  footer {
    .acr-footer {
      ul.social {
        li {
          margin: 0 .8rem;

          span {
            &:first-of-type {
              height: 44px;
              width: 44px;
            }
          }

          .acr-icon {
            height: 2rem;
          }
        }
      }
    }
  }
}

@media #{$lg-breakpoint} {
  footer {
    padding: 3rem 2rem;

    .acr-footer {
      max-width: 790px;

      ul.social {
        li {
          margin: 0 7px;

          span {
            &:first-of-type {
              height: 40px;
              width: 40px;
            }
          }

          .acr-icon {
            height: 1.6rem;
          }
        }
      }
    }
  }
}
