.acr-hero {
  background-color: $acr-white;
  padding-bottom: 2rem;

  .hero-slide {
    &:focus {
      outline: none;
    }
  }

  >.hero-slide {
    display: none;
    position: relative;
    width: 100%;

    &:first-child {
      display: block;
    }
  }

  .slick-arrow {
    height: 5.4rem;
    margin-top: 0;
    opacity: .5;
    padding: 0;
    transform: translateY(-50%);
    z-index: 1;

    &:hover {
      opacity: 1;
    }

    svg {
      display: none;
      fill: $acr-white;
      height: inherit;
      vertical-align: super;
      width: 2.4rem;
    }
  }

  .slick-dots {
    bottom: auto;
  }

  .acr-hero-panel {
    text-align: center;
    text-transform: uppercase;

    h2 {
      float: none;
      margin-bottom: 3.5rem;
      margin-left: auto;
      margin-right: auto;
      margin-top: 3.2rem;
    }

    h3 {
      display: none;
      float: none;
      font-family: "Acura Bespoke Regular", Arial, sans-serif;
      font-size: .8rem;
      margin: auto auto 2rem;
    }

    br {
      display: none;
    }

    .hero-ctas {
      width: inherit;

      &::after {
        clear: both;
        content: '';
        display: block;
      }

      .hero-ctas-inner {
        float: none;
        margin: auto;
      }

      a {

        &.hero-link {

          &.hero-mobile {
            float: none;
            margin-top: 0;
          }
        }
      }

      .hero-modal {
        &.hero-mobile {
          &:first-child {
            display: none;
          }

          .acr-cta {
            display: none;
          }
        }
      }

      .hero-link {
        &.hero-mobile {
          ~.hero-modal {
            display: none;
          }
        }
      }
    }
  }

  .hero-media {
    line-height: 0;
    margin-bottom: 2rem;
    overflow: hidden;
    position: relative;

    video,
    img {
      background: no-repeat;
      background-size: cover;
      height: auto;
      object-fit: cover;
      width: 100%;
      z-index: -100;
    }
  }

  .video-fallback {
    display: none;
  }

  .hero-mobile {
    &.hero-modal:first-child {
      background-color: transparent;
      border: 0;
      height: 100%;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: 1;

      &::before {
        left: 0;
        position: absolute;
        right: 0;
        top: 25%;
        vertical-align: middle;
      }
    }

    .acr-cta-icon {
      display: none;
    }

    &.hero-cta {
      display: block;
      margin: 0 auto 2rem;
    }
  }

  &.hero-mobile {
    &.acr-hero-parallax {
      background-color: transparent;
    }

    h2,
    h3 {
      color: $acr-black;
    }

    .acr-hero-panel {
      .acr-cta-r-w-b {
        .cta-text {
          color: $acr-black;
          display: inline-block;
          transition: all .3s ease;
          vertical-align: super;
        }

        &:hover .cta-text {
          color: $acr-white;
        }

        @media #{$sm-breakpoint} {
          &:hover {
            .cta-text {
              color: $acr-white;
            }
          }
        }
      }

      .acr-cta-text {
        .cta-text {
          display: inline-block;
          vertical-align: middle;
        }
      }
    }

    .hero-media {
      .hero-modal {
        .acr-icon {
          fill: $acr-white;
          height: 48%;
          left: 50%;
          position: absolute;
          top: 49%;
          transform: translate3d(-50%, -66%, 0);
          width: 48%;
        }
      }
    }
  }
}

@media #{$sm-breakpoint} {
  .acr-hero {
    padding-bottom: 0;

    .slick-arrow {
      svg {
        display: inline;
      }
    }

    .slick-prev {
      left: 0;
      text-align: right;
    }

    .slick-next {
      right: 0;
      text-align: left;
    }

    &.slick-slider {
      height: 100%;
      left: 0;
      position: relative;
      top: 0;
      width: 100%;
    }

    .slick-list,
    .slick-track {
      height: 100%;
      width: 100%;
    }

    .slick-dots {
      bottom: 8.2rem;
      top: auto;
    }

    .acr-hero-panel {
      color: $acr-white;
      position: absolute;
      top: 50%;
      width: inherit;

      h2 {
        margin-bottom: 0;

        @media #{$sm-breakpoint} {
          padding: 0;
        }

      }

      h3 {
        font-size: 1.6rem;
        margin: auto;
      }

      br {
        display: block;
      }

      .acr-hero-panel-container {
        float: none;
        margin: -5.1rem auto auto;
        padding: 0;
        transform: translateY(-50%);
      }

      .hero-ctas {
        margin-top: 2%;

        .acr-cta-container {
          ~.acr-cta-container {
            border-left-style: solid;
            border-left-width: .1rem;
            padding-left: 4rem;
          }

          a {
            border-bottom: 0;
          }

          &.hero-mobile {
            display: none;
          }

          &:first-of-type {
            padding-right: 3rem;

            a {
              float: right;
            }
          }

          &:only-child {
            float: none;
            margin: auto;
            padding: 0;

            a {
              float: none;
              margin: auto;
            }
          }

          .acr-cta-icon {
            display: inline-block;
          }

          .cta-text {
            text-align: left;
          }
        }
      }

      &.acr-vjustify {
        height: 100%;
        top: 0;

        .acr-hero-panel-container {
          height: inherit;
          margin: 0;
          position: absolute;
          transform: none;
          width: 100%;

          .arc-hero-heading {
            position: absolute;
            top: 4.2rem;
            width: 100%;
          }

          .hero-ctas {
            bottom: 15rem;
            position: absolute;
          }
        }
      }
    }

    .light-theme {
      .arc-hero-heading {
        h2,
        h3 {
          color: $acr-black;
        }
      }

      .acr-cta-container {
        border-left-color: $acr-black;
      }
    }

    .dark-theme {
      .arc-hero-heading {
        h2,
        h3 {
          color: $acr-white;
        }
      }

      .acr-cta-container {
        border-left-color: $acr-white;
      }
    }

    .hero-mobile {
      display: none;
    }

    .hero-media {
      margin-bottom: 0;
    }
  }
}

@media #{$lg-breakpoint} {
  .acr-hero {
    .acr-hero-panel {
      .acr-hero-panel-container {
        margin-top: -6rem;
      }

      h3 {
        display: block;
        font-size: 2rem;
      }

      .hero-ctas {
        margin-top: 5.5%;
      }

      &.acr-vjustify {
        .acr-hero-panel-container {
          .arc-hero-heading {
            top: 3rem;
          }

          .hero-ctas {
            bottom: 15.3rem;
          }
        }
      }
    }

    .slick-dots {
      bottom: 10rem;
    }

    .slick-arrow {
      margin-top: -9rem;
    }
  }
}

@media #{$xl-breakpoint} {
  .acr-hero {
    .acr-hero-panel {
      .acr-hero-panel-container {
        margin-top: -12rem;

        .hero-ctas {
          margin-top: 3%;
        }
      }

      &.acr-vjustify {
        .acr-hero-panel-container {
          .arc-hero-heading {
            top: 6.4rem;
          }

          .hero-ctas {
            bottom: 27rem;
          }
        }
      }
    }

    h2 {
      width: 100%;
    }

    .slick-dots {
      bottom: 19rem;
    }

    .slick-arrow {
      margin-top: -13rem;
    }
  }
}

.appleios {
  .acr-hero {
    .video-fallback {
      display: block;
    }

    video {
      display: none;
    }
  }
}
