.hmc-grid-100 {
  background-repeat: no-repeat;
  background-size: cover;
  margin: auto;
  max-width: $wrapper-max-width;
  padding: 0;
  position: relative;
  z-index: 3; // for the vertical line animation to work

  $rows: 10;
  $cols: 10;
  $delta: 5;
  $breakpoints: 'xs' $xs-breakpoint,
                'sm' $sm-breakpoint,
                'md' $lg-breakpoint,
                'lg' $xl-breakpoint;

  @for $x from 0 through $rows + 1 {
    @for $y from 0 through $cols + 1 {
      .hmc-x#{$x}-y#{$y} {
        @media #{$sm-breakpoint} {
          left: percentage($x / $rows);
          top: percentage($y / $cols);
        }
      }
    }
  }

  @for $i from 1 through (100 / $delta) {
    .hmc-w#{$i * $delta} {
      @media #{$sm-breakpoint} {
        width: percentage($i * $delta / 100);
      }
    }
  }

  @each $breakpoint in $breakpoints {
    &.hmc-stacked-#{nth($breakpoint, 1)},
    &.hmc-carousel-#{nth($breakpoint, 1)} {
      @media #{nth($breakpoint, 2)} {
        .hmc-grid-item {
          left: auto;
          position: relative;
          top: auto;
          transform: none;
          width: auto;

          &.image-tile {
            .image-tile-content {
              width: auto;
            }
          }
        }
      }
    }

    &.hmc-grid-#{nth($breakpoint, 1)} {
      @media #{nth($breakpoint, 2)} {
        .hmc-grid-item {
          position: absolute;
          transform: translate(-50%, -50%);

          img {
            width: 100%;
          }

          &.image-tile {
            .image-tile-content {
              width: 100%;
            }
          }
        }

        @for $x from 0 through $rows + 1 {
          @for $y from 0 through $cols + 1 {
            .hmc-x#{$x}-y#{$y} {
              left: percentage($x / $rows);
              top: percentage($y / $cols);
            }
          }
        }

        @for $i from 1 through (100 / $delta) {
          .hmc-w#{$i * $delta} {
            width: percentage($i * $delta / 100);
          }
        }
      }
    }
  }

  .hmc-grid-items {
    height: 100%;
    margin: auto;
    max-width: $blade-max-width;
    position: relative;
  }

  .hmc-grid-item {
    position: relative;

    @media #{$sm-breakpoint} {
      position: absolute;
      transform: translate(-50%, -50%);

      img {
        width: 100%;
      }
    }
  }

  .hmc-grid-carousel {
    display: none;

    &.visible {
      display: block;

      .hmc-carousel-slide {
        &.visible {
          display: block;

          img {
            margin: auto;
          }
        }
      }
    }
  }

  .slick-next {
    right: 0;
    z-index: 1;
  }

  .slick-prev {
    left: 0;
    z-index: 1;
  }

  .slick-dots {
    top: 100%;
  }

  .hmc-grid-tile-info {
    background: $acr-red;
    color: $acr-white;
    left: 0;
    position: absolute;
    top: 0;
    z-index: 100;
  }

  .hmc-grid-tile-warning {
    background-color: $acr-red;
    color: $acr-white;
    display: block;
    font-weight: bold;
    left: 5px;
    padding: 5px;
    position: absolute;
    right: 5px;
    top: 35px;
    z-index: 100;

    &::before {
      color: $acr-black;
      content: '!';
      font-size: 14px;
      font-weight: bold;
      left: 13px;
      margin-top: -8px;
      position: absolute;
      top: 50%;
      z-index: 102;
    }

    &::after {
      background: transparent;
      border-color: $acr-orange transparent;
      border-style: solid;
      border-width: 0 11px 18px;
      content: '';
      left: 5px;
      margin-top: -10px;
      position: absolute;
      top: 50%;
      z-index: 1;
    }

    ul li {
      color: $acr-white;
    }
  }
}
