.acr-promotions {
  background: linear-gradient(to bottom, $acr-dim-grey-15 0%, r$acr-dim-grey-16 100%);
  display: block;
  position: relative;
  width: 100%;

  a {
    border-bottom: 0;
    display: block;
    height: 100%;
  }

  .promotions-slick {
    float: none;
    margin: auto;
    padding-bottom: 11.2rem;
    padding-left: 4.1rem;
    padding-right: 4.1rem;
    padding-top: 11.8rem;

    @media #{$sm-breakpoint} {
      box-shadow: 0 17px 50px 0 rgba($acr-black, .2);
    }

    article {
      backface-visibility: hidden;
      min-height: 26.8rem;
      padding: 0;
      position: relative;
      z-index: 5;

      &:last-child {
        @media #{$sm-breakpoint} {
          padding-left: .5px;
          z-index: 4;
        }
      }

      &:focus {
        outline: none;
      }

      .acr-heading {
        left: 0;
        margin: 0;
      }

      .copy {
        padding: 5%;
        position: absolute;
        z-index: 1;
      }

      h5 {
        bottom: 0;
      }

      img {
        display: inline-block;
        left: 0;
        position: relative;
        top: 0;
        transform: translateZ(0) scale3d(1, 1, 1);
        transition: transform 500ms cubic-bezier(.335, .015, .46, .995);
        width: 100%;
        z-index: 0;
      }

      &:hover img {
        transform: scale3d(1.1, 1.1, 1);
        transform-style: preserve-3d;
      }
    }

    .slick-dots {
      top: 84%;

      button {
        background-color: $acr-mid-grey;
        height: .3rem;
        padding: .3rem;
        width: .3rem;
      }

      .slick-active {
        button {
          background-color: $acr-black;
          height: 1rem;
          width: 1rem;
        }
      }
    }

    .slick-prev {
      left: 0;
    }

    .slick-next {

      right: 0;
    }

    .slick-arrow {
      background-repeat: no-repeat;
      background-size: contain;
      margin-top: -7rem;
      z-index: 1;

      &:hover {
        opacity: .5;
      }
    }

    .slick-list {
      position: relative;
    }
  }
}

@media #{$sm-breakpoint} {
  .acr-promotions {
    padding-bottom: 9.8rem;
    padding-left: 0;
    padding-right: 0;
    padding-top: 6.8rem;

    .promotions-slick {
      float: none;
      font-size: 0;
      letter-spacing: 0;
      line-height: 0;
      min-height: 17.2rem;
      padding: 0;
      position: relative;

      &::before,
      &::after {
        bottom: 0;
        left: 1rem;
        right: 1rem;
      }

      article {
        display: inline-block;
        float: none;
        min-height: 0;
        overflow: hidden;
        padding: 0;
      }
    }
  }
}

@media #{$lg-breakpoint} {
  .acr-promotions {
    padding-bottom: 7.6rem;
    padding-top: 4rem;

    .promotions-slick {
      min-height: 32.1rem;
    }
  }
}

@media #{$xl-breakpoint} {
  .acr-promotions {
    padding-bottom: 13.5rem;
    padding-top: 11.8rem;

    .promotions-slick {
      min-height: 43.9rem;
    }
  }
}
