.acr-sub-page-hero {
  height: auto;

  .header {
    padding-top: 3rem;

    @media #{$sm-breakpoint} {
      padding-top: 4rem;
    }

    @media #{$lg-breakpoint} {
      padding-top: 5rem;
    }
  }
}

.acr-features-transmission {
  @include acr-shadow-2;

  background-color: $acr-dim-grey-3;
  background-size: cover;

  h4 {
    font-size: 2.6rem;
    width: 90%;

    @media #{$sm-breakpoint} {
      font-size: 2rem;
    }
  }

  img {
    width: 100%;
  }

  .info {
    position: static;

    &.top {
      padding: 3rem 2rem;

      @media #{$sm-breakpoint} {
        align-items: flex-start;
        padding: 0;
        top: 29%;
      }
    }

    &.right {
      h3 {
        font-family: $acr-bespoke-light;
      }

      @media #{$sm-breakpoint} {
        .info-content {
          float: right;
          margin-top: -10%;
          padding-right: 1rem;
          width: 49%;
        }

        p {
          color: $acr-black;
        }
      }

      @media screen and (min-width: 650px) {
        .info-content {
          margin-top: 0;
        }
      }

      @media #{$md-breakpoint} {
        .info-content {
          margin-top: 10%;
        }

        h3 {
          font-size: 3.4rem;
        }
      }

      @media #{$lg-breakpoint} {
        .info-content {
          float: right;
          margin-top: 3%;
          padding-right: 3rem;
          width: 40%;
        }

        h4 {
          color: $acr-black;
          font-size: 2.6rem;
          line-height: 1.2em;
        }

        h3 {
          font-size: 3.6rem;
        }

        p {
          color: $acr-black;
        }
      }

      @media #{$xl-breakpoint} {
        .info-content {
          padding-right: 15rem;
        }
      }
    }
  }
}
