// SECTION HEADING
.acr-heading {
  margin: 0;

  &.acr-center {
    h6 {
      text-align: center;
    }

    .acr-underheading {
      margin: auto;
    }
  }

  &.acr-left {
    h6 {
      text-align: left;
    }

    .acr-underheading {
      margin-left: 0;
    }
  }

  &.acr-right {
    h6 {
      text-align: right;
    }

    .acr-underheading {
      margin-right: 0;
    }
  }

  .acr-underheading {
    background: $acr-red;
    height: 4px;
    margin: auto;
    width: 30px;

    &.acr-center {
      h6 {
        text-align: center;
      }

      .acr-underheading {
        margin: auto;
      }
    }

    &.acr-left {
      margin: 0;

      h6 {
        text-align: left;
      }

      .acr-underheading {
        margin-left: 0;
      }
    }

    &.acr-right {
      h6 {
        text-align: right;
      }

      .acr-underheading {
        margin-right: 0;
      }
    }

    // these styles below are for an adjustment made for mobile removing the
    // acr-anim class
    @media #{$sm-breakpoint} {
      // &.acr-left {
      //   margin: 0;
      // }

      &.acr-right {
        float: right;
        right: 0;
        width: 30px;
      }
    }

    @media #{$sm-breakpoint} {
      width: 25px;
    }
  }

  &.acr-anim { // class 'anim' to avoid jump on pageload
    opacity: 1;

    &.acr-underheading {
      margin: auto;
    }

    @media #{$sm-breakpoint} {
      opacity: 0;

      &.acr-underheading {
        width: 0;
      }

      &.acr-fleft {
        text-align: left;

        .acr-underheading {
          animation-delay: .8s;
          background: $acr-red;
          float: left;
          left: 0;
          width: 30px;

          @media #{$sm-breakpoint} {
            width: 25px;
          }
        }
      }

      &.acr-fbottom {
        text-align: center;

        .acr-underheading {
          animation-delay: .8s;
          background: $acr-red;
          height: 4px;
          margin: 0 auto;
          width: 30px;

          @media #{$sm-breakpoint} {
            width: 25px;
          }
        }
      }

      &.acr-fright {
        text-align: right;

        .acr-underheading {
          animation-delay: .8s;
          background: $acr-red;
          float: right;
          right: 0;
          width: 30px;

          @media #{$sm-breakpoint} {
            width: 25px;
          }
        }
      }
    }
  }
}

.blade-wrapper {
  .acr-heading {
    margin: 0 18px;
    padding: 0;

    @media #{$sm-breakpoint}{
      margin: 0 52px;
    }

    @media #{$xl-breakpoint}{
      margin: 0 85px;
    }
  }
}

.acr-underline { border-bottom: 1px solid $acr-black; }

// SEARCH AND DEALER LOCATOR

.acr-btn-find-dealer {
  border: 1px solid $acr-red;
  height: calc(100% - 1.2rem);

  span {
    color: $acr-dim-grey-1;
    font-size: 2rem;
    margin: .5rem;
    position: relative;
    top: .8rem;
  }
}

.acr-btn-mag {
  height: calc(100% - 1.2rem);

  span {
    color: $acr-dim-grey-1;
    font-size: 2rem;
    margin: .5rem;
    position: relative;
    top: .4rem;
  }
}

.acr-btn-close {
  height: calc(100% - 1.2rem);

  span {
    color: $acr-white;
    font-size: 2.3rem;
    font-weight: bold;
    margin-left: 1rem;
    position: relative;
    top: .7rem;
  }
}

.acr-btn-search {
  background: $acr-red;
  height: calc(100% - 1.2rem);

  span {
    color: $acr-white;
    font-size: 4rem;
  }
}

.acr-hero {

  .slick-dots {
    bottom: 8.2rem;
    display: block;
    left: 0;
    list-style: none;
    margin: 0;
    padding: 0;
    position: absolute;
    right: 0;
    text-align: center;
    top: auto;
    width: 100%;

    li {
      button {
        background: $acr-white;
      }
    }

    @media #{$lg-breakpoint} {
      button {
        padding: .4rem;
      }

      .slick-active {
        button {
          padding: .65rem;
        }
      }
    }
  }
}

.slick-dots {
  bottom: auto;
  padding: 0;
  text-align: center;
  top: 95%;

  li {
    cursor: pointer;
    display: inline-block;
    height: 1.5rem;
    margin: 0;
    padding: 0;
    position: relative;
    vertical-align: middle;
    width: 1.5rem;

    @media #{$lg-breakpoint} {
      width: 3rem;
    }

    button {
      @include vertical-align;
      background: $acr-deep-grey;
      border-radius: 50%;
      border-width: 0;
      color: transparent;
      display: block;
      font-size: 0;
      height: .2rem;
      line-height: 0;
      margin: auto;
      opacity: .5;
      outline: none;
      padding: .2rem;
      position: relative;
      width: .2rem;

      @media #{$lg-breakpoint} {
        padding: .4rem;
      }

      &:hover,
      &:focus {
        outline: none;
      }
    }
  }

  .slick-active {
    height: 1.3rem;
    margin: 0;

    button {
      background-color: $acr-deep-grey;
      height: .6rem;
      opacity: 1;
      padding: .4rem;

      @media #{$lg-breakpoint} {
        padding: .65rem;
      }
    }
  }
}

.slick-arrow {
  background: transparent;
  border: 0;
  cursor: pointer;
  margin-top: -2.8rem;
  padding: 2.7rem 1.2rem;
  position: absolute;
  top: 50%;

  &:focus {
    outline: 0;
  }
}

.acr-responsive-background-setheight {
  background-size: contain;
}

.acr-video-container,
.modal-body {
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;

  iframe,
  object,
  embed {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
}

