// CTA text types
.acr-cta-text-red { // red text
  @include cta-txt-type($acr-red);
}

.acr-cta-text-blk { // black text
  @include cta-txt-type($acr-black);
}

.acr-cta-text-wh { // white text
  @include cta-txt-type($acr-white);
}

.acr-cta-text-org { // orange text
  @include cta-txt-type($acr-orange);

  &::after {
    color: $acr-orange;
  }

  &:hover {
    color: $acr-orange;

    &::after {
      color: $acr-orange;
    }
  }
}

.acr-cta-dropdown {
  backface-visibility: hidden;
  display: inline-block;
  line-height: 1.4;
  text-align: center;

  @media #{$sm-breakpoint} {
    text-align: left;
  }

  [type="checkbox"] {
    display: none;

    &:checked {
      ~ .acr-cta-dropdown-container label {
        span {
          color: $acr-black;

          &::before {
            background: $acr-red;
          }

          &::after {
            left: 2px;
            top: -1px;
            transform: rotate(-90deg);
         }
        }
      }

      ~ .acr-cta-dropdown-container ul {
        @include acr-shadow-3;
        opacity: 1;
      }
    }
  }

  .acr-cta-dropdown-container {
    position: relative;

    label {
      @include cta-txt-type($acr-red);
      cursor: pointer;
      margin-bottom: 6px;
      padding-left: 24px;
      padding-right: 22px;
      position: relative;
      user-select: none;
      width: 100%;
      z-index: 15;

      span {
        &::after {
          backface-visibility: hidden;
          display: inline-block;
          left: 2px;
          transform: rotate(90deg) translateZ(0) scale(1, 1);
          // z-value for Chrome 1px movement issue
          transform-style: preserve-3d;
          transition: transform .3s ease;
        }
      }
    }

    ul {
      line-height: 30px;
      list-style: none;
      margin: -46px 0 0;
      opacity: 0;
      padding: 40px 20px 14px 24px;
      transition: opacity 1s;
      z-index: 10;

      a {
        border-bottom: 0;
        color: $acr-default;
        font-size: 1.4rem;
        font-weight: bold;
        text-decoration: none;
        white-space: nowrap;

        &:hover {
          color: $acr-red;
        }
      }
    }
  }
}

//Spinner
@keyframes slide-spin {
  to {
    transform: rotate(360deg);
  }
}

.animated-spinner {
  left: 50%;
  margin-left: -35px;
  margin-top: -35px;
  position: absolute;
  top: 50%;
  z-index: 2;

  &::before {
    animation: slide-spin .75s linear infinite;
    border-radius: 50%;
    border-right: 2px solid transparent;
    border-top: 2px solid $acr-red;
    content: '';
    height: 70px;
    position: absolute;
    width: 70px;
  }
}

a,
area,
input,
select,
textarea,
button,
iframe {
  &:focus,
  &:active {
    outline: none;
  }
}

// Remove controls from Firefox
input[type=number] {
  -moz-appearance: textfield;

  &::-webkit-inner-spin-button,
  &::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0; // Removes leftover margin
  }
}

.acr-disclaimer {
  display: none;
}
