// [AR-2907] PL: Create SASS file [Promo Tiles - Type 1]
.promo-tiles-1 {
  //Mobile First (< 551px)
  display: inline-block;
  margin: 20px 0;
  padding: 20px 0 0;
  //transform: translate3d(0, 0, 0);
  width: 100%;

  //Latest From Acura
  .acr-heading {
    margin-bottom: 4rem;
  }

  header {
    strong {
      display: block;
      font-family: $acr-avenir-ltpro-heavy;
      padding-bottom: 20px;
      text-align: center;
      vertical-align: center;

      //'+' icon
      [aria-haspopup] {
        cursor: pointer;
        font-size: 2.45rem;
        vertical-align: middle;
      }
    }
  }
  //Tiles are links
  a {
    height: inherit;
    width: 100%;

    figure {
      box-shadow: .6px 1.2px 15px $acr-black-15-opacity;
      color: $acr-white;
      display: block;
      height: 0;
      margin: 0 auto;
      overflow: hidden;
      padding-bottom: 43.33%;
      position: relative;
      width: 83.6%;
      z-index: 5;

      img {
        //Tile Images
        display: inline-block;
        height: auto;
        position: relative;
        transform: translateZ(0) scale3d(1, 1, 1);
        transition: transform 500ms cubic-bezier(.335, .015, .46, .995);
        width: 100%;
      }

      figcaption {
        //Tile Copy
        bottom: 1.25rem;
        left: 1.45rem;
        position: absolute;

        h5 {
          font-size: 1.8rem;
          line-height: 1.2;
          margin: 0;
        }
      }
    }

    &:last-child {
      figure {
        margin: 15px auto 35px;
      }
    }

    &:hover {
      figure {
        backface-visibility: hidden;
        z-index: 5;

        img {
          transform: scale3d(1.1, 1.1, 1);
          //transform-origin: 50% 50%;
          transform-style: preserve-3d;
        }
      }
    }
  }
  //Small Breakpoint +
  @media #{$sm-breakpoint} {
    box-shadow: none;
    display: block;
    margin: 0 auto;
    margin-bottom: 45px;
    margin-top: -62px;
    padding: 0;
    width: 93.2%;

    .acr-heading {
      display: none;
    }

    a {
      display: block;

      figure {
        box-shadow: 3.6px 4.8px 25px $acr-black-25-opacity;
        display: inline-block;
        float: left;
        margin-left: -.5px;
        margin-right: -.5px;
        padding-bottom: 25.9%;
        width: 50%;

        figcaption {
          bottom: 1rem;
          left: 1.8rem;

          h5 {
            font-size: 1.6rem;
            line-height: 1.8rem;
          }
        }
      }

      &:last-child {
        figure {
          margin: 0 0 0 -1px;
        }
      }
    }
  }
  //Large Breakpoint +
  @media #{$lg-breakpoint} {
    margin-bottom: 70px;
    margin-top: -75px;
    width: 89%;

    a figure figcaption {
      bottom: 2.4rem;
      left: 3rem;

      h5 {
        font-size: 2.2rem;
        line-height: 2.8rem;
      }
    }
  }
  //XL Breakpoint +
  @media #{$xl-breakpoint} {
    margin-bottom: 120px;
    margin-top: -150px;

    a figure figcaption {
      bottom: 2.8rem;
      left: 3.8rem;

      h5 {
        font-size: 3rem;
        line-height: 4rem;
      }
    }
  }
}
