.acr-header[role=menu] nav.flyout {
  display: none;
}
//Tablet View
@media #{$sm-breakpoint} {
  .acr-header[role=menu] nav {
    //Flyout (Desktop +)
    &.flyout {
      //display: none;
      background-color: $acr-black-50-opacity;
      height: 0;
      max-width: 2000px;
      position: fixed;
      top: 6rem;
      transition: all .25s ease-out;
      width: 100%;
      z-index: 50;

      &.expanded {
        display: block;

        &.overflowable {
          overflow: auto;
        }
      }

      h3 {
        color: $acr-black;
        display: inline-block;
        font-size: 1.8rem;
        text-align: left;
      }

      > aside {
        background-color: $acr-white;
        position: relative;
      }

      .flyout-disclaimer {
        background-color: $acr-white;
        bottom: -48px;
        display: block;
        font-size: 1rem;
        padding: 10px;
        position: absolute;
        text-align: left;
      }

      &[data-flyout=vehicles-flyout] {
        aside {
          display: flex;
          flex-flow: row wrap;

          section {
            display: inline-flex;
            flex: 2;
            flex-direction: column;
            padding: 6rem 1.5rem;

            h3 {
              font-family: $acr-avenir-ltpro-roman;
              line-height: 1.2;
            }

            a {
              padding: 1.5rem 0;
            }

            img,
            p {
              float: left;
              text-align: left;
              width: 50%;
            }

            &:last-child {
              align-items: center;
              background-color: $acr-dim-grey-7;
              flex: 0 0 100%;
              flex-direction: row;
              justify-content: space-around;

              h3 {
                text-align: center;
              }

              img,
              p {
                float: none;
                text-align: center;
              }

              > div {
                flex: 0 0 50%;
                height: 16.5vw;
                min-height: 13rem;
              }
            }
          }
        }
      }

      &[data-flyout=shopping-tools-flyout] {
        > aside {
          display: flex;
          flex-flow: column nowrap;
          width: 100%;
        }

        &.no-touch section {
          &.acr-shopping-tools-header {
            .acr-tools {
              .hoverline::after {
                background: transparent;
                bottom: -.1rem;
                content: '';
                display: block;
                height: 4px;
                left: 0;
                margin: auto;
                position: absolute;
                right: 0;
                transition: width .4s ease, background-color .4s ease;
                width: 0;

                @media #{$lg-breakpoint} {
                  bottom: -1rem;
                }
              }

              &:hover .hoverline::after {
                background: $acr-red;
                height: 4px;
                width: 100%;
              }
            }
          }

          ol,
          ul {
            li {
              a:hover {
                color: $acr-red;
              }

              a:hover::before {
                border-color: transparent transparent transparent $acr-red;
                border-style: solid;
                border-width: .4rem 0 .4rem .4rem;
                content: '';
                display: inline-block;
                height: 0;
                margin-left: -1rem;
                margin-right: .6rem;
                transform: rotate(360deg);
                width: 0;
              }
            }
          }
        }

        section {
          &.acr-shopping-tools-header {
            align-content: space-around;
            align-items: center;
            display: inline-flex;
            flex-flow: row wrap;
            height: 25rem;
            padding: 4vh 6vw;

            .acr-icon {
              width: 8rem;
            }

            .acr-tools {
              align-items: center;
              display: inline-flex;
              flex: 0 0 33%;
              flex-flow: column nowrap;
              justify-content: center;
              margin: auto;
              text-decoration: none;

              .tool-icon {
                font-size: 6.8rem;
              }

              .acr-desc {
                font-family: $acr-avenir-ltpro-heavy;
                font-size: 1.2rem;
                line-height: 3;
                position: relative;
              }
            }
          }

          &:last-child {
            background-color: $acr-dim-grey-7;
          }

          h3 {
            font-family: $acr-avenir-ltpro-heavy;
            font-size: 1.5rem;
            line-height: 2.8rem;
          }

          a {
            color: $acr-mid-grey;
          }

          ol,
          ul {
            list-style-type: none;
            padding: 0;

            li {
              position: relative;
              text-align: left;

              a {
                align-items: center;
                color: $acr-mid-grey;
                display: flex;
                font-size: 1.4rem;
                line-height: 1;
                padding: .9rem 0;
              }
            }
          }
          //Right side top row (columns)
          ul {
            border-bottom: 2px solid $acr-white;
            display: flex;
            margin: 0;
            padding: 2.6rem 4vw;
            width: 100%;

            > li {
              flex: 1;
            }
          }
          //Right side bottom row
          aside {
            border-top: 2px solid $acr-white;
            padding: 2.6rem 4vw;
            width: 100%;

            a {
              display: block;
              text-align: left;
            }

            h3 {
              font-size: 2rem;
              line-height: 1;
              margin-bottom: 2.6rem;
            }

            blockquote {
              border: 0;
              color: $acr-black;
              font-size: 1.8rem;
              margin-bottom: 0;
              padding: 0;
              text-align: left;

              p {
                line-height: 7rem;
              }

              span {
                display: inline-block;
              }

              span:first-child {
                font-family: $acr-avenir-ltpro-medium;
              }

              span:last-child {
                color: $acr-black;
                font-size: 1.7rem;
                line-height: 2rem;
                margin-left: .5rem;
              }
            }
          }
        }
      }

      .model {
        color: $acr-mid-grey;
        font-family: $acr-avenir-ltpro-roman;
        font-size: 1.31rem;
        line-height: 1.2;

        p {
          display: inline-block;

          strong {
            color: $acr-black;
            display: block;
            font-family: $acr-avenir-ltpro-black;
            font-size: 1.5rem;
            font-weight: normal;
          }
        }
      }
    }
  }
}

//Desktop View
@media #{$lg-breakpoint} {
  .acr-header[role=menu] {
    nav {
      &.flyout {
        top: 7rem;

        &[data-flyout=vehicles-flyout] > aside {
          flex-flow: row nowrap;
          position: relative;

          section {
            flex: 1;
            margin: 0;

            > h3 {
              padding-left: 2rem;
            }
          }

          section:last-child {
            align-items: center;
            flex: 0 0 25%;
            flex-flow: row nowrap;
            justify-content: center;

            > div {
              flex: 0 1 72%;
              height: 12vw;
            }

            p,
            img {
              width: 100%;
            }
          }

          .model p strong {
            font-size: 1.8rem;
          }
        }

        .flyout-disclaimer {
          bottom: 0;
          display: block;
          font-size: 1rem;
          padding: 1rem;
          position: absolute;
          text-align: left;
          width: 70%;
        }

        &[data-flyout=shopping-tools-flyout] > aside {
          flex-flow: row nowrap;

          section {
            margin: 0;

            ul {
              li {
                line-height: 1.4;
              }

              ol li a {
                font-size: 1.2rem;
                //line-height: 1.2;
                padding: .5rem 0;
              }

              // a:hover::before {
              //   line-height: 2.1rem;
              // }
            }

            aside {
              h3 {
                font-size: 1.5rem;
              }

              a {
                font-size: 1.3rem;
                line-height: 1.2;

                span {
                  margin: 0;
                }
              }

              blockquote {
                font-size: 1.4rem;
                line-height: 1.2;

                p {
                  line-height: 4.5rem;
                }

                span {
                  font-size: 3rem;
                }

                span:last-child {
                  font-size: 1.2rem;
                  line-height: 1.2;
                }
              }
            }

            &.acr-shopping-tools-header {
              flex: 1 1 58.5%;
              height: auto;

              .tool-icon {
                font-size: 7.6rem;
              }

              .acr-desc {
                font-size: 1.4rem;
              }
            }

            &:last-child {
              display: flex;
              flex: 1 1 41.5%;
              flex-flow: column nowrap;
            }
          }
        }
      }
    }
  }
}
//When larger than 1400
@media #{$xl-breakpoint} {
  .acr-header[role=menu] nav.flyout[data-flyout=vehicles-flyout] > aside section:last-child > div {
    max-height: 21rem;
  }

  .acr-header[role=menu] nav.flyout[data-flyout=shopping-tools-flyout] section ul {
    padding-right: 12vw;
  }

  .acr-header[role=menu] nav.flyout[data-flyout=vehicles-flyout] > aside {
    margin: 0 auto;
    max-width: 1600px;
  }
}
