.acr-header[role=menu] {
  nav {
    //Hamburger Menu (Mobile and Tablet)
    &[role=navigation] {
      //Fullscreen
      bottom: 0;
      display: none;
      height: 0;
      left: 0;
      opacity: 0;
      overflow: hidden;
      padding-bottom: 5rem;
      position: fixed;
      right: 0;
      top: 5rem;
      transition: all .3s ease-out;
      z-index: 0;

      &.expanded {
        display: block;
        height: calc(110% + 100px);
        opacity: 1;
        overflow-y: auto;
        padding-bottom: 100px;
        z-index: 51;
      }

      header {
        display: none;
        width: 100%;

        .logo {
          float: left;
          padding: 1.2rem 0 0 2.6rem;
        }

        .acr-icon-button-close {
          color: $acr-white;
          float: right;
          font-size: 3.5rem;
          padding: 2.8rem 3rem 0 0;

          &:hover,
          &:focus {
            color: $acr-mid-grey;
          }
        }
      }

      ul[role=menu] {
        background: $acr-black;
        margin: 2rem 0 0;
        padding: 0;
        //Hide tablet menu for mobile breakpoint
        &:last-child {
          display: none;
        }

        li[role=menuitem] {
          cursor: pointer;
          display: block;
          margin: 0 auto;
          padding: 1.4rem 0;
          text-align: center;
          width: 83.63%;

          &:hover,
          &:focus {
            background: $acr-mid-grey;
          }

          a {
            color: $acr-white;
            font-family: $acr-bespoke-regular;
            font-size: 1.4rem;
            position: relative;
            text-transform: uppercase;
          }
        }
      }
    }
  }
}

//Tablet View
@media #{$sm-breakpoint} {
  .acr-header[role=menu] nav {
    //Hamburger Menu (Mobile and Tablet)
    &[role=navigation] {
      padding-bottom: 6rem;
      top: 6rem;
      //Hide mobile menu for tablet breakpoint
      ul[role=menu] {
        margin: 1.5rem 0 0;

        &:first-child {
          display: none;
        }

        &:last-child {
          display: block;
        }

        li[role=menuitem] {
          padding: 2.3rem 0;

          a {
            font-size: 2rem;
          }
        }
      }
    }
  }
}
