.wrapper.ilx {
  .acr-mlp {
    .hero-container {
      @media #{$xl-breakpoint} {
        padding-top: 0;
      }
    }

    .main {
      @media #{$xl-breakpoint} {
        top: 8rem;
      }
    }

    .details {

      @media #{$sm-breakpoint} {
        position: relative;
        top: -25px;

      }

      @media #{$xl-breakpoint} {
        margin-bottom: 0;
        margin-top: 23rem;
      }
    }

    .cta {
      margin-bottom: 5rem;

      @media #{$xl-breakpoint} {
        margin-top: 9rem;
      }
    }

    .acr-heading {
      @media #{$xl-breakpoint} {
        margin-bottom: 8rem;
        margin-top: 14rem;
      }
    }

    .acr-x25-y25 {
      @media #{$xl-breakpoint} {
        top: 24%;
      }
    }
  }

  .acr-offers {
    padding-bottom: 3.5rem;

    .acr-cta-text-blk {
      span {
        @media #{$xl-breakpoint} {
          margin: 2rem 0;
        }
      }
    }
  }

  .shoping-tools-component {
    margin-top: 0;
    padding-top: 0;

    @media #{$sm-breakpoint} {
      padding-top: 4rem;
    }

    @media #{$lg-breakpoint} {
      padding-top: 6.5rem;
    }

    .buttons-container {
      a {
        @media #{$xl-breakpoint} {
          height: 18rem;
        }
      }
    }

    .acr-cta-container {
      @media #{$xl-breakpoint} {
        margin-top: 1rem;
      }
    }
  }

  .acr-mini-gallery {
    @media #{$sm-breakpoint} {
      padding-top: 4rem;
    }

    @media #{$xl-breakpoint} {
      padding-bottom: 9.5rem;
      padding-top: .5rem;
    }
  }

  .acr-text-block {
    @media #{$xl-breakpoint} {
      margin-bottom: 8.5rem;
      margin-top: 14rem;
    }

    h3 {
      @media #{$xl-breakpoint} {
        margin-bottom: 1.5rem;
      }
    }
  }

  .performance {
    .lightningcontainer {
      @media #{$xl-breakpoint} {
        top: 19%;
      }
    }

    .lighcapturecontainer {
      @media #{$xl-breakpoint} {
        padding-top: 2rem;
      }
    }

    .title {
      @media #{$xl-breakpoint} {
        font-size: 7rem;
      }
    }
  }

  .acr-accolades {
    .title {
      @media #{$sm-breakpoint} {
        padding-top: 7rem;
      }

      @media #{$lg-breakpoint} {
        padding-top: 8rem;
      }

      @media #{$xl-breakpoint} {
        padding-bottom: 8rem;
        padding-top: 17.5rem;
      }
    }

    .accolades-slider {
      @media #{$xl-breakpoint} {
        margin-top: 2rem;
      }
    }
  }

  .acr-packages {
    @media #{$sm-breakpoint} {
      padding-bottom: 3rem;
    }

    @media #{$lg-breakpoint} {
      padding-bottom: 4rem;
    }

    @media #{$xl-breakpoint} {
      padding-bottom: 3.5rem;
    }

    .acr-packages-title {
      @media #{$lg-breakpoint} {
        padding-top: 9rem;
      }

      @media #{$xl-breakpoint} {
        padding-top: 13rem;
      }
    }

    .acr-packages-model {
      @media #{$xl-breakpoint} {
        padding-bottom: 10rem;
        padding-top: 4rem;
      }
    }

    .acr-packages-cta {
      padding-top: 3.5rem;
    }
  }

  .find-a-dealer {
    .find-a-dealer-content {
      padding-bottom: 4rem;
    }

    .find-a-dealer-title .acr-heading {
      @media #{$sm-breakpoint} {
        padding-top: 4.5rem;
      }

      @media #{$lg-breakpoint} {
        padding-top: 8.5rem;
      }

      @media #{$xl-breakpoint} {
        padding-top: 10.5rem;
      }
    }
  }

  .acr-sign-up-form-emails {
    padding-bottom: 4rem;
    padding-top: 3.5rem;

    @media #{$sm-breakpoint} {
      padding-bottom: 5rem;
      padding-top: 0;
    }

    @media #{$lg-breakpoint} {
      padding-bottom: 10rem;
      padding-top: 8rem;
    }

    @media #{$xl-breakpoint} {
      padding-bottom: 9rem;
      padding-top: 11rem;
    }

    header {
      @media #{$xl-breakpoint} {
        margin-top: 7.5rem;
      }
    }

    form {
      display: table;
    }
  }
}
