//Mobile View
$hamburger-offset-xs: 3.9rem;
$hamburger-offset-sm: 6.3rem;

nav.mlp-nav[role=navigation] {
  backface-visibility: hidden;
  background-color: $acr-white;
  border-bottom: 1px solid $acr-dim-grey-13;
  max-width: $wrapper-max-width;
  position: fixed;
  top: 50px;
  transition: transform .5s ease-in-out 0s;
  width: 100%;
  z-index: 49;

  @media #{$sm-breakpoint} {
    top: 60px;
  }

  @media #{$lg-breakpoint} {
    top: 70px;
  }

  section {
    align-items: center;
    background: $acr-white;
    display: flex;
    height: $hamburger-offset-xs;
    justify-content: space-between;
    margin: 0 auto;
    max-width: $blade-max-width;
    padding: 0 1.9rem 0 1.5rem;
    position: relative;
    z-index: 1;

    > ul li {
      a {
        border-bottom: 0;
      }
    }

    > h2 {
      align-items: flex-end;
      display: flex;
      flex: 1 1 17%;
      justify-content: flex-start;
      position: relative;

      svg:first-child {
        fill: $acr-black;
        height: 1rem;
        width: 4.7rem;
      }

      div[role=presentation] {
        align-items: center;
        cursor: pointer;
        display: flex;
        font-size: 1rem;
        font-weight: 600;
        height: .8rem;
        justify-content: space-between;
        pointer-events: none;
        position: relative;
        //width: 4.8rem;

        div[role=listbox] {
          height: .7rem;
          letter-spacing: .2rem;
          line-height: 0;
          margin-right: .5rem;
        }

        &.expanded {
          a {
            border-bottom: 0;
            display: block;
            margin-bottom: .5rem;

            &.selected {
              text-decoration: underline;
            }
          }
        }

        a {
          border-bottom: 0;
          color: $acr-black;
          display: none;
          font-size: 1rem;
          letter-spacing: .1rem;
          //margin-right:.2rem;

          &.selected {
            display: block;
            line-height: 1;
            pointer-events: none;
          }
        }

        svg {
          display: none;
          fill: $acr-red;
          height: .5rem;
          margin-right: -1rem;
          width: .8rem;
        }
      }
    }

    > button {
      background: $acr-white;
      border: 0;
      color: $acr-gray-blue;
      display: inline-block;
      flex: 1 1 30%;
      font-family: $acr-avenir-ltpro-heavy;
      font-size: 1rem;
      letter-spacing: .13rem;
      vertical-align: middle;

      svg {
        display: inline-block;
        fill: $acr-gray-blue;
        height: 1rem;
        vertical-align: middle;
        width: 1.1rem;
      }
    }

    > span {
      display: flex;
      flex: 1 1 15%;
      justify-content: flex-end;

      a.acr-cta-btn {
        border: 1px $acr-red solid;
        font-family: $acr-avenir-ltpro-medium;
        font-size: 1rem;
        line-height: 1;
        //max-width: unset;
        min-width: 0;
        padding: .6rem 1.1rem .5rem;
      }
    }
  }

  //Mobile Hamburguesa Menu
  aside[role=menu] {
    align-items: center;
    background: $acr-deep-grey;
    bottom: 0;
    color: $acr-white;
    display: flex;
    flex-flow: column nowrap;
    font-size: 1.4rem;
    justify-content: space-around;
    padding: 1.2rem 0 100vh;
    position: relative;
    top: 0;
    width: 100%;

    &.acr-animation {
      transition: top .3s ease-out;
    }

    strong {
      border-bottom: .1rem $acr-white solid;
      font-family: $acr-bespoke-bold;
      line-height: 1;
      margin-bottom: 1.2rem;
      margin-top: 2rem;
      padding-bottom: 1rem;
      //transform: translateY(-50%);
    }

    a {
      border-bottom: 0;
      color: $acr-white;
      font-family: $acr-bespoke-regular;
      //line-height: 1;
      padding: 2rem 30vw;

      &:hover {
        background: $acr-gray-blue;
      }
    }
  }
}

html.ie nav.mlp-nav[role=navigation] section > h2 div[role=listbox] {
  min-height: 1.1rem;
}

//Tablet View
@media #{$sm-breakpoint} {
  nav.mlp-nav[role=navigation] {
    border-bottom: 2px solid $acr-dim-grey-13;

    section {
      height: $hamburger-offset-sm;
      padding: 0 3.5rem;

      > h2 {
        flex: 1 1 15%;

        svg:first-child {
          height: 1.4rem;
          width: 6.8rem;
        }

        div[role=listbox] a {
          letter-spacing: .15rem;
        }
      }

      button {
        font-family: $acr-avenir-ltpro-medium;
        // font-size: 1.6rem;
        // letter-spacing: .2rem;
        font-size: 1.65rem;
        letter-spacing: .15rem;

        svg {
          height: 1.4rem;
          width: 1.5rem;
        }
      }

      > span a.acr-cta-btn {
        border-width: .2rem;
        font-size: 1.4rem;
        letter-spacing: .15rem;
        padding: .7rem 1.4rem .6rem;
      }
    }

    aside[role=menu] {
      padding: 5rem 0 100vh;

      strong {
        font-size: 2rem;
        letter-spacing: .1rem;
        margin-bottom: 1rem;
        padding-bottom: .5rem;
      }

      a {
        font-size: 2rem;
        letter-spacing: .1rem;
        padding: 2rem 32vw;
      }
    }
  }
}

//Desktop View
@media #{$lg-breakpoint} {
  nav.mlp-nav[role=navigation] {
    section {
      padding: 0 3.8rem 0 6rem;

      > h2 {
        flex: 0 0 10%;
        justify-content: flex-start;

        div[role=presentation] {
          pointer-events: all;

          div[role=listbox] {
            //padding-right: .35rem;
            line-height: 1;
          }

          svg {
            display: block;
          }
        }
      }

      > ul {
        align-items: flex-start;
        display: flex;
        flex: 1 1 68%;
        flex-flow: row nowrap;
        height: calc(100% + 1px);
        justify-content: space-around;
        list-style-type: none;
        margin-bottom: 0;

        li {
          color: $acr-gray-blue;
          cursor: pointer;
          height: inherit;
          position: relative;

          //&.no-touch section {
            //&.acr-shopping-tools-header {
          //.acr-tools {
          .hoverline::after {
            background: transparent;
            bottom: 0;
            content: '';
            display: block;
            height: 2px;
            left: 0;
            margin: auto;
            position: absolute;
            right: 0;
            transition: width .4s ease, background-color .4s ease;
            width: 0;
          }

            // &:hover .hoverline::after {
            //   background: $acr-red;
            //   height: 4px;
            //   width: 100%;
            // }
          //}
            //}

          &:hover,
          &.active {
            //border-bottom: 2px solid $acr-red;
            color: $acr-red;

            .hoverline::after {
              background: $acr-red;
              width: 100%;
            }
          }

          a {
            color: inherit;
            display: inline-block;
            font-family: $acr-avenir-ltpro-medium;
            font-size: 1.4rem;
            height: inherit;
            letter-spacing: .15rem;
            padding-top: 2rem;
          }
        }
      }
    }
  }
}

@media #{$xl-breakpoint} {

  nav.mlp-nav[role=navigation] {
    section {
      padding: 0 3.8rem 0 6.5rem;

      h2 {
        //width: auto;
      }

      > ul {
        padding: 0 28rem 0 5rem;
      }

      > span {
        padding-right: 1rem;
      }
    }
  }
}
