.culture {

  @media #{$sm-breakpoint} {
    padding-top: 90px;
  }

  @media #{$lg-breakpoint} {
    padding-top: 50px;
  }

  @media #{$xl-breakpoint} {
    padding-top: 50px;
  }

  .row {
    margin: 0;
    padding: 0;
  }

  .inner-content {
    overflow: inherit;
  }

  .acr-card {

    .title-text {
      line-height: 3.4rem;
      margin-top: 0;
      text-align: center;

      @media #{$sm-breakpoint} {
        text-align: left;
      }

      @media #{$lg-breakpoint} {
        text-align: left;
      }

      @media #{$xl-breakpoint} {
        text-align: left;
      }
    }

    .desc-text {
      @media #{$sm-breakpoint} {
        margin: 1rem 0;
      }
    }
  }

  img {
    @include acr-shadow-4;
  }

  .acr-heading {
    margin-bottom: 24px;

    .acr-underheading {
      margin: 3px auto 0;
    }
  }

  .acr-callout {
    display: none;

    @media #{$sm-breakpoint} {
      display: block;
    }

    @media #{$lg-breakpoint} {
      display: block;
      margin-top: 50px;
    }

    @media #{$xl-breakpoint} {
      display: block;
      margin-top: 100px;
    }
  }

  .acr-larger {
    margin: 0 auto;
    max-width: 70%;

    @media #{$sm-breakpoint} {
      margin: 11px auto 0;
      max-width: 70%;
    }

    @media #{$lg-breakpoint} {
      margin: 20px auto 0;
      max-width: 70%;
    }

    @media #{$xl-breakpoint} {
      margin: 20px auto 0;
      max-width: 50%;
    }
  }

  .culture-grid {
    padding-top: 20px;

    @media #{$sm-breakpoint} {
      padding-top: 0;
    }

    @media #{$lg-breakpoint} {
      padding-top: 0;
    }

    @media #{$xl-breakpoint} {
      margin-bottom: -50px;
      padding-bottom: 200px;
      padding-top: 0;
    }
  }

  .bg {
    border: 0;
    max-height: 750px;
    position: absolute;

    @media #{$sm-breakpoint} {
      min-height: 1350px;
      position: relative;
    }

    @media #{$lg-breakpoint} {
      min-height: 1350px;
      position: relative;
    }

    @media #{$xl-breakpoint} {
      min-height: 1350px;
      position: relative;
    }
  }

  .internal-gallery {
    padding: 0 0 0 6px;

    @media #{$sm-breakpoint} {
      height: 640px;
      margin-top: -10%;
      position: relative;
      width: 280px;
    }

    @media #{$lg-breakpoint} {
      height: 350px;
      left: -21px;
      margin-top: 38%;
      position: relative;
      width: 330px;
    }

    @media #{$xl-breakpoint} {
      height: 350px;
      left: 62px;
      position: relative;
      top: -43px;
      width: 424px;
    }

    .galleryimg1 {
      @media #{$sm-breakpoint} {
        left: 42%;
        top: 180px;

      }

      @media #{$lg-breakpoint} {
        left: 16%;
        top: 0;
      }

      @media #{$xl-breakpoint} {
        left: 16%;
        top: 20px;
      }
    }

    .galleryimg2 {
      @media #{$sm-breakpoint} {
        left: 47%;
        margin-top: 4%;
        z-index: 20;
      }

      @media #{$lg-breakpoint} {
        left: 12%;
        top: 27%;
      }

      @media #{$xl-breakpoint} {
        left: 4%;
        top: 40%;
      }
    }

    .galleryimg3 {
      @media #{$sm-breakpoint} {
        margin-top: -54px;
        right: 6%;
      }

      @media #{$lg-breakpoint} {
        margin-top: -12%;
        right: 20%;
      }

      @media #{$xl-breakpoint} {
        right: 29%;
        top: 88%;
      }
    }

    .acr-grid-item {
      margin: 20px 100px;
      overflow: inherit;

      @media #{$sm-breakpoint} {
        @include acr-shadow-4;
        margin: 0;
        overflow: hidden;
        padding: 0;
      }

      @media #{$lg-breakpoint} {
        @include acr-shadow-4;
        margin: 0;
        overflow: hidden;
        padding: 0;
      }

      @media #{$xl-breakpoint} {
        @include acr-shadow-4;
        margin: 0;
        overflow: hidden;
        padding: 0;
      }
    }

    a {
      display: block;
      position: relative;

      span {
        color: $acr-white;
        left: 50%;
        position: absolute;
        top: 50%;
        z-index: 10;

        &::before {
          left: -40px;
          position: relative;
          top: -40px;
        };

        &.acr-icon-insta {
          font-size: 3rem;
          right: -30%;
          top: 8%;

          &::before {
            left: 0;
            position: relative;
            top: -10px;
          };
        }

        &.acr-icon-play-btn {
          font-size: 8rem;
        }
      }
    }

    .slick-slide {
      img {
        margin: 0 auto;
        max-height: 130px;
        min-height: 130px;
      }

      a {
        overflow: hidden;
      }
    }

    .slick-track {
      padding: 40px 0;
    }

    .slick-prev {
      left: 0;
    }

    .slick-next {
      right: 0;
    }

    .slick-arrow {
      background-repeat: no-repeat;
      background-size: contain;
      margin-top: -6rem;
      z-index: 1;

      &:hover,
      &:focus {
        color: $acr-black;
      }
    }
  }

  .social-container {
    left: -35px;
    position: relative;

    @media #{$sm-breakpoint} {
      left: 0;
    }

    @media #{$lg-breakpoint} {
      left: 0;
    }

    @media #{$xl-breakpoint} {
      left: 0;
    }

    .social-title {
      color: $acr-black;
      font-family: $acr-avenir-ltpro-black;
      font-size: 1.4rem;
      margin-top: 40px;
      text-align: left;
    }

    .social {
      margin-top: 20px;
      padding: 0;

      @media #{$sm-breakpoint} {
        margin-top: 4px;
      }

      @media #{$lg-breakpoint} {
        margin-top: 15px;
      }

      @media #{$xl-breakpoint} {
        margin-top: 0;
      }

      li {
        border: 1px solid $acr-dim-grey-2;
        float: left;
        list-style-type: none;
        padding-bottom: 1px;
        padding-top: 14px;
        text-align: center;
        width: 25%;

        &:first-child {
          border-left: 0;
        }

        &:last-child {
          border-right: 0;
        }


        @media #{$sm-breakpoint} {
          border: 0;
          display: inline-block;
          height: 25px;
          margin-left: 15px;
          padding: 0;
          width: auto;

          &:first-child {
            margin-left: -12px;
          }
        }

        @media #{$lg-breakpoint} {
          border: 0;
          display: inline-block;
          height: 45px;
          margin-left: 15px;
          padding: 0;
          width: auto;

          &:first-child {
            margin-left: -12px;
          }
        }

        @media #{$xl-breakpoint} {
          border: 0;
          display: inline-block;
          height: 35px;
          margin-left: 15px;
          padding: 0;
          width: auto;

          &:first-child {
            margin-left: -12px;
          }
        }

        a {
          color: $acr-mid-grey;
          font-size: 3.5rem;

          @media #{$lg-breakpoint} {
            font-size: 4rem;
          }

          @media #{$xl-breakpoint} {
            font-size: 4rem;
          }

          &:hover,
          &:focus {
            color: $acr-deep-grey;
          };
        }
      }
    }
  }

  .cultural-mobile-icon-video-slick {
    color: $acr-white;
    font-size: 4rem;
    z-index: 10;

    @media #{$lg-breakpoint} {
      top: 40%;
    }

    @media #{$xl-breakpoint} {
      top: 40%;
    }

    &::before {
      left: -10%;
      position: relative;
      top: -25%;
    };
  }

  .spotlightimagecontainer {
    margin-bottom: 40px;

    @media #{$sm-breakpoint} {
      right: 29%;
      top: 12%;
    }

    @media #{$lg-breakpoint} {
      right: 29%;
      top: 23%;
    }

    @media #{$xl-breakpoint} {
      right: 30%;
      top: 27%;
    }

    img {
      @include acr-shadow-4;
      max-height: 178px;

      @media #{$sm-breakpoint} {
        max-height: 222px;
      }

      @media #{$lg-breakpoint} {
        max-height: 336px;
      }

      @media #{$xl-breakpoint} {
        max-height: 442px;
      }
    }
  }
}

.add-social-margin {
  left: 15%;
  margin-top: -190px;
}

.add-social-margin-xl {
  margin-top: -80px;
}

.modal-culture {
  background-color: $acr-white;
}
