.mlp-shopping-tools {
  background-color: $acr-white;
  padding-top: 6rem;

  .row {
    margin: 0 -15px;
  }

  .mlp-shopping-tools-title {
    margin-bottom: 3rem;
  }

  .acr-shopping-tools {
    margin-bottom: 3rem;

    .acr-tools-mobile {
      border-right: 1px solid $acr-white;
      cursor: pointer;
      height: 105px;
      min-height: 105px;
      padding: 1.5rem 0;
      position: relative;

      .hoverline {
        &::after {
          background: transparent;
          bottom: 0;
          content: '';
          display: block;
          height: 4px;
          left: 0;
          margin: auto;
          position: absolute;
          right: 0;
          transition: width .4s ease, background-color .4s ease;
          width: 0;
        }
      }

      &:hover .hoverline::after {
        background: $acr-red;
        height: 4px;
        width: 100%;
      }

      span {
        display: block;

        &.tool-icon {
          font-size: 5rem;

          @media #{$sm-breakpoint}{
            font-size: 5.7rem;
          }

          @media #{$lg-breakpoint}{
            font-size: 6.3rem;
          }

          @media #{$xl-breakpoint}{
            font-size: 5.7rem;
          }
        }
      }
    }
  }
}

.acr-brochure-link {
  margin-bottom: 20px;
  text-align: center;
}

@media #{$sm-breakpoint} {
  .mlp-shopping-tools {
    .acr-shopping-tools {
      margin-bottom: 2.5rem;

      .acr-tools-mobile {
        height: 135px;
        min-height: 135px;
      }
    }

    .mlp-shopping-tools-title {
      margin-bottom: 4rem;
    }

    .acr-cta-container {
      .acr-brochure-link {
        padding-left: 2.5rem;
        text-align: left;

        &:first-child {
          padding-right: 2.5rem;
          text-align: right;
        }
      }
    }
  }
}

@media #{$lg-breakpoint} {
  .mlp-shopping-tools {
    .acr-shopping-tools {
      margin-bottom: 4.2rem;

      .acr-tools-mobile {
        height: 180px;
        min-height: 180px;
      }

      .col-md-6 {
        width: $twelve-36th;
      }
    }

    .mlp-shopping-tools-title {
      margin-bottom: 7rem;
    }
  }
}

@media #{$xl-breakpoint} {
  .mlp-shopping-tools {
    .acr-shopping-tools {
      margin-bottom: 4.2rem;

      .acr-tools-mobile {
        height: 180px;
        min-height: 180px;
      }
    }

    .mlp-shopping-tools-title {
      margin-bottom: 8rem;
    }
  }
}
