//// ---------------------------------------------------------------------------------------------------  ////
////    Start _responsive-table-detail-collapse.scss
//// ---------------------------------------------------------------------------------------------------  ////

.responsive-table-detail,
.acr-modal-page-container {
  .collapse-content {
    margin: 0 auto;
    max-width: 1400px;
    text-align: left;

    h5 {
      color: $acr-black;
      font-family: $acr-bespoke-light;
      font-size: 2.4rem;
      letter-spacing: 0;
      position: relative;
      text-transform: none;

      @media #{$lg-breakpoint} {
        color: $acr-mid-grey;
      }
    }

    .header {
      background-color: $acr-white;
      height: 5.5rem;
      position: relative;

      svg {
        display: none;
      }

      @media #{$sm-breakpoint} {
        height: 7.5rem;
        padding: 1.8rem 3rem;
      }

      @media #{$xl-breakpoint} {
        height: 8.5rem;
        padding: 1.8rem 4rem;
      }
    }

    .header-name {
      display: none;

      @media #{$lg-breakpoint} {
        display: inline-block;
        font-size: 2.4rem;
      }

      @media #{$xl-breakpoint} {
        font-size: 3rem;
      }
    }

    .content-container {
      background-color: $acr-white;

      @media #{$lg-breakpoint} {
        display: flex;
        max-height: calc(100vh - 7.5rem);
        padding: 0 3rem 3rem;
      }

      @media #{$xl-breakpoint} {
        padding: 0 4rem 4rem;
      }
    }

    picture {
      width: 100%;

      @media #{$lg-breakpoint} {
        max-width: 50%;
        order: 2;
      }

      img {
        width: 100%;
      }
    }

    .content {
      overflow: auto;
      padding: .3rem 1.7rem;

      @media #{$lg-breakpoint} {
        order: 1;
        padding: 0;
        width: 50%;
      }

      @media #{$lg-breakpoint} {
        max-height: calc(100vh - 8.5rem);
      }
    }

    .description {
      font-family: $acr-avenir-ltpro-light;
      padding-bottom: 4rem;

      ul {
        li {
          font-family: $acr-avenir-ltpro-book;
          font-size: 1.7rem;
        }
      }

      @media #{$lg-breakpoint} {
        border-top: 1px solid $acr-dim-grey-14;
        font-size: 1.7rem;
        padding-right: 5rem;
        padding-top: 2.5rem;
      }

      @media #{$xl-breakpoint} {
        font-size: 1.7rem;
      }
    }

    .content-name {
      @media #{$lg-breakpoint} {
        display: none;
      }
    }
  }
}

.acr-modal-page-container {
  overflow: hidden;
}
