.acr-packages {
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0;

  .row {
    margin: 0;
  }

  .acr-packages-title {
    margin: 0 -15px;
    padding: 3.4rem 0;
  }

  .acr-packages-ctas {
    padding: .5rem 0;
    text-align: center;

    div:first-child {
      margin-bottom: 1.4rem;
    }
  }

  .acr-packages-model {
    padding-bottom: 1.4rem;
    text-align: center;
  }

  .acr-packages-slider.editing-mode {
    overflow-x: scroll;
  }

  .acr-packages-slick {
    padding-bottom: 2.4rem;

    .package {
      outline: 0;

      .title {
        padding-bottom: 1.5rem;
      }

      .tagline {
        padding-bottom: 1rem;
      }

      .description {
        margin-bottom: 50px;
      }
    }

    .slick-slide {
      background-position: bottom;
      background-repeat: no-repeat;
      background-size: contain;
      margin: 0 0 1rem;
      padding: 0 2.4rem 10%;
      position: relative;

      // This will work as long as we have 10 cards or less
      @for $i from 1 through 10 {
        &:nth-child(#{$i}) {
          z-index: #{10 - $i};
        }
      }
    }

    .slick-dots {
      li button {
        background-color: $acr-mid-grey;
        border-radius: 4rem;
        height: 2rem;
        transform: scale(.3);
        width: 2rem;
      }

      li.slick-active button {
        background-color: $acr-deep-grey;
        transform: scale(.6);
      }
    }

    .slick-disabled {
      cursor: auto;
      opacity: 0;
    }

    .slick-arrow {
      font-size: 2rem;
      z-index: 1;
    }

    .slick-next {
      right: 0;
    }

    .slick-prev {
      left: 0;
    }
  }
}

// 768px+
@media #{$md-breakpoint} {
  .acr-packages {
    background-size: cover;

    .acr-packages-title {
      padding: 8.4rem 0 5rem;
    }

    .acr-packages-model {
      font-size: 3.5rem;
      padding: 0 0 7rem;
    }

    .acr-packages-ctas {
      padding: 1rem 0 6rem;

      div:first-child {
        margin-bottom: 1.6rem;
      }
    }

    .acr-packages-slick {
      overflow: hidden;

      .slick-list {
        overflow: visible;

        .package {
          margin: 0 -15px;
          max-width: 450px;
          padding: 0 0 135px 35px;
          width: calc(100vw/3.9);

          &:nth-child(odd) {
            padding-bottom: 240px;
            width: calc(100vw/3.3);
          }

          &:first-child {
            margin-left: 5px;
            margin-right: -70px;
            padding: 0 45px 240px 10px;
          }

          .title {
            max-width: 190px;
          }

          .description {
            margin-bottom: 5px;
            margin-right: 15px;
            max-width: 220px;
            min-width: 160px;
          }
        }
      }
    }
  }
}

// 1024+
@media #{$lg-breakpoint} {
  .acr-packages .acr-packages-slick .slick-list .package {
    margin: 0 -20px 0 -30px;
    padding: 0 10px 145px 40px;

    &:nth-child(odd) {
      padding: 0 0 260px 50px;
    }

    &:first-child {
      margin-left: 7px;
      margin-right: -50px;
      padding-right: 35px;
    }

    &:last-child {
      right: 10px;
    }

    .title {
      max-width: 250px;
    }

    .description {
      margin-bottom: 20px;
      max-width: 200px;
      min-width: 190px;
    }
  }
}

// 1400+
@media #{$xl-breakpoint} {
  .acr-packages {
    background-size: cover;

    .acr-packages-slick .slick-list .package {
      &:nth-child(odd) {
        padding-bottom: 280px;
      }

      &:first-child {
        padding-left: 85px;
      }

      &:last-child {
        right: 30px;
      }

      .description {
        margin-bottom: 45px;
        max-width: 270px;
        min-width: 250px;
      }
    }

    .acr-packages-title {
      padding: 12.8rem 0 0;
    }

    .acr-packages-model {
      font-size: 4rem;
      padding: 3.5rem 0 9rem;
    }
  }
}
