.hmc-grid-100 {
  .acr-heading {
    margin-top: 4rem;
  }

  .heading-tile {
    h2 {
      span {
        display: block;
      }
    }

    .inner-content {
      overflow: visible;
    }
  }

  .text-tile {
    .inner-content {
      overflow: hidden;

      @media #{$sm-breakpoint} {
        overflow: visible;
      }
    }

    h4 {
      padding: 10px 0;

      @media #{$lg-breakpoint} {
        padding: 12px 0;
      }
    }

    p {
      font-size: 1.6rem;
      padding: 10px 0;

      @media #{$lg-breakpoint} {
        padding: 12px 0;
      }
    }

    .hmc-grid-ctas {
      padding: 10px 0;

      @media #{$lg-breakpoint} {
        padding: 12px 0;
      }
    }

    span {
      display: inline-block;
    }

    .acr-cta-text {
      div {
        display: inline-block;
        padding: 0;
      }
    }

    .text-tile-content {
      height: 100%;
      padding: 14px 30px;

      @media #{$sm-breakpoint} {
        height: auto;

        &.border {
          border: 2px solid $acr-white;
          padding: 0;

          p {
            padding: 20px;
          }

          .hmc-grid-ctas {
            padding: 0;
          }
        }
      }

      @media #{$lg-breakpoint} {
        padding: 18px 40px;
      }

      &.acr-white-background {
        background-color: $acr-white;
      }

      &.acr-black-background {
        background-color: $acr-black;
      }
    }

    .acr-cta-btn {
      max-width: none;
      min-width: auto;
      padding-left: 1.5rem;
      padding-right: 1.5rem;
      width: 100%;

      @media #{$sm-breakpoint} {
        font-size: 1rem;
      }

      @media #{$md-breakpoint} {
        font-size: 1.3rem;
      }

      @media #{$lg-breakpoint} {
        font-size: 1.6rem;
      }
    }
  }

  .video-tile {
    a {
      border-bottom: 0;
      display: block;
    }

    img {
      width: 100%;
    }

    video {
      display: none;
      margin-top: .3rem;
      width: 100%;
    }

    .play-icon {
      fill: $acr-white;
      left: calc(50% - 2.25rem);
      position: absolute;
      top: calc(50% - 2.25rem);

      .acr-icon {
        fill: $acr-white;
      }
    }
  }

  .image-tile {
    img,
    picture {
      width: 100%;
    }
  }

  .image-tile-content {
    position: relative;

    img {
      width: 100%;
    }

    h2 {
      left: -.4em;
      position: absolute;
      top: -1.3em;

      @media #{$sm-breakpoint} {
        font-size: 2rem;
      }

      @media #{$md-breakpoint} {
        font-size: 3.6rem;
      }

      @media #{$lg-breakpoint} {
        font-size: 5rem;
      }

      span {
        display: block;
      }
    }
  }

  .image-tile,
  .cta-link {
    a {
      border-bottom: 0;
      display: block;
    }

    svg {
      fill: $acr-white;
      height: 3.4rem;
      width: 3.4rem;
    }

    .btn-wrapper {
      bottom: 1.5rem;
      left: 1.5rem;
      min-width: 40%;
      position: absolute;
      right: .1rem;

      a {
        display: table;
      }
    }

    .cta-txt {
      color: $acr-white;
      display: table-cell;
      font-family: $acr-avenir-ltpro-heavy;
      font-size: 1.45rem;
      letter-spacing: .07rem;
      line-height: 1.5rem;
      position: relative;
      text-transform: uppercase;
      top: 1px;
      vertical-align: middle;

      &.is-black {
        color: $acr-black;
      }

      &.is-red {
        color: $acr-red;
      }

      &.is-orange {
        color: $acr-orange;
      }

      &.is-white {
        color: $acr-white;
      }
    }

    .inner-content {
      display: flex;
      justify-content: center;
      overflow: visible;
    }
  }

  .cta-link {
    .cta-link-content {
      .btn-wrapper {
        position: static;
      }
    }
  }

  .inner-content {
    margin: 0 1.8rem;

    @media #{$sm-breakpoint} {
      margin: 0;
    }
  }

  // Spacing
  .hmc-grid-items {
    .heading-tile {
      margin-bottom: 20rem;
      margin-top: 9rem;

      @media #{$sm-breakpoint} {
        margin: 0;
      }

      h2 {
        font-size: 4.3rem;

        @media #{$sm-breakpoint} {
          font-size: 6.1rem;
        }

        @media #{$lg-breakpoint} {
          font-size: 6.4rem;
        }

        @media #{$xl-breakpoint} {
          font-size: 7.4rem;
        }
      }
    }

    .video-tile {
      margin-bottom: 7.5rem;
      margin-top: 7.5rem;

      @media #{$sm-breakpoint} {
        margin: 0;
      }

      .inner-content {
        overflow: visible;

        .acr-video-grow {
          overflow: hidden;
        }
      }
    }

    .image-tile {
      margin-bottom: 7.5rem;
      margin-top: 7.5rem;

      @media #{$sm-breakpoint} {
        margin-bottom: 5rem;
        margin-top: 5rem;
      }

      .image-tile-content {
        @media #{$sm-breakpoint} {
          width: 100%;
        }
      }

      h2 {
        font-size: 6.3rem;

        @media #{$sm-breakpoint} {
          font-size: 6.5rem;
        }

        @media #{$lg-breakpoint} {
          font-size: 6.3rem;
        }

        @media #{$xl-breakpoint} {
          font-size: 7.4rem;
        }
      }
    }
  }
}
