@mixin cta-btn-type-brdr($btn-border-color, $btn-bg-color, $btn-text-color, $btn-hover-bg-color, $btn-hover-text-color) {
  background: $btn-bg-color;
  border: 2px solid $btn-border-color;
  color: $btn-text-color;
  cursor: pointer;
  transition: none;

  svg {
    fill: $btn-text-color;
  }

  &:hover,
  &:active,
  &:focus,
  &.is-active {
    background-color: $btn-hover-bg-color;
    color: $btn-hover-text-color;

    svg {
      fill: $btn-hover-text-color;
    }
  }

  @media #{$sm-breakpoint} {
    background: linear-gradient(to right, $btn-hover-bg-color 50%, transparent 0%);
    background-position: right bottom;
    background-size: 200% 100%;
    border: 2px solid $btn-border-color;
    transition: all .3s ease;

    &:hover,
    &:active,
    &:focus,
    &.is-active {
      background-color: transparent;
      background-position: left bottom;
      color: $btn-hover-text-color;

      svg {
        fill: $btn-hover-text-color;
      }
    }
  }
}

@mixin cta-btn-type-full($btn-bg-color, $btn-text-color, $btn-hover-bg-color) {
  background: $btn-bg-color;
  border: 0;
  color: $btn-text-color;
  cursor: pointer;
  padding: 1.6rem 3.3rem 1.2rem;
  transition: color .3s ease, background-color .3s ease;

  svg {
    fill: $btn-text-color;
  }

  &:hover,
  &:active,
  &:focus,
  &.is-active {
    background-color: $btn-hover-bg-color;
  }

  @media #{$sm-breakpoint} {
    background: linear-gradient(to right, $btn-hover-bg-color 50%, $btn-bg-color 0%);
    background-position: right bottom;
    background-size: 200% 100%;
    transition: all .3s ease;

    &:hover,
    &:active,
    &:focus,
    &.is-active {
      background-color: transparent;
      background-position: left bottom;
    }
  }
}

@mixin cta-btn-wicon($btn-border-color, $btn-bg-color, $btn-icon-color, $btn-border-hover-color, $btn-hover-bg-color, $btn-icon-hover-color, $btn-text-color) {
  border: 2px solid $btn-border-color;
  cursor: pointer;

  .cta-text {
    color: $btn-text-color;
  }

  svg {
    fill: $btn-icon-color;
  }

  &:hover,
  &:active,
  &:focus,
  &.is-active {
    background: $btn-hover-bg-color;

    svg {
      fill: $btn-icon-hover-color;
    }
  }

  @media #{$sm-breakpoint} {
    background: linear-gradient(to right, $btn-hover-bg-color 50%, transparent 0%);
    background-position: right bottom;
    background-size: 200% 100%;
    transition: background .3s ease;

    &:hover,
    &:active,
    &:focus,
    &.is-active {
      background-position: left bottom;
      border: 2px solid $btn-border-hover-color;
    }
  }
}

@mixin cta-txt-type($btn-text-color) {
  border-bottom: 0;
  color: $btn-text-color;
  display: inline-block;
  font-family: $acr-avenir-ltpro-heavy;
  font-size: 1.5rem;
  letter-spacing: .07rem;
  line-height: 10px;
  text-transform: uppercase;

  @media #{$sm-breakpoint} {
    font-size: 1.4rem;
    letter-spacing: 0;
  }

  svg {
    fill: $btn-text-color;
  }

  span {
    color: $btn-text-color;
    display: inline-block;
    line-height: 1.4;
    margin: .5rem 0;
    overflow: hidden;
    padding-right: 4px;
    position: relative;
    top: .06rem;

    &::before {
      background: transparent;
      bottom: 0;
      content: '';
      height: 2px;
      position: absolute;
      transition: width .4s ease, background-color .4s ease;
      width: 0;
    }

    &::after {
      color: $acr-red;
      //content: '\e914';
      font-family: 'acura-icon';
      font-size: 1.1rem;
      left: 0;
      margin-left: .4rem;
      position: relative;
      top: 0;
    }
  }

  &:hover span,
  &:active span {
    &::before {
      background: $acr-red;
      width: calc(100% - 1.5rem);
    }
  }

  &.no-carat {
    span::after {
      display: none;
    }

    &:hover span::before {
      width: 100%;
    }
  }
}

@mixin cta-btn-toggle($btn-border-color, $btn-bg-color, $btn-text-color, $btn-active-bg-color, $btn-hover-bg-color, $btn-hover-text-color) {
  background: $btn-bg-color;
  border: 0;
  border-bottom: 2px solid transparent;
  color: $btn-text-color;
  cursor: pointer;
  position: relative;
  transition: all .3s ease;
  z-index: 1;

  svg {
    fill: $btn-text-color;
  }

  &:active,
  &.is-active {
    background: $btn-active-bg-color;
    border-color: $btn-border-color;
    box-shadow: 0 3px 10px 1px rgba($acr-black, .21);
    color: $btn-hover-text-color;
    z-index: 2;

    svg {
      fill: $btn-hover-text-color;
    }

    &:hover {
      background: $btn-active-bg-color;
      border-color: $btn-border-color;
      box-shadow: 0 3px 10px 1px rgba($acr-black, .21);
      color: $btn-hover-text-color;
      z-index: 2;

      svg {
        fill: $btn-hover-text-color;
      }
    }
  }

  @media #{$sm-breakpoint} {
    &:hover {
      background: $btn-hover-bg-color;
      border-color: none;
      box-shadow: none;
      color: $btn-hover-text-color;
      z-index: 2;

      svg {
        fill: $btn-hover-text-color;
      }
    }
  }
}
