.acr-model-slick {
  padding-bottom: 5rem;

  .slick-next {
    display: inline-block;
    right: 0;
  }

  .slick-prev {
    display: inline-block;
    left: 0;
  }

  .slick-arrow {
    height: 2.2rem;
    margin-top: 0;
    opacity: .5;
    padding: 0;
    transform: translateY(-50%);
    z-index: 2;

    @media #{$sm-breakpoint} {
      height: 5.4rem;
    }

    @media #{$lg-breakpoint} {
      z-index: auto;
    }

    &:hover {
      opacity: 1;
    }
  }
}
