.acr-disclaimer {
  @include acr-shadow-2;
  background-color: $acr-white;
  bottom: 0;
  float: none;
  left: 50%;
  overflow: auto;
  padding: 3rem 4rem 3rem 2.5rem;
  position: fixed;
  transform: translateX(-50%);
  z-index: 2048;

  p {
    margin-top: 2rem;
  }

  h4 {
    font-size: 2.7rem;
    letter-spacing: .02rem;
    line-height: 1.1;
    text-transform: uppercase;
    width: 90%;

    sup {
      font-family: $acr-avenir-ltpro-heavy;
      font-size: 39%;
      top: 1em;
    }
  }

  .acr-icon-button-close {
    background: none;
    border: 0;
    color: $acr-mid-grey;
    font-size: 4rem;
    padding: 2px 6px 3px;
    position: absolute;
    right: 12px;
    top: 18px;

    &:hover {
      color: $acr-black;
    }

    &:active,
    &:focus {
      border: 0;
      outline: none;
    }
  }

  @media #{$sm-breakpoint} {
    padding-right: 10rem;
    padding-top: 3rem;

    .acr-icon-button-close {
      right: 13px;
      top: 17px;
    }

    p {
      margin-top: 1rem;
    }
  }

  @media #{$lg-breakpoint} {
    padding-left: 3.7rem;
    padding-right: 14rem;

    h4 {
      font-size: 3rem;
    }
  }

  @media #{$xl-breakpoint} {
    h4 {
      margin-bottom: .9rem;
    }
  }
}

.acr-disclaimer-big {
  overflow: auto;
  top: 0;
}

