.acr-gallery-modal {
  background-color: $acr-white;
  display: none;
  height: 100vh;
  left: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1000;

  @media #{$sm-breakpoint} {
    background-color: $acr-black-50-opacity;
  }

  &.visible {
    align-items: center;
    display: flex;
    justify-content: center;
    user-select: none;
  }

  img {
    margin: auto;
    opacity: 0;

    // the image loading in FF has some fouc, so we manually make them visible on load
    &.visible {
      opacity: 1;
    }
  }

  h5 {
    font-size: 3rem;

    @media #{$sm-breakpoint} {
      font-family: $acr-bespoke-light;
      font-size: 3.6rem;
      line-height: 1em;
    }

    @media #{$sm-breakpoint} {
      font-size: 5rem;
    }
  }

  p {
    margin-bottom: 1em;
  }

  picture {
    background-color: $acr-black;
    display: none;
    overflow: hidden;

    &.visible {
      align-items: center;
      display: flex;
    }
  }

  iframe {
    display: none;
    margin: auto;

    &.visible {
      display: block;
    }
  }

  .gallery-modal-window {
    background-color: $acr-white;
    height: 100vh;
    overflow: hidden;
    position: relative;
    width: 100vw;
  }

  .header {
    background-color: $acr-white;
    height: 5.5rem;
    position: relative;

    @media #{$sm-breakpoint} {
      background: transparent;
      height: 7.5rem;
      padding: 1.8rem 3rem;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 1000;
    }

    @media #{$xl-breakpoint} {
      height: 8.5rem;
      padding: 1.8rem 4rem;
    }
  }

  .gallery-modal-close {
    cursor: pointer;
    height: 2.7rem;
    margin: 1.3rem .8rem;
    position: absolute;
    right: 0;
    top: 0;
    width: 2.7rem;

    @media #{$sm-breakpoint} {
      fill: $acr-white;
      height: 4.2rem;
      margin: 2rem;
      width: 4.2rem;
    }

    @media #{$xl-breakpoint} {
      height: 5.5rem;
      margin: 3rem;
      width: 5.5rem;
    }
  }

  .content-container {
    background-color: $acr-white;
    text-align: center;

    @media #{$sm-breakpoint} {
      text-align: left;
    }
  }

  .content {
    overflow: auto;
    padding: .3rem 1.7rem;

    @media #{$sm-breakpoint} {
      height: 25rem;
      padding: 2rem 5.5rem;
    }

    @media #{$xl-breakpoint} {
      padding: 3.5rem 6rem;
    }
  }

  .gallery-slider {
    background-color: $acr-black;
    position: relative;
  }

  .prev-container {
    left: 0;
    padding-left: 1rem;
    padding-right: 1rem;
    position: absolute;
    top: calc(50% - 1.35rem);

    @media #{$lg-breakpoint} {
      top: calc(50% - 2rem);
    }

    @media #{$lg-breakpoint} {
      top: calc(50% - 2.5rem);
    }
  }

  .next-container {
    padding-left: 1rem;
    padding-right: 1rem;
    position: absolute;
    right: 0;
    top: calc(50% - 1.35rem);

    @media #{$lg-breakpoint} {
      top: calc(50% - 2rem);
    }

    @media #{$lg-breakpoint} {
      top: calc(50% - 2.5rem);
    }
  }

  .gallery-modal-next,
  .gallery-modal-prev {
    cursor: pointer;
    height: 2.7rem;
    width: 2.7rem;

    @media #{$lg-breakpoint} {
      height: 4rem;
      margin: 0 1rem;
      width: 4rem;
    }

    @media #{$xl-breakpoint} {
      height: 5rem;
      margin: 0 2rem;
      width: 5rem;
    }
  }

  .description {
    @media #{$sm-breakpoint} {
      font-size: 1.6rem;
      padding-top: 1rem;
    }
  }

  .cta-text {
    margin: 2.5rem 0;
  }

  .cta-container {
    @media #{$sm-breakpoint} {
      text-align: right;
    }
  }

  .cta-anchor {
    display: none;

    &.visible {
      display: block;
    }
  }
}
