//Icons
%ui-icon-base {
  fill: $acr-default;
  height: 4.5rem;
  width: 4.5rem;
}

//CTA
%acr-cta-base {
  display: inline-block;
  font-family: $acr-avenir-ltpro-black;
  font-size: 1.4rem;
  letter-spacing: .07rem;
  line-height: 1.5;
  max-width: 290px;
  min-width: 190px;
  padding: 1.4rem 3rem 1rem;
  text-align: center;
  text-transform: uppercase;

  @media #{$sm-breakpoint} {
    padding: 1.5rem 1.5rem 1rem;
  }

  @media #{$lg-breakpoint} {
    padding: 1.4rem 3.1rem 1rem;
  }

  svg {
    height: 1rem;
    // In order for the auto with to work, the SVG icon should have the
    // viewBox property defined
    width: auto;
  }
}

%acr-cta-base-wicon {
  display: inline-block;
  font-family: $acr-avenir-ltpro-black;
  font-size: 1.4rem;
  letter-spacing: .07rem;
  line-height: 1.5;
  padding: .6rem 0 .3rem;
  text-align: center;
  text-transform: uppercase;
  width: 33px;

  svg {
    height: 1rem;
    // In order for the auto with to work, the SVG icon should have the
    // viewBox property defined
    width: auto;
  }
}

//SHADOWS
@mixin acr-shadow-1 {
  box-shadow: 0 1px 3px rgba($acr-black, .12), 0 1px 2px rgba($acr-black, .24);
}

@mixin acr-shadow-2 {
  box-shadow: 0 3px 10px 1px rgba($acr-black, .21);
}

@mixin acr-shadow-3 {
  box-shadow: 6px 10px 87px 0 rgba($acr-dim-grey-17, .1);
}

@mixin acr-shadow-4 {
  box-shadow: 6px 10px 87px 0 rgba($acr-dim-grey-17, .3);
}

@mixin acr-shadow-5 {
  box-shadow: 15px 30px 70px 0 rgba($acr-black, .3);
}

@mixin acr-shadow-6 {
  box-shadow: 0 2px 30px 0 rgba($acr-black, .1);
}
