// Default sitewide stagger animation start times within a div : add class="stagger" to each element you would like staggered

.acr-stagger {

  &:nth-child(2) {
    animation-delay: .5s;
  }

  &:nth-child(3) {
    animation-delay: 1s;
  }

  &:nth-child(4) {
    animation-delay: 1.5s;
  }

  &:nth-child(5) {
    animation-delay: 2s;
  }

  &:nth-child(6) {
    animation-delay: 2.5s;
  }

  &:nth-child(7) {
    animation-delay: 3s;
  }

  &:nth-child(8) {
    animation-delay: 3.5s;
  }

  // currently used for vehicles
  &.is-01s {
    animation-delay: .1s;
  }

  &.is-02s {
    animation-delay: .2s;
  }

  &.is-03s {
    animation-delay: .3s;
  }

  &.is-04s {
    animation-delay: .4s;
  }

  &.is-05s {
    animation-delay: .5s;
  }

  &.is-06s {
    animation-delay: .6s;
  }

  &.is-07s {
    animation-delay: .7s;
  }

  &.is-08s {
    animation-delay: .8s;
  }

  &.is-09s {
    animation-delay: .9s;
  }

  &.is-95s {
    animation-delay: 1s;
  }

  &.is-1s {
    animation-delay: 1.3s;
  }

  &.is-1-5s {
    animation-delay: 1.5s;
  }

  &.is-2s {
    animation-delay: 2s;
  }

  &.is-3s {
    animation-delay: 3s;
  }

  &.is-4s {
    animation-delay: 4s;
  }
}

.scatter {
  // currently shopping tools
  &:nth-child(1) {
    animation-delay: .45s;
  }

  &:nth-child(2) {
    animation-delay: .5s;
  }

  &:nth-child(3) {
    animation-delay: .55s;
  }

  &:nth-child(4) {
    animation-delay: .6s;
  }

  &:nth-child(5) {
    animation-delay: .65s;
  }

  &:nth-child(6) {
    animation-delay: .7s;
  }

  &:nth-child(7) {
    animation-delay: .75s;
  }

  &:nth-child(8) {
    animation-delay: .8s;
  }
}


.acr-anim { opacity: 0;}// start off at zero opacity

/// hover classes
// over promo images and tile images
// for use within a div with fixed width and height
.acr-grow {
  transform-origin: 50% 50%;
  transition: transform 500ms cubic-bezier(.335, .015, .46, .995);

  &:hover {
    transform: scale3d(1.1, 1.1, 1);
    transform-origin: 50% 50%;
  }
}

@media handheld {
  .acr-grow {
    transform: none;

    &:hover {
      transform: none;
    }
  }
}

// for use within a div with fixed width and height
.acr-video-grow {
  position: relative;

  &:hover img {
    transform: scale3d(1.1, 1.1, 1);
    transform-origin: 50% 50%;
  }

  &:hover svg {
    opacity: .7;
    transition: opacity 500ms ease-in-out;
    transition-timing-function: ease, ease-out, linear;
  }

  img {
    transform-origin: 50% 50%;
    transition: transform 500ms cubic-bezier(.335, .015, .46, .995);
    z-index: 1;
  }

  a {
    cursor: pointer;
    display: block;
    height: 100%;
  }

  svg {
    bottom: 0;
    fill: $acr-white;
    height: 70px;
    left: 0;
    margin: auto;
    opacity: 1;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity all 1s ease;
    width: 48%;
    z-index: 2;
  }
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}


// fadeFromLeft
@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-left {
  animation-name: fadeInLeft;
  left: 0;
}

// fadeFromRight
@keyframes fadeInRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-right {
  animation-name: fadeInRight;
}


// fadeFromBottom
@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-up {
  animation-name: fadeInUp;
}



// VEHICLE LINEUP

// fadeFromBottomScatter -- IE: vehicle lineup
@keyframes fadeInUpScatter {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 60px, 0);
    transform: translate3d(0, 60px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-up-scatter {
  animation-name: fadeInUpScatter;
}

@keyframes fadeInUpScatterShort {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30px, 0);
    transform: translate3d(0, 30px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-up-scatter-short {
  animation-name: fadeInUpScatterShort;
}

// FadeIn & Move (for vehicle line up)
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

// fadeIn
.acr-fade-in {
  animation: fadeIn ease-in 1;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  backface-visibility: hidden;
  opacity: 0;// initial state of zero for a fade in
}

// fadeFromTop
@keyframes fadeInDown {
  from {
    backface-visibility: hidden;
    opacity: 0;
    transform: translate3d(0, -20px, 0);
    transition: opacity 1.3s cubic-bezier(.215, .61, .355, 1), transform 1s cubic-bezier(.165, .84, .44, 1);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-down {
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-name: fadeInDown;
  backface-visibility: hidden;
  opacity: 0;// initial state of zero for a fade in
}


// fadeFromTop more
@keyframes fadeInDownMore {
  from {
    backface-visibility: hidden;
    opacity: 0;
    transform: translate3d(0, -20px, 0);
    transition: opacity 2s cubic-bezier(.215, .61, .355, 1), transform 1s cubic-bezier(.165, .84, .44, 1);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-down-more {
  animation-duration: 3s;
  animation-fill-mode: forwards;
  animation-name: fadeInDownMore ease-in 1;
  opacity: 0;// initial state of zero for a fade in
}



// special fadefromDown
@keyframes fadeInTop {
  from {
    opacity: 0;
    -webkit-transform: scale(1) translate3d(0, -20px, 0);
    transform: scale(1) translate3d(0, -20px, 0);
    -webkit-transition: opacity 1.3s cubic-bezier(.215, .61, .355, 1), -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
    transition: opacity 1.3s cubic-bezier(.215, .61, .355, 1), transform 1s cubic-bezier(.165, .84, .44, 1);
    transition-delay: 600ms;
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.acr-fade-in-top {
  animation: fadeInTop ease-in 1;
  animation-delay: .3s;
  animation-duration: .5s;
  animation-fill-mode: forwards;
  opacity: 0;// initial state of zero for a fade in
}


// centerFillLines
// fill underline of section header from center
@keyframes fromCenterFill {
  from {
    display: inline-block;
    opacity: 1;
    transition: width .5s, color .5s;
    width: 0;
  }

  to {
    background-size: 100% 100%;
    opacity: 1;
    width: 100%;
  }
}

.acr-fill-from-center { // red
  animation: fromCenterFill ease-in 1;
  animation-duration: 6s;
  animation-fill-mode: forwards;
  background-color: $acr-red;
  background-position: 50% 100%;
  border-top: 3px solid $acr-red;
  max-width: 25px;
  position: relative;
  transition: width .5s, color .5s;
  width: 0;
}

.acr-fill-from-center-is-grey { // grey
  animation: fromCenterFill ease-in-out 1;
  animation-duration: 4s;
  animation-fill-mode: forwards;
  background-color: $acr-dim-grey-2;
  background-position: 50% 100%;
  border-top: 3px solid $acr-dim-grey-2;
  max-width: 89px;
  position: relative;
  transition: width .5s, color .5s;
  transition-delay: 600ms;
  width: 0;
}



// Zoom Effect

// fadeIn Zoom
@keyframes fadeInZoom {
  from {
    opacity: 0;
    transform: scale3d(.3, .3, .3);
  }

  50% {
    opacity: 1;
  }
}

.acr-fade-in-zoom {
  animation: fadeInZoom ease-in 1;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  opacity: 1;// initial state of zero for a fade in
}



.zoom-it {
  animation: zoomInto 2.5s;
  animation-fill-mode: forwards;
  left: 0;
  position: absolute;
  top: 0;
}

@keyframes zoomInto {
  0% {
    animation-timing-function: ease-in-out;
    opacity: 1;
    transform: scale3d(1.1, 1.1, 1.1) translate3d(10px, 0, 100px);
  }

  100% {
    opacity: 1;
    transform: scale3d(1.2, 1.2, 1.2) translate3d(5px, 0, 40px);
  }
}

.zoom-it-box {
  animation: zoomBoxInto 2.5s;
  animation-fill-mode: forwards;
  left: 0;
  position: absolute;
  top: 0;
}


.key-image-box {
  height: auto;
  width: 400px;
}

@keyframes zoomBoxInto {
  0% {
    animation-timing-function: ease-in-out;
    opacity: 1;
    transform: scale3d(.9, .9, .9) translate3d(10px, 0, 100px);
  }

  100% {
    opacity: 1;
    transform: scale3d(1.2, 1.2, 1.2) translate3d(5px, 0, 40px);
  }
}

// Zoom Effect
.zoom-it-2 {
  animation: zoomInto2 8s;
  animation-fill-mode: forwards;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
}

@keyframes zoomInto2 {
  from {
    opacity: 0;
    transform: scale3d(.9, .9, .9);
  }

  90% {
    opacity: 1;
  }
}

// fadeZoomUp
@keyframes fadeScaleUp {
  from {
    backface-visibility: hidden;
    opacity: 0;
    transform: scale(.9) translate3d(0, 50px, 0);
    transition: opacity 1.3s cubic-bezier(.215, .61, .355, 1), transform 1s cubic-bezier(.165, .84, .44, 1);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.acr-fade-scale-up {
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-name: fadeScaleUp;
  backface-visibility: hidden;
  opacity: 0;// initial state of zero for a fade in
}



// Small Devices, Tablets
@media only screen and (max-device-width: 1024px) {
  .acr-anim,
  .acr-fade-in-down,
  .acr-fdown,
  .acr-fdown-more,
  .acr-ftop,
  .acr-fbottom-scatter,
  .acr-fbottom.acr-fleft,
  .acr-fright,
  .acr-underheading.acr-fcenter,
  .acr-fcenter-is-grey,
  .acr-stagger,
  .acr-scattered,
  .acr-fade-zoom,
  .key-image-box,
  .key-headline,
  .acr-fade-scale-up {
    opacity: 1;
  }

  .animated,
  .acr-anim,
  .acr-fade-in-down,
  .acr-fdown,
  .acr-fdown-more,
  .acr-ftop,
  .acr-fbottom-scatter,
  .acr-fbottom.acr-fleft,
  .acr-fright,
  .acr-underheading.acr-fcenter,
  .acr-fcenter-is-grey,
  .acr-stagger,
  .acr-scattered,
  .acr-fade-zoom,
  .key-image-box,
  .key-headline ,
  .acr-fade-scale-up {
    animation: none;
    transform: none;
    transition-property: none;
  }
}
