.find-a-dealer {
  padding: 0 15px;
  text-align: center;

  .find-a-dealer-title {
    background-repeat: no-repeat;
    background-size: cover;

    .title-row {
      margin: 3.45em -1.5rem 17.85rem;
    }
  }

  .find-a-dealer-content {
    background: $acr-dim-grey-3;
    padding: 3.5rem 0 3.85rem;

    .find-a-dealer-form {
      &.desktop-target {
        display: none;
      }

      &.mobile-target {
        display: block;
      }

      .find-a-dealer-form-content {

        h3 {
          font-size: 3.4rem;
          line-height: 1.25;
          margin-bottom: 2rem;
        }

        .input-wrapper {
          display: inline-block;
          padding-top: 2.5rem;
          position: relative;

          .error-message {
            color: $acr-red;
            position: absolute;
            top: 0;
          }

          .acr-input-pre {
            background-color: $acr-white;
            display: inline-block;
            height: 4.4rem;
            padding: 1.1rem;
            vertical-align: middle;
            width: 4.4rem;
          }

          input {
            background-color: $acr-white;
            height: 4.4rem;
            margin: 0 -.5rem;
            outline: 0;
            padding: 1.3rem 0;
            vertical-align: middle;

            @media #{$sm-breakpoint} {
              width: 144px;
            }

            @media #{$lg-breakpoint} {
              width: 230px;
            }

            @media #{$xl-breakpoint} {
              width: 230px;
            }
          }
        }

        .acr-cta-btn {
          min-width: 0;
          padding: .7rem;

          .acr-icon-explore {
            vertical-align: middle;
          }
        }
      }
    }
  }
}

@media #{$sm-breakpoint} {
  .find-a-dealer {

    .find-a-dealer-title {
      .title-row {
        margin: 4em -1.5rem 17.85rem;
      }
    }

    .find-a-dealer-content {
      background: $acr-white;
      padding: 1rem 2rem;

      .find-a-dealer-form {
        &.desktop-target {
          display: block;
        }

        &.mobile-target {
          display: none;
        }

        .find-a-dealer-form-content {
          background: $acr-white;
          box-shadow: 6px 10px 87px 0 rgba($acr-dim-grey-17, .1);
          padding: 3.5rem 5rem 4rem;
          transform: translateY(-68px);

          h3 {
            font-size: 3.3rem;
          }

          .input-wrapper {
            padding-top: 3rem;

            .error-message {
              top: 1rem;
            }

            input {
              background-color: $acr-dim-grey-3;
            }

            .acr-input-pre {
              background-color: $acr-dim-grey-3;
            }
          }
        }
      }
    }
  }
}

@media #{$md-breakpoint} {
  .find-a-dealer {
    .find-a-dealer-title {
      .title-row {
        margin: 6.4em -1.5rem 17.85rem;
      }
    }

    .find-a-dealer-content {
      background: $acr-white;
      padding: 1rem 2rem 0;

      .find-a-dealer-form {
        .find-a-dealer-form-content {
          padding: 5.5rem 8rem 6.6rem;
          transform: translateY(-120px);

          h3 {
            font-size: 4.6rem;
          }

          .input-wrapper {
            padding-top: 4rem;

            .error-message {
              top: 2rem;
            }
          }
        }
      }
    }
  }
}

@media #{$xl-breakpoint} {
  .find-a-dealer {
    .find-a-dealer-title {
      .title-row {
        margin: 7.2em -1.5rem 17.85rem;
      }
    }

    .find-a-dealer-content {
      background: $acr-white;
      padding: 1rem 2rem;

      .find-a-dealer-form {
        .find-a-dealer-form-content {
          padding: 7rem 20rem;
          transform: translateY(-135px);

          .input-wrapper {
            padding-top: 5rem;

            .error-message {
              top: 2.6rem;
            }
          }
        }
      }
    }
  }
}
