.performance {
  overflow: hidden;
  padding-bottom: 3rem;
  position: relative;

  @media #{$sm-breakpoint} {
    overflow: visible;
    padding-bottom: 0;
  }

  .row {
    margin: 0;
    padding: 0;
  }

  .bg {
    background-position: center top;
    background-repeat: no-repeat;
    background-size: 800px auto;
    border: 0;
    min-height: 1067px;
    position: absolute;
    width: 100%;

    @media #{$sm-breakpoint} {
      background-position: center -15.75rem;
      background-repeat: no-repeat;
      background-size: 148.5rem auto;
      min-height: 168.9rem;
    }

    @media #{$lg-breakpoint} {
      background-position: center -6.75rem;
      background-repeat: no-repeat;
      background-size: 140.5rem auto;
      min-height: 168.9rem;
    }

    @media #{$xl-breakpoint} {
      background-position: center -6.75rem;
      background-repeat: no-repeat;
      background-size: 1600px auto;
      max-height: 168.9rem;
    }
  }

  .acr-heading {
    left: 0;
    padding: 1.625rem;
    position: absolute;
    top: 0;
    z-index: 2;

    @media #{$sm-breakpoint} {
      padding: 9.313rem 2.875rem;
    }

    @media #{$lg-breakpoint} {
      padding: 6.313rem 2.875rem;
    }

  }

  .acr-grid-25-container {
    overflow: inherit;
    padding: 0;

    @media #{$sm-breakpoint} {
      min-height: 168.9rem;
    }

    @media #{$lg-breakpoint} {
      min-height: 165.9rem;
    }

    @media #{$xl-breakpoint} {
      min-height: 169rem;
    }

    .acr-grid-item {
      padding: 0 1.625rem;
      position: relative;

      @media #{$sm-breakpoint} {
        padding: 0;
        position: absolute;
      }

      @media #{$lg-breakpoint} {
        padding: 0;
        position: absolute;
      }

      @media #{$xl-breakpoint} {
        padding: 0;
        position: absolute;
      }

      &.ligthcapturecontainer {
        padding-top: 11.56rem;

        @media #{$sm-breakpoint} {
          padding-top: 0;
          right: 27%;
        }

        @media #{$lg-breakpoint} {
          letter-spacing: 1rem;
          padding-top: 27.5rem;
          right: 27%;
        }

        @media #{$lg-breakpoint} {
          letter-spacing: 1rem;
          padding-top: 47.5rem;
          right: 35%;
        }

        .title-text {
          color: $acr-white;
          font-family: $acr-bespoke-regular;
          font-size: 4.5rem;
          width: 80%;

          @media #{$sm-breakpoint} {
            min-height: 7.563rem;
            width: 100%;
          }

          @media #{$lg-breakpoint} {
            font-size: 6.5rem;
            min-height: 7.563rem;
            width: 100%;
          }

          @media #{$lg-breakpoint} {
            font-size: 4.625rem;
            min-height: 7.563rem;
            width: 100%;
          }


        }
      }

      &.videocontainer {
        box-shadow: 0 2px 30px 0 $acr-black-50-opacity;
        margin-top: 13.88rem;
        min-height: 9.375rem;

        @media #{$sm-breakpoint} {
          margin-top: 20rem;
          min-height: 12.81rem;
        }

        @media #{$lg-breakpoint} {
          margin-left: -3rem;
          margin-top: 8.625em;
          min-height: 14.875em;
        }

        a {
          display: block;
          position: relative;

          span {
            color: $acr-white;
            font-size: 5.25rem;
            position: absolute;
            top: 40%;
            z-index: 10;

            &::before {
              left: -25%;
              position: relative;
              top: -25%;
            };
          }
        }
      }

      &.statscontainer {
        margin-top: 2.68rem;

        @media #{$sm-breakpoint} {
          margin-top: 0;
        }
      }

      &.descriptioncontainer {
        border: 0;
        color: $acr-white;
        margin: 2.938rem auto 0;
        width: 90%;

        @media #{$sm-breakpoint} {
          border: 0;
          margin-top: 17rem;
          width: 65%;
        }

        @media #{$lg-breakpoint} {
          border: 2px solid $acr-white;
          padding: 0;
          right: 19%;
          width: 25.6rem;

          &::after {
            background: $acr-black;
            bottom: 0;
            content: '';
            left: 0;
            opacity: .1;
            position: absolute;
            right: 0;
            top: 0;
            z-index: -1;
          };
        }

        .desc-text {
          margin-bottom: 3.5rem;

          @media #{$lg-breakpoint} {
            padding: 2.063rem 2.063rem 0;
          }
        }

        .btn-wrapper {
          margin: 0 auto;
          text-align: center;
          width: 98%;

          @media #{$lg-breakpoint} {
            width: 100%;

            div {
              width: 100%;
            }
          }
        }
      }

      &.tacometercontainer {
        bottom: 4%;
        padding: 0;
        right: 18.2%;
      }
    }
  }
}
