@import 'global-nav-inputs';
@import 'global-nav-hamburger';
@import 'global-nav-flyouts';

iframe[name='google_conversion_frame'] {
  float: left;
  font-size: 0;
  height: 0;
  line-height: 0;
  margin-top: -13px;
  width: 0;
}

.acr-header[role=menu] {
  backface-visibility: hidden;
  background-color: $acr-black;
  height: 5rem;
  max-width: $wrapper-max-width;
  padding-bottom: 5rem;
  position: fixed;
  top: 0;
  transition: transform .5s ease-in-out 0s;
  width: 100%;
  z-index: 100;

  a {
    border-bottom: 0;
    text-decoration: none;
  }

  //h1 has global margin
  h1 {
    align-items: center;
    display: flex;
    justify-content: center;
    margin: 0;
  }

  &.fixed-expanded {
    > div {
      justify-content: flex-start;
    }

    nav[role=menubar] {
      position: fixed;

      header {
        .dealers-btn-mobile,
        .menu {
          display: none;
        }
      }

      section {
        display: none;
      }

      > .acr-icon-button-close {
        display: inline-block;
        margin: 0 0 0 1.25rem;
      }
    }
  }

  button {
    border: 0;
    padding: 0;
    vertical-align: middle;

    &.acr-icon-button-close {
      background-color: transparent;
    }
  }

  .acr-header-bar {
    display: block;
  }

  > div {
    background: $acr-black;
    display: flex;
    height: inherit;
    justify-content: center;
    max-width: inherit;
    position: fixed;
    top: 0;
    width: 100%;
  }
  //Mobile BP
  nav {
    background: $acr-black;
    overflow: hidden;
    padding: 0;
    position: relative;
    text-align: center;
    width: 100%;
    //Main navigation bar
    &[role=menubar] {
      align-items: center;
      display: flex;
      height: inherit;
      justify-content: space-between;
      margin: 0 auto;
      max-width: $blade-max-width;
      //position: fixed;
      width: 100%;

      .logo {
        color: $acr-white;

        &.acr-icon-acura-logo-horizontal {
          font-size: 1.4rem;
        }
      }

      > .acr-icon-button-close {
        display: none;
      }

      .menu {
        align-items: center;
        color: $acr-white;
        display: inline-flex;
        flex-flow: column nowrap;
        font-family: $acr-avenir-ltpro-black;
        font-size: 1rem;
        justify-content: center;
        line-height: 1.5;
        text-align: left;
        text-transform: uppercase;
        transition: .6s;
        //Two horizontal lines
        span {
          background: $acr-white;
          border-radius: 10px;
          display: block;
          height: 3px;
          position: relative;
          width: 3rem;

          &:last-child {
            margin-top: .5rem;
          }
        }
      }
      //Mobile Header
      header {
        align-items: center;
        display: flex;
        height: 5rem;
        justify-content: space-between;
        overflow: hidden;
        padding: .7rem 1rem .7rem 1.6rem;
        width: 100%;

        a.logo {
          left: 50%;
          position: absolute;
          top: 50%;
          transform: translate(-50%, -50%);

          .acr-icon-header {
            height: 3rem;
            vertical-align: bottom;
            width: 9rem;
          }
        }

        .acr-icon-button-close {
          display: none;
          font-size: 4.5rem;
          left: 2rem;
          position: absolute;
          top: 2.2rem;
        }
      }
      //Primary & Secondary in menubar
      section {
        display: none;
        height: inherit;
        vertical-align: middle;

        > ul {
          align-items: stretch;
          display: flex;
          height: 100%;
          justify-content: flex-start;
          list-style-type: none;
          margin: 0;
          padding: 0;

          li {
            align-items: center;
            cursor: pointer;
            display: inline-flex;

            > a {
              font-family: $acr-avenir-ltpro-medium;
            }
          }
        }

        ul.primary {
          li {
            margin-left: 2rem;

            &:first-child {
              margin-left: 0;

              @media #{$md-breakpoint} {
                margin-left: 2rem;
              }
            }

            a {
              border-bottom: 0;
              color: $acr-white;
              font-family: $acr-avenir-ltpro-heavy;
              font-size: 1.2rem;
              padding: 0 .6rem 0 1.25rem;
              text-decoration: none;
              text-transform: uppercase;
              white-space: nowrap;
            }

            svg {
              height: .25rem;
              width: .5rem;
            }

            &.expanded {
              background: $acr-white;

              svg {
                visibility: hidden;
              }

              a {
                color: $acr-black;
              }

              &.no-touch:hover a {
                color: $acr-black;
              }
            }
            //Because hover state sticks on touch devices,
            //only apply hover state to non-touch devices
            &.no-touch:hover {
              // svg {
              //   visibility: hidden;
              // }

              a {
                color: $acr-dim-grey-1;
              }
            }
          }
        }
      }
    }
  }

  .acr-icon-nav {
    fill: $acr-white;
  }
}

//Tablet View
@media #{$sm-breakpoint} {
  .acr-header[role=menu] {
    height: 6rem;
    padding-bottom: 6rem;

    &.fixed-expanded nav[role=menubar] {
      flex-direction: row-reverse;

      .logo {
        flex: 0 0 18%;
      }

      > .acr-icon-button-close {
        display: inline-block;
        margin: 0 1.25rem;
      }
    }
  }
  //Main navigation bar
  .acr-header[role=menu] nav[role=menubar] {
    //Header only on mobile
    header {
      display: none;
    }

    .logo {
      flex: 0 0 18%;

      .acr-icon-nav {
        height: 3.2rem;

        &.acr-icon-slogan {
          width: 13.7rem;
        }

        &.acr-icon-stacked {
          width: 6rem;
        }
      }
    }

    section {
      align-items: center;
      display: inline-flex;
      flex: 0 1 50%;
      margin: auto;

      &:last-child {
        flex: 1 1 27%;
        justify-content: flex-end;
        margin: 0;
      }

      ul.primary {
        flex: 1;

        li {
          position: relative;
        }

        a {
          font-size: 1.3rem;
        }
      }

      ul.secondary {
        flex: 1;
        justify-content: flex-end;

        li {
          a {
            border-bottom: 0;

            &:hover {
              color: $acr-dim-grey-1;
              text-decoration: none;
            }
          }

          &.menu {
            font-size: 1rem;

            span {
              width: 3rem;

              &:last-child {
                margin-top: .4rem;
              }
            }
          }
        }
      }
    }
  }
}

//Desktop View
@media #{$lg-breakpoint} {
  .acr-header[role=menu] {
    height: 7rem;
    padding-bottom: 7rem;

    nav[role=menubar] {
      .menu {
        display: none;
      }

      .logo {
        align-items: center;
        display: flex;
        flex: 1 1 20%;
        height: 100%;
        justify-content: center;
        margin: 0;
      }

      section {
        &:first-of-type {
          flex: 1 1 42%;
        }

        &:last-of-type {
          flex: 0 1 45%;
        }

        ul {
          &.primary {
            li {
              margin-left: 1rem;

              &:last-child {
                margin-left: 2rem;
              }

              a {
                font-size: 1.7rem;
                padding: 0 .7rem 0 1.45rem;
              }

              svg {
                height: .35rem;
                width: .7rem;
              }
            }
          }

          &.secondary {
            flex: 1;

            li {
              justify-content: unset;

              &:nth-child(1) {
                flex: 1 1 23%;
              }

              &:nth-child(2) {
                flex: 1 1 40%;
              }

              &:nth-child(3) {
                flex: 1 1 20%;
              }

              &:nth-child(4) {
                flex: 1 1 15%;
              }

              > a,
              > span {
                font-size: 1.5rem;
              }
            }
          }
        }
      }
    }
  }
}
//Extended View
@media #{$xl-breakpoint} {
  .acr-header[role=menu] {
    nav[role=menubar] {
      .logo {
        flex: 1 1 22%;
      }

      section {
        &:first-of-type {
          flex: 1 1 60%;

          ul.primary li:last-child {
            margin-left: 4.2rem;
          }
        }

        &:last-of-type {
          flex: 0 1 50%;
        }
      }
    }
  }
}
