.acr-icon {
  @extend %ui-icon-base;

  &.is-red {
    fill: $acr-red;
  }
}

.acr-link-icon {
  @extend %ui-icon-base;
  height: 2.6rem;
  width: 2.6rem;
}

.acr-icon-white {
  fill: $acr-white;
}


// LEGACY ICONS
//// ---------------------------------------------------------------------------------------------------  ////
////    Start _icons.scss
//// ---------------------------------------------------------------------------------------------------  ////


[class^='acr-icon-'],
[class*=' acr-icon-'] {
  // use !important to prevent issues with browser extensions that change fonts
  font-family: 'acura-icon';
  // better Font Rendering
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  speak: none;
  text-transform: none;
}

// icon font sizes
.acr-f1 { font-size: 3rem; }

.acr-f2 { font-size: 4rem; }

.acr-f3 { font-size: 5rem; }

.acr-f4 { font-size: 6rem; }

.acr-f5 { font-size: 7rem; }

.acr-f6 { font-size: 8rem; }


